<template>
  <v-card flat color="transparent">
    <v-container>
      <v-row>
        <v-col>
          <v-range-slider
            v-model.lazy="range"
            step="0.1"
            :max="max"
            :min="min"
            hide-details
            thumb-label="always"
            class="align-center mt-5"
          >
          </v-range-slider>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="3" class="pr-0">
          <v-text-field
            v-model.lazy="minRange"
            class="centered-input"
            hide-details
            outlined
            label="影响因子下限"
            type="number"
          ></v-text-field>
        </v-col>
        <v-col cols="6" class="px-0">
          <v-subheader
            class="justify-center font-weight-bold mx-0 pa-0"
            style="font-size: 20px"
            >影响因子范围</v-subheader
          >
          <!-- &lt;&nbsp;&nbsp;Impact Factor&nbsp;&nbsp;&lt; -->
        </v-col>
        <v-col cols="3" class="pl-0">
          <v-text-field
            v-model.lazy="maxRange"
            class="centered-input"
            hide-details
            outlined
            label="影响因子上限"
            type="number"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
export default {
  name: "impactFactor",
  data() {
    return {
      min: 0,
      max: 300,
    };
  },
  computed: {
    ...mapState({
      filter: (state) => state.filter,
    }),
    range: {
      get() {
        return this.filter.importanceMeasurement.IMF;
      },
      set(val) {
        this.$store.commit("setFilter", {
          param: ["importanceMeasurement", "IMF"],
          value: val,
        });
      },
    },
    minRange: {
      get() {
        return this.range[0];
      },
      set(val) {
        Vue.set(this.range, 0, val);
      },
    },
    maxRange: {
      get() {
        return this.range[1];
      },
      set(val) {
        Vue.set(this.range, 1, val);
      },
    },
  },
};
</script>

<style>
.centered-input input {
  text-align: center;
}
</style>
