<template>
  <div>
    <v-divider />
    <v-list v-for="(item, index) in items" :key="index">
      <div class="d-flex flex-row">
        <v-col cols="1" class="align-self-start">
          <v-checkbox
            class="my-0 py-0"
            :input-value="checkboxState(item.label)"
            @click="checkboxClick(item.label)"
          ></v-checkbox>
        </v-col>
        <v-col cols="8" class="d-flex flex-column pa-1">
          <div>
            <span class="primary--text text-subtitle-2 font-weight-bold">
              <!-- @click="
                link(`https://pubmed.ncbi.nlm.nih.gov/${parse(item).pmid}`)
              " -->
              {{ `${parse(item).title}` }}
            </span>
            <span class="text-subtitle-2">{{
              `[Project: ${parse(item).project}]`
            }}</span>
          </div>
          <div class="d-flex flex-row justify-space-between">
            <div
              class="text-caption col-8 pa-0"
              :title="parse(item).containerTitle"
            >
              {{
                `Journal: ${
                  parse(item).containerTitleShort == "NA"
                    ? parse(item).containerTitle
                    : parse(item).containerTitleShort
                }`
              }}
              <span
                class="ml-2 mr-1 px-1 primary white--text rounded"
                v-if="parse(item).imf != 'NA'"
                :style="qbstyle(parse(item).imf)"
                >{{
                  `IF: ${parse(item).imf} ${parse(item).jcr} ${parse(item).cas}`
                }}</span
              >
              <!-- <span
                class="mx-1 px-1 primary--text rounded-pill font-weight-bold"
                :style="qbstyle(parse(item).jcr)"
                >{{ `JCR: ${parse(item).jcr}` }}</span
              >
              <span
                class="mx-1 px-1 primary--text rounded-pill font-weight-bold"
                :style="qbstyle(parse(item).cas)"
                >{{ `CAS: ${parse(item).cas}` }}</span
              > -->
            </div>
            <!-- <div class="text-caption col-8 pa-0">
              {{
                `IF:${parse(item).imf} JCR:${parse(item).jcr} CAS:${
                  parse(item).cas
                } `
              }}
            </div> -->
            <div class="col-4 pa-0 text-caption">
              {{ `Update Time: ${parse(item).updateTimeString}` }}
            </div>
          </div>
          <div class="d-flex flex-row justify-space-between">
            <div class="text-caption col-4 pa-0">
              {{ `Year: ${parse(item).year}` }}
            </div>
            <div class="text-caption col-4 pa-0">
              {{ `First Author: ${parse(item).firstAuthor}` }}
            </div>
            <div class="text-caption col-4 pa-0">
              {{ `Last Author: ${parse(item).lastAuthor}` }}
            </div>
          </div>
          <div class="d-flex flex-row justify-space-between">
            <div class="text-caption col-4 pa-0">
              PMID:
              <span
                @click="
                  link(`https://pubmed.ncbi.nlm.nih.gov/${parse(item).pmid}/`)
                "
                v-if="parse(item).pmid != 'NA'"
                class="primary--text text-decoration-underline"
                style="cursor: pointer"
                >{{ `${parse(item).pmid}` }}</span
              >
              <span v-if="parse(item).pmid == 'NA'">NA</span>
            </div>
            <div class="text-caption col-4 pa-0">
              PMCID:
              <span
                @click="
                  link(
                    `https://www.ncbi.nlm.nih.gov/pmc/articles/${
                      parse(item).pmcid
                    }/`
                  )
                "
                v-if="parse(item).pmcid != 'NA'"
                class="primary--text text-decoration-underline"
                style="cursor: pointer"
                >{{ `${parse(item).pmcid}` }}</span
              >
              <span v-if="parse(item).pmcid == 'NA'">NA</span>
            </div>
            <div class="text-caption col-4 pa-0">
              DOI:
              <span
                @click="link(`https://www.doi.org/${parse(item).doi}`)"
                v-if="parse(item).doi != 'NA'"
                class="primary--text text-decoration-underline"
                style="cursor: pointer"
                >{{ `${parse(item).doi}` }}</span
              >
              <span v-if="parse(item).doi == 'NA'">NA</span>
              <!-- {{ `DOI: ${parse(item).doi}` }} -->
            </div>
          </div>
          <div class="d-flex flex-row justify-space-between">
            <div class="text-caption col-4 pa-0">
              <v-icon :color="parse(item).star > 0 ? 'orange' : 'grey'"
                >mdi-star</v-icon
              >
              <v-icon :color="parse(item).star > 1 ? 'orange' : 'grey'"
                >mdi-star</v-icon
              >
              <v-icon :color="parse(item).star > 2 ? 'orange' : 'grey'"
                >mdi-star</v-icon
              >
              <v-icon :color="parse(item).star > 3 ? 'orange' : 'grey'"
                >mdi-star</v-icon
              >
              <v-icon :color="parse(item).star > 4 ? 'orange' : 'grey'"
                >mdi-star</v-icon
              >
              <v-icon v-if="parse(item).redFlag" color="red">mdi-flag</v-icon>
              <v-icon v-if="parse(item).blueFlag" color="blue">mdi-flag</v-icon>
            </div>
            <div class="col-8 pa-0">
              <span class="text-caption mr-1">Tag:</span>
              <span
                v-for="(tag, index) in parse(item).tag"
                :key="index"
                class="
                  rounded-pill
                  primary
                  white--text
                  px-2
                  mx-1
                  d-inline-block
                "
              >
                {{ tag }}
              </span>
            </div>
          </div>
          <div v-if="parse(item).note != ''" class="d-flex flex-row">
            <div class="text-caption col-1 pa-0">Note:</div>
            <div class="col-11 pa-0" v-show="!expandedNote[item.label]">
              <div
                class="text-caption"
                :ref="`note-${item.label}`"
                style="white-space: pre-line"
              >
                {{ parse(item).note.substring(0, 200) }}
                <span
                  @click="expandNote(item.label)"
                  class="primary--text"
                  style="cursor: pointer"
                  v-if="parse(item).note.length > 200"
                  ><v-icon x-small>mdi-arrow-down-drop-circle</v-icon></span
                >
              </div>
            </div>
            <div class="col-11 pa-0" v-show="expandedNote[item.label]">
              <div
                class="text-caption"
                style="white-space: pre-line"
                :ref="`note-${item.label}`"
              >
                {{ parse(item).note }}
                <span
                  @click="$set(expandedNote, item.label, false)"
                  class="primary--text"
                  style="cursor: pointer"
                  ><v-icon x-small>mdi-arrow-up-drop-circle</v-icon></span
                >
              </div>
            </div>
          </div>
          <div class="d-flex flex-row pt-3 align-center">
            <div class="text-caption col-1 pa-0 align-self-start">
              Abstract:
            </div>
            <!-- <div class="col-11 pa-0 d-flex flex-column">
              <div
                class="text-caption"
                :ref="`abstract-${name}`"
                style="white-space: pre-line"
              ></div>
              <div
                class="col col-12 pa-0 d-flex flex-row justify-center hover"
                @click="expandCollapseAbstract(name)"
              >
                <v-icon size="small" :ref="`icon-down-abstract-${name}`"
                  >mdi-arrow-down-drop-circle</v-icon
                >
                <v-icon
                  size="small"
                  :ref="`icon-up-abstract-${name}`"
                  style="display: none"
                  >mdi-arrow-up-drop-circle</v-icon
                >
              </div>
            </div> -->
            <div class="col-11 pa-0 d-flex flex-column">
              <div
                class="text-caption"
                :ref="`abstract-${item.label}`"
                style="white-space: pre-line"
                v-show="expandedAbstract[item.label]"
              ></div>
              <div
                class="pa-0 d-flex flex-row justify-center hover"
                v-if="!expandedAbstract[item.label]"
                @click="expandAbstract(item.label, item)"
              >
                <v-icon size="small" :ref="`icon-down-abstract-${item.label}`"
                  >mdi-arrow-down-drop-circle</v-icon
                >
              </div>
              <div
                class="pa-0 d-flex flex-row justify-center hover"
                v-if="expandedAbstract[item.label]"
                @click="collapseAbstract(item.label)"
              >
                <v-icon size="small" :ref="`icon-up-abstract-${item.label}`"
                  >mdi-arrow-up-drop-circle</v-icon
                >
              </div>
            </div>
          </div>
        </v-col>
        <v-col cols="3" class="d-flex flex-column pa-1">
          <div class="d-flex flex-row justify-space-between">
            <div class="col-6 py-1">
              <v-menu open-on-hover bottom offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <div v-bind="attrs" v-on="on">
                    <v-btn
                      style="width: 100%"
                      class="primary lighten-2"
                      small
                      v-if="!isDownloadItemExist[item.label]"
                      @click="download(item.label, item)"
                      :disabled="
                        scihubPdfList[item.label] == 'error' ||
                        scihubPdfList[item.label] == undefined
                      "
                      ><v-icon left dark> mdi-download</v-icon>Download</v-btn
                    >
                    <v-btn
                      style="width: 100%"
                      class="primary lighten-2"
                      small
                      v-if="isDownloadItemExist[item.label]"
                      v-bind="attrs"
                      v-on="on"
                      @click="openFile(item.label, item)"
                      ><v-icon left dark> mdi-file-document-outline</v-icon
                      >Open</v-btn
                    >
                  </div></template
                >
                <v-btn
                  class="d-block primary"
                  small
                  tile
                  v-if="isSuppExistis(item)"
                  style="width: 100%"
                  @click="openSupp(item)"
                >
                  Supplement
                </v-btn>
              </v-menu>
            </div>
            <div class="col-6 py-1">
              <v-btn
                style="width: 100%"
                class="primary lighten-2"
                small
                @click="citation(item.label, item)"
                ><v-icon left dark> mdi-alpha-c-box</v-icon>citation</v-btn
              >
            </div>
          </div>
          <div class="d-flex flex-row justify-space-between">
            <div class="col-6 py-1 d-flex flex-strech">
              <v-menu open-on-hover bottom offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    style="width: 100%"
                    class="primary lighten-2"
                    :title="scihubPdfList[item.label]"
                    small
                    v-bind="attrs"
                    v-on="on"
                    :disabled="
                      scihubPdfList[item.label] == 'error' ||
                      scihubPdfList[item.label] == undefined
                    "
                    @click="link(scihubPdfList[item.label])"
                    >Full-text PDF</v-btn
                  >
                </template>
                <v-btn
                  style="width: 100%"
                  class="d-block primary"
                  small
                  tile
                  v-if="parse(item).doi != 'NA'"
                  @click="link(`${selectedURL}${parse(item).doi}`)"
                  >Sci-HUB link</v-btn
                >
              </v-menu>

              <!-- parse(item).doi == 'NA' || -->
              <!-- <v-icon left dark> mdi-pdf-box</v-icon> -->
            </div>
            <div class="col-6 py-1">
              <v-btn
                class="d-block primary lighten-2 px-0"
                small
                style="width: 100%"
                @click="editCsl(item)"
              >
                Update Ref
              </v-btn>
            </div>
          </div>
          <div class="d-flex flex-row justify-space-between">
            <div class="col-6 py-1">
              <v-menu open-on-hover bottom offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    class="primary lighten-2"
                    style="width: 100%"
                    dark
                    small
                    v-bind="attrs"
                    v-on="on"
                    @click="attach(item)"
                  >
                    <v-icon left dark> mdi-link</v-icon>Attach
                  </v-btn>
                </template>
                <v-btn
                  class="d-block primary"
                  small
                  tile
                  style="width: 100%"
                  @click="attachSupp(item)"
                >
                  supplement
                </v-btn>
              </v-menu>
            </div>
            <div class="col-6 py-1">
              <v-btn
                style="width: 100%"
                class="primary lighten-2"
                small
                @click="reload(item)"
                ><v-icon left dark> mdi-refresh</v-icon>reload</v-btn
              >
            </div>
          </div>
          <div class="d-flex flex-row justify-space-between">
            <div class="col-3 py-1">
              <v-btn
                class="primary lighten-2"
                small
                title="Open folder"
                :disabled="!isDownloadItemExist[item.label]"
                @click="openFolder(item)"
                ><v-icon dark> mdi-folder-open</v-icon></v-btn
              >
            </div>
            <div class="col-3 py-1 d-flex flex-strech">
              <v-btn
                small
                class="primary lighten-2"
                title="Save as"
                :disabled="scihubPdfList[item.label] == 'error'"
                @click="download(item.label, item, { saveAs: true })"
                ><v-icon dark> mdi-content-save</v-icon></v-btn
              >
            </div>
            <div class="col-3 py-1">
              <v-btn
                class="primary lighten-2"
                small
                title="Edit"
                @click="edit(item.label, item)"
                ><v-icon dark> mdi-pencil</v-icon></v-btn
              >
            </div>
            <div class="col-3 py-1 d-flex flex-strech">
              <v-btn
                small
                class="error lighten-1"
                title="Delete"
                @click="del(item.label, item)"
                ><v-icon dark> mdi-delete</v-icon></v-btn
              >
            </div>
          </div>
        </v-col>
      </div>
      <v-divider />
    </v-list>
    <referenceAttach
      :referenceAttachOpen="referenceAttachOpen"
      :referenceAttach="referenceAttach"
      v-on:close="referenceAttachOpen = false"
    ></referenceAttach>
    <referenceSupp
      :referenceSuppOpen="referenceSuppOpen"
      :referenceSupp="referenceSupp"
      v-on:close="referenceSuppOpen = false"
    ></referenceSupp>
    <referenceAttachSupp
      :referenceAttachSuppOpen="referenceAttachSuppOpen"
      :referenceAttachSupp="referenceAttachSupp"
      v-on:close="referenceAttachSuppOpen = false"
    ></referenceAttachSupp>
    <referenceEdit
      :referenceEditOpen="referenceEditOpen"
      :referenceEdit="referenceEdit"
      :allTags="
        projectData.summary != undefined ? projectData.summary.tags : []
      "
      :options="options"
      v-on:close="referenceEditOpen = false"
    ></referenceEdit>
    <referenceEditCsl
      :referenceEditCslOpen="referenceEditCslOpen"
      :referenceEditCsl="referenceEditCsl"
      v-on:close="referenceEditCslOpen = false"
    ></referenceEditCsl>
    <referenceDelete
      :referenceDeleteOpen="referenceDeleteOpen"
      :referenceDelete="referenceDelete"
      v-on:close="referenceDeleteOpen = false"
    ></referenceDelete>
    <referenceCitation
      :referenceCitationOpen="referenceCitationOpen"
      :referenceCitation="referenceCitation"
      :options="options"
      :journalImfqbtData="journalImfqbtData"
      v-on:close="referenceCitationOpen = false"
    ></referenceCitation>
    <input
      ref="fileInput"
      type="file"
      accept="application/pdf"
      @change="fileChange"
      style="display: none"
    />
  </div>
</template>

<script>
import referenceEdit from "./referenceEdit";
import referenceEditCsl from "./referenceEditCsl";
import referenceDelete from "./referenceDelete";
import referenceCitation from "./referenceCitation";
import referenceAttach from "./referenceAttach";
import referenceAttachSupp from "./referenceAttachSupp";
import referenceSupp from "./referenceSupp";
import ifqbt from "../json/ifqbt_2021.json";
import { getAbstract } from "./getAbstract";
import { getScihubPdf, getFullTextPdf } from "./getScihubPdf";
import { parseCsl, parseData, parse } from "./parse";
import {
  update,
  updateItem,
  updateDeleteDownloadItemList,
} from "./setProjectData";
import { makeFilename, downloadArticle } from "./downloadArticle";

export default {
  name: "articleDisplay",
  props: {
    projectData: Object,
    project: String,
    selectedURL: String,
    options: Object,
    selectAllArticle: Boolean,
    forceClear: Boolean,
  },
  components: {
    referenceEdit,
    referenceEditCsl,
    referenceDelete,
    referenceCitation,
    referenceAttach,
    referenceAttachSupp,
    referenceSupp,
  },
  data() {
    return {
      selectedArticles: [],
      expandedNote: {},
      expandedAbstract: {},
      scihubPdfList: {},
      isDownloadItemExist: {},
      referenceEditOpen: false,
      referenceEdit: {},
      referenceEditCslOpen: false,
      referenceEditCsl: {},
      referenceDeleteOpen: false,
      referenceDelete: {},
      referenceCitationOpen: false,
      referenceCitation: {},
      referenceAttachOpen: false,
      referenceAttach: {},
      referenceSuppOpen: false,
      referenceSupp: {},
      referenceAttachSuppOpen: false,
      referenceAttachSupp: {},
      journalImfqbtData: {},
      parsedList: {},
      uploadFileItem: {},
    };
  },
  computed: {
    projectItems() {
      return Object.keys(this.projectData.items);
    },
    items() {
      return this.projectData.items;
    },
  },
  methods: {
    attach(item) {
      this.referenceAttachOpen = true;
      this.referenceAttach = item;
    },
    attachSupp(item) {
      this.referenceAttachSuppOpen = true;
      this.referenceAttachSupp = item;
    },
    del(name, item) {
      this.referenceDeleteOpen = true;
      this.referenceDelete = { name: name, item: item };
    },
    edit(name, item) {
      this.referenceEditOpen = true;
      this.referenceEdit = { name: name, item: item };
    },
    editCsl(item) {
      this.referenceEditCslOpen = true;
      this.referenceEditCsl = item;
    },
    citation(name, item) {
      this.referenceCitationOpen = true;
      this.referenceCitation = { name: name, item: item };
      // console.log(this.referenceCitation);
    },
    openFolder(item) {
      chrome.downloads.show(item.data.downloadId);
    },
    openFile(name, item) {
      chrome.downloads.open(item.data.downloadId);
    },
    openSupp(item) {
      this.referenceSuppOpen = true;
      this.referenceSupp = item;
    },
    async download(name, item, { saveAs = false } = {}) {
      let url = this.scihubPdfList[name];
      if (Array.isArray(url) == true) {
        url = url[0];
      }
      let filename = await makeFilename(item);
      if (saveAs == true) {
        chrome.downloads.download({
          url: url,
          filename: filename.filenameShortPdf,
          saveAs: true,
        });
        return;
      }
      downloadArticle(url, item);
    },
    link(url) {
      // console.log("url", url);
      window.open(url);
    },
    expandNote(name) {
      this.$set(this.expandedNote, name, true);
    },
    collapseNote(name) {
      this.$set(this.expandedNote, name, false);
    },
    expandAbstract(name, item) {
      this.$set(this.expandedAbstract, name, true);
      let ref = `abstract-${name}`;
      if (this.$refs[ref][0].innerHTML == "") {
        this.$refs[ref][0].innerHTML = "Loading...";
        let pmid = name.match(/\d+/)[0];
        if (item.data.abstract != undefined) {
          this.$refs[ref][0].innerHTML = item.data.abstract;
          return;
        }
        getAbstract(pmid).then((result) => {
          let abstract = result[pmid].bib.Abstract;
          this.$refs[ref][0].innerHTML = abstract;
          item.data.abstract = abstract;
          // update(item.data.project, item.label, item);
          updateItem(item.data.project, item.label, { abstract: abstract });
        });
      }
    },
    collapseAbstract(name) {
      this.$set(this.expandedAbstract, name, false);
    },
    expandCollapseNote(name) {
      let ref = `note-${name}`;
      let height = this.$refs[ref][0].scrollHeight;
      if (this.$refs[ref][0].classList.contains("note")) {
        this.$refs[ref][0].classList.remove("note");
      } else {
        this.$refs[ref][0].classList.add("note");
      }
    },
    checkboxState(name) {
      // console.log(this.selectedArticles);
      if (this.selectedArticles.indexOf(name) == -1) {
        return false;
      } else {
        return true;
      }
    },
    checkboxClick(name) {
      if (this.selectedArticles.indexOf(name) == -1) {
        // this.selectedArticles = Array.from(
        //   new Set(this.selectedArticles.concat(name))
        // );
        this.selectedArticles = this.selectedArticles.concat(name);
      } else {
        this.selectedArticles = this.selectedArticles.filter((i) => {
          return i != name;
        });
      }
      if (this.selectAllArticle == true) {
        this.$emit("disableSelectAll");
      }
    },
    parse(item) {
      // console.log(item);
      if (Object.keys(this.parsedList).indexOf(item.label) != -1) {
        return this.parsedList[item.label];
      }
      let ret = parse(item);
      this.parsedList[item.label] = ret;
      // console.log(parsedCsl, ret);
      return ret;
    },
    qbstyle(x) {
      switch (x) {
        case "Q1":
        case "B1":
          return { "background-color": "#ef5350" };
        case "Q2":
        case "B2":
          return { "background-color": "#ff9800" }; //#fdfe2a
        case "Q3":
        case "B3":
          return { "background-color": "#31fe2a" };
        case "Q4":
        case "B4":
          return { "background-color": "#aeb19e" };
      }
    },
    openFileInput(item) {
      this.$refs.fileInput.click();
      this.uploadFileItem = item;
      // console.log(this.$refs.fileInput);
      // console.log(item);
    },
    fileChange(event) {
      if (event.target.files[0] == undefined) return;
      console.log(event.target.files[0]);
      let url = URL.createObjectURL(event.target.files[0]);
      downloadArticle(url, this.uploadFileItem);
      // console.log(this.uploadFileItem, url);
      // chrome.downloads.download({ url: url });
    },
    async reload(item) {
      if (item.data.downloadId != undefined) {
        chrome.downloads.removeFile(item.data.downloadId, () => {});
        delete item.data.downloadId;
        delete item.data.fulltextUrl;
        // update(item.data.project, item.label, item);
        updateItem(item.data.project, item.label, {
          downloadId: undefined,
          fulltextUrl: undefined,
        });
        this.scihubPdfList[item.label] = undefined;
      }

      let parsed = this.parse(item);
      let scihubPdf = await getScihubPdf(
        parsed.pmid,
        parsed.doi,
        this.selectedURL
      );
      // console.log(this.scihubPdfList, scihubPdf);
      this.$set(this.scihubPdfList, item.label, scihubPdf.pdfURL);
      if (scihubPdf.pdfURL != "error") {
        item.data.fulltextUrl = scihubPdf.pdfURL;
        // update(item.data.project, item.label, item);
        updateItem(item.data.project, item.label, {
          fulltextUrl: scihubPdf.pdfUrl,
        });
      }
    },
    isSuppExistis(item) {
      if (item.data.supplement == undefined) return false;
      if (Object.keys(item.data.supplement).length == 0) return false;
      return true;
    },
  },
  watch: {
    selectedArticles(n) {
      this.$emit("selectedArticles", n);
    },
    selectAllArticle(n) {
      if (n == true) {
        this.selectedArticles = this.projectData.items.map((x) => {
          return x.label;
        });
      }
    },
    forceClear(n) {
      if (n == true) {
        this.selectedArticles = [];
      }
    },
    project() {
      this.selectedArticles = [];
    },
    projectData: {
      deep: true,
      handler: function (n, o) {
        this.parsedList = [];
        // this.scihubPdfList = {};
        // console.log("projectData", n, o);
        for (var item of this.projectData.items) {
          if (item.data.fulltextUrl != undefined) {
            this.$set(this.scihubPdfList, item.label, item.data.fulltextUrl);
          }
        }
      },
    },
    // items(n, o) {
    //   console.log(n, o);
    // },
  },
  mounted() {
    let _this = this;
    setInterval(async function () {
      var deleteDownloadItemList = [];
      for (var [name, item] of Object.entries(_this.projectData.items)) {
        var exists;
        if (item.data.downloadId == undefined) {
          exists = false;
        } else {
          exists = await new Promise((resolve) => {
            chrome.downloads.search(
              { id: item.data.downloadId },
              function (result) {
                // console.log(result);
                resolve(result[0].exists);
              }
            );
          });
          if (exists == false) {
            deleteDownloadItemList.push(item);
          }
        }
        // console.log(name, exists);
        if (_this.isDownloadItemExist[item.label] != exists) {
          _this.$set(_this.isDownloadItemExist, item.label, exists);
          // console.log(item.csl.title, exists);
        }
      }
      if (deleteDownloadItemList.length != 0) {
        updateDeleteDownloadItemList(deleteDownloadItemList);
      }
    }, 1000);
  },

  async updated() {
    // console.log("a", this.projectData.items);
    if (this.projectData == undefined) return;
    var labels = Object.keys(this.scihubPdfList);
    // console.log(this.scihubPdfList);
    for (var item of this.projectData.items) {
      if (labels.indexOf(item.label) != -1) {
        continue;
      }
      if (
        item.data.fulltextUrl != undefined &&
        item.data.fulltextUrl != "error"
      ) {
        this.$set(this.scihubPdfList, item.label, item.data.fulltextUrl);
        continue;
      }
      let scihubPdf = await getFullTextPdf(item);
      if (this.scihubPdfList[item.label] == undefined) {
        this.$set(this.scihubPdfList, item.label, scihubPdf.pdfURL);
        if (scihubPdf.pdfURL != "error") {
          // item.data.fulltextUrl = scihubPdf.pdfURL;
          // update(item.data.project, item.label, item);
          console.log(item.data.project, item.label, {
            fulltextUrl: scihubPdf.pdfURL,
          });
          updateItem(item.data.project, item.label, {
            fulltextUrl: scihubPdf.pdfURL,
          });
        }
      }
    }
  },
};
</script>

