<template>
  <v-card
    class="d-flex flex-column flex-space-between elevation-0"
    height="624"
    width="800"
  >
    <v-tabs centered grow color="primary">
      <v-tab
        class="font-weight-bold"
        v-for="(item, key) in [
          'Select subject Category',
          'preview Selected Category',
        ]"
        :key="key"
        :href="`#tab2-${key}`"
      >
        <v-icon
          left
          size="28"
          v-text="
            key == 0 ? 'mdi-check-box-multiple-outline' : 'mdi-eye-outline'
          "
        ></v-icon>
        {{ cn(item) }}
      </v-tab>

      <v-tab-item value="tab2-0">
        <v-card class="ma-0 scroll elevation-0" height="570">
          <!-- <v-card-title class="primary white--text" primary-title>Select Subject Category</v-card-title> -->
          <v-list>
            <v-list-item
              @click.stop="toggleCompleteSelection"
              :input-value="allNodesSelected"
              active-class="primary white--text"
            >
              <v-list-item-action>
                <v-checkbox
                  :indeterminate="someNodesSelected"
                  :input-value="allNodesSelected"
                  @click.stop="toggleCompleteSelection"
                  color="white"
                />
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>全选</v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item-group v-model="selected_nodes" multiple>
              <v-list-item
                v-for="(item, index) in itemsData"
                :key="index"
                v-show="item.nFilter != 0"
                :value="item.node"
                active-class="background-color:'#b8c2d7'"
              >
                <template v-slot:default="{ active }">
                  <v-list-item-action>
                    <v-checkbox :input-value="active" color="#20558a" />
                  </v-list-item-action>
                  <v-list-item-content>
                    <v-list-item-title
                      v-text="item.nodeDisplay"
                    ></v-list-item-title>
                  </v-list-item-content>
                </template>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-tab-item>
      <v-tab-item value="tab2-1">
        <v-container class="d-flex flex-row justify-space-between ma-0 pa-0">
          <v-card class="d-flex flex-column ma-1" height="560" width="50%">
            <v-card-title class="primary white--text"
              >已选择杂志分类</v-card-title
            >
            <v-card class="scroll elevation-0" tile>
              <v-chip
                v-for="(value, key) in selected_nodes"
                v-show="
                  itemsData.filter((val) => val.node == value)[0].nFilter != 0
                "
                :key="key"
                close
                color="blue-grey"
                text-color="grey lighten-2"
                class="ma-1"
                @click="remove(value)"
                @click:close="remove(value)"
                >{{
                  itemsData.filter((val) => val.node == value)[0].nodeDisplay
                }}</v-chip
              >
            </v-card>
          </v-card>
          <v-card class="d-flex flex-column ma-1" height="560" width="50%">
            <v-card-title class="primary white--text" primary-title
              >未选择杂志分类</v-card-title
            >
            <v-card class="scroll elevation-0" tile>
              <v-chip
                v-for="(value, key) in not_selected_nodes"
                v-show="
                  itemsData.filter((val) => val.node == value)[0].nFilter != 0
                "
                :key="key"
                color="blue-grey"
                text-color="grey lighten-2"
                class="ma-1"
                @click="add(value)"
              >
                {{
                  itemsData.filter((val) => val.node == value)[0].nodeDisplay
                }}
                <v-hover v-slot:default="{ hover }">
                  <v-icon
                    right
                    small
                    @click="add(value)"
                    :color="hover ? 'grey lighten-1' : 'grey lighten-2'"
                    v-bind:style="{ cursor: hover ? 'pointer' : 'auto' }"
                    >mdi-plus-circle</v-icon
                  >
                </v-hover>
              </v-chip>
            </v-card>
          </v-card>
        </v-container>
      </v-tab-item>
    </v-tabs>
  </v-card>
</template>

<script>
import titleJson from "../json/n_area_json.json";
import { mapState } from "vuex";

export default {
  name: "allSelect",
  props: {
    nodesClass: String,
    nodesName: String,
    // nodes: Array,
    // nodesDisplay: Array
  },
  data() {
    return {
      tab: null,
    };
  },
  computed: {
    itemsData() {
      var data = [];
      let classData = titleJson.filter((val) => val.class == this.nodesName)[0];
      let nfilterData = this.$store.getters.nImportanceArea[this.nodesName];
      // console.log(classData.subclasses);
      for (const subclassData of classData.subclasses) {
        if (nfilterData[subclassData.subclass] != undefined) {
          let ret = {};
          ret.node = subclassData.subclass;
          ret.node_cn = subclassData.subclass_cn;
          ret.n = subclassData.n;
          ret.nFilter =
            nfilterData[subclassData.subclass] == undefined
              ? 0
              : nfilterData[subclassData.subclass];
          ret.nodeDisplay = `${ret.node_cn} (${ret.nFilter}/${ret.n})`;
          //   ret.disabled = this.selected_nodes.includes(ret.node);
          data.push(ret);
        } else {
          let ret = {};
          ret.node = subclassData.subclass;
          ret.n = subclassData.n;
          ret.nFilter = 0;
          data.push(ret);
        }
      }
      // console.log("d", data);
      return data;
    },
    //.filter(val => !this.selected_nodes.includes(val.node))
    allnodes() {
      return this.itemsData.map((val) => val.node);
    },
    //   itemArrayDisplayArea: function(item, subclasses) {
    //   let x = this.$store.getters.nImportanceArea[item];
    //   return subclasses.map(function(i) {
    //     return i.subclass + " (" + x[i.subclass] + "/" + i.n + ")";
    //   });
    // },
    noNodesSelected() {
      return this.selected_nodes.length === 0;
    },
    someNodesSelected() {
      return this.selected_nodes.length > 0 && !this.allNodesSelected;
    },
    allNodesSelected() {
      return this.selected_nodes.length === this.allnodes.length;
    },
    ...mapState({
      filter: (state) => state.filter,
    }),
    selected_nodes: {
      get: function () {
        return this.filter[this.nodesClass][this.nodesName];
        // if (this.nodesClass=="importanceMeasurement") {
        //   return this.filter.importanceMeasurement[this.nodesName];
        // } else {
        //   return this.filter.areaCategory[this.nodesName];
        // }
      },
      set: function (val) {
        // console.log("a", val);
        this.$store.commit("setFilter", {
          param: [this.nodesClass, this.nodesName],
          value: val,
        });
        // this.$store.dispatch("update");
      },
    },
    not_selected_nodes() {
      return this.allnodes.filter((val) => !this.selected_nodes.includes(val));
    },
    selected_nodes_reverse() {
      let x = this.selected_nodes;
      console.log(x.reverse());
      return x.slice().reverse();
    },
    // ...mapGetters{[]}
  },
  methods: {
    cn(i) {
      if (i == "Select subject Category") return "选择杂志分类";
      if (i == "preview Selected Category") return "预览杂志分类";
    },
    add(item) {
      var x = this.selected_nodes.concat(item);
      this.selected_nodes = x;
    },
    remove(item) {
      var x = this.selected_nodes.filter((val) => val != item);
      this.selected_nodes = x;
    },
    toggleCompleteSelection() {
      // console.log("HI");
      if (this.noNodesSelected || this.someNodesSelected) {
        this.selected_nodes = this.allnodes;
        // this.$store.commit("setFilter", {
        //   param: [this.nodesClass, this.nodesName],
        //   value: this.nodes
        // });
      } else {
        this.selected_nodes = [];
        // this.$store.commit("setFilter", {
        //   param: [this.nodesClass, this.nodesName],
        //   value: []
        // });
      }
    },
  },
  watch: {
    // selected_nodes(a, b) {
    //   // console.log(a, b);
    //   // console.log(this.nodesName, this.nodes);
    //   let nodes = this.itemsData.map((i) => i.node);
    //   this.selected_nodes = this.selected_nodes.filter((val) =>
    //     nodes.includes(val)
    //   );
    //   this.$store.commit("setFilter", {
    //     param: [this.nodesClass, this.nodesName],
    //     value: this.selected_nodes,
    //   });
    // },
    // itemsData: {
    //   // deep: true,
    //   handler: function (n, o) {
    //     this.selected_nodes = this.selected_nodes.filter((val) =>
    //       n.node.includes(val)
    //     );
    //   },
    // },
  },
  mounted() {},
};
</script>

<style scoped>
.scroll {
  overflow-y: auto;
  overflow-x: auto;
}
</style>
