<template>
  <div>
    <v-card>
      <v-toolbar color="primary" dark dense>
        <v-spacer />
        <v-toolbar-title>PubMed检索式</v-toolbar-title>
        <v-spacer />
      </v-toolbar>
      <v-card-text style="background-color: #dbe0eb" class="pb-1">
        <v-textarea
          class="ma-5"
          label="自定义PubMed检索式"
          placeholder="输入PubMed检索式, 如输入'y_5[Filter]'或'2015[Date - Publication] : 3000[Date - Publication])'以搜索近五年发表文献"
          v-model="customize"
          outlined
          filled
          no-resize
          counter
          rows="2"
        ></v-textarea>
        <v-textarea
          class="ma-5"
          label="PubMed检索式"
          v-model="filteredSend"
          outlined
          filled
          readonly
          no-resize
          counter="4000"
          rows="4"
        ></v-textarea>
      </v-card-text>
      <v-card color="primary" dark tile>
        <v-row justify="end">
          <!-- <v-btn color="info" class="my-3 mr-3">
            Save
            <v-icon right>mdi-content-save</v-icon>
          </v-btn>-->
          <v-btn
            color="info"
            class="my-3 mr-6"
            @click="
              copyQuery();
              isCopySuccess = true;
            "
          >
            复制检索式
            <v-icon right>mdi-content-copy</v-icon>
          </v-btn>
          <!-- <v-btn color="info" class="my-3 mr-6">
            Share
            <v-icon right>mdi-share</v-icon>
          </v-btn>-->
        </v-row>
      </v-card>
    </v-card>
    <v-snackbar v-model="isCopySuccess" timeout="3000" top color="success">
      成功复制检索式至剪贴板

      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="isCopySuccess = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  name: "filteredJson",
  data() {
    return {
      isCopySuccess: false,
    };
  },
  computed: {
    customize: {
      get() {
        return this.$store.state.customize;
      },
      set(val) {
        this.$store.commit("setFilter", {
          param: "customize",
          value: val,
        });
      },
    },
    filteredSend() {
      return this.$store.getters.filteredSend;
    },
  },
  methods: {
    copyQuery() {
      copytoClip(this.filteredSend);
    },
  },
};

function copytoClip(content) {
  var aux = document.createElement("input");
  aux.setAttribute("value", content);
  document.body.appendChild(aux);
  aux.select();
  document.execCommand("copy");
  document.body.removeChild(aux);
  // success_prompt("Success copy bibtex to clipboard");
}
</script>

