<template>
  <div>
    <v-dialog
      id="dialogEditCsl"
      v-model="referenceEditCslOpen"
      v-if="referenceEditCslOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Update Reference Data</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 400px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="pt-3 pb-1 px-1 mb-0">
                <div class="d-block text-center white--text primary lighten-2">
                  Original Information
                </div>
              </v-col>
              <v-col cols="12" class="pt-0 pb-1 px-1">
                <div class="d-block my-1">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Title
                  </div>
                  <div class="d-inline">{{ `${parsedOriginal.title}` }}</div>
                </div>
                <div
                  class="d-block my-1"
                  style="max-height: 40px; overflow-y: auto"
                >
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Author
                  </div>
                  <div class="d-inline">{{ `${parsedOriginal.author}` }}</div>
                </div>
                <div class="d-block my-1">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Year
                  </div>
                  <div class="d-inline">{{ `${parsedOriginal.year}` }}</div>
                </div>
              </v-col>
              <v-col
                cols="12"
                class="pt-1 pb-1 px-1 mb-0"
                v-if="Object.keys(parsedUpdated).length != 0"
              >
                <div class="d-block text-center white--text primary lighten-2">
                  New Information
                </div>
              </v-col>
              <v-col
                cols="12"
                class="pt-0 pb-1 px-1"
                v-if="Object.keys(parsedUpdated).length != 0"
              >
                <div class="d-block my-1">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Title
                  </div>
                  <div class="d-inline">{{ `${parsedUpdated.title}` }}</div>
                </div>
                <div
                  class="d-block my-1"
                  style="max-height: 40px; overflow-y: auto"
                >
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Author
                  </div>
                  <div class="d-inline">{{ `${parsedUpdated.author}` }}</div>
                </div>
                <div class="d-block my-1">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Year
                  </div>
                  <div class="d-inline">{{ `${parsedUpdated.year}` }}</div>
                </div>
                <div class="d-block my-1" v-if="parsedUpdated.doi != 'NA'">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    DOI
                  </div>
                  <div class="d-inline">{{ `${parsedUpdated.doi}` }}</div>
                </div>
                <div class="d-block my-1" v-if="parsedUpdated.pmid != 'NA'">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    PMID
                  </div>
                  <div class="d-inline">{{ `${parsedUpdated.pmid}` }}</div>
                </div>
              </v-col>
              <v-col cols="12" class="pt-3 pb-1 px-1">
                <v-textarea
                  v-model="cslString"
                  label="Reference Data"
                  filled
                  no-resize
                  hide-details
                  spellcheck="false"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-3" color="info" @click="guess">
            Find PMID/DOI
            <v-icon right>mdi-magnify</v-icon>
          </v-btn>
          <v-btn
            class="mr-3"
            color="success"
            @click="updateCsl()"
            v-show="isUpdateDisplay"
          >
            update
            <v-icon right>mdi-plus</v-icon>
          </v-btn>
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      id="dialogEditCsl"
      v-model="findOpen"
      v-if="findOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Find PMID/DOI</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="pt-3 pb-1 px-1">
                <div class="d-block my-1">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Title
                  </div>
                  <div class="d-inline">{{ `${parsedOriginal.title}` }}</div>
                </div>
                <div
                  class="d-block my-1"
                  style="max-height: 40px; overflow-y: auto"
                >
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Author
                  </div>
                  <div class="d-inline">{{ `${parsedOriginal.author}` }}</div>
                </div>
                <div class="d-block my-1">
                  <div
                    class="d-inline-block primary white--text text-center mr-2"
                    style="width: 75px"
                  >
                    Year
                  </div>
                  <div class="d-inline">{{ `${parsedOriginal.year}` }}</div>
                </div>
              </v-col>
              <v-col cols="12" class="pt-3 pb-1 px-1">
                <v-progress-linear
                  v-if="isLoading"
                  color="primary"
                  stream
                  height="25"
                  indeterminate
                  ><template>
                    <span
                      class="white--text text--lighten-1 font-weight-bold"
                      >{{ `Loading...` }}</span
                    ></template
                  ></v-progress-linear
                >
              </v-col>
              <v-col cols="12" class="pt-3 pb-1 px-1" v-if="isLoading != true">
                <div v-for="(item, index) in displayItems" :key="index">
                  <div
                    class="text-body-2 font-weight-bold primary--text"
                    style="cursor: pointer"
                    @click="
                      select(item);
                      isUpdateDisplay = true;
                    "
                  >
                    {{ item.title }}
                  </div>
                  <div
                    class="text-caption grey--text d-block"
                    style="max-height: 40px; overflow-y: auto"
                  >
                    {{ `Authors:${item.author}` }}
                  </div>
                  <div class="text-caption grey--text" v-if="item.pmid != 'NA'">
                    {{ `PMID: ${item.pmid}` }}
                  </div>
                  <div class="text-caption grey--text" v-if="item.doi != 'NA'">
                    {{ `DOI: ${item.doi}` }}
                  </div>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-3"
            color="primary"
            v-if="isFindMoreDisplay"
            @click="guessDoi"
          >
            Find more
            <v-icon right>mdi-magnify</v-icon>
          </v-btn>
          <v-btn class="mr-6" color="primary" @click="findOpen = false">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { parseCsl } from "./parse";
import { updateCsl } from "./setProjectData";
import { guessDoi, guessPmid, fetchFinalCsl } from "./fetchData";
export default {
  name: "referenceEditCsl",
  props: {
    referenceEditCslOpen: Boolean,
    referenceEditCsl: Object,
  },
  data() {
    return {
      cslOriginal: {},
      parsedOriginal: {},
      cslUpdated: {},
      parsedUpdated: {},
      cslString: "",
      findOpen: false,
      isLoading: false,
      isFindMoreDisplay: false,
      isUpdateDisplay: false,
      displayItems: [],
    };
  },
  computed: {
    // parsedOriginal() {
    //   return parseCsl(this.referenceEditCsl.csl);
    // },
  },
  watch: {
    referenceEditCslOpen(n) {
      if (n == true) {
        this.cslOriginal = this.referenceEditCsl.csl;
        this.cslString = JSON.stringify(this.cslOriginal);
        this.parsedOriginal = parseCsl(this.referenceEditCsl.csl);
        this.cslUpdated = {};
        this.parsedUpdated = {};
        this.isFindMoreDisplay = false;
        this.isUpdateDisplay = false;
        this.displayItems = [];
        this.findOpen = false;
      }
    },
  },
  methods: {
    async guess() {
      this.findOpen = true;
      this.isLoading = true;
      this.isFindMoreDisplay = false;
      let _guessPmid = await guessPmid(this.referenceEditCsl.csl);
      if (_guessPmid != "error") {
        this.isLoading = false;
        this.displayItems = _guessPmid;
        this.isFindMoreDisplay = true;
      } else {
        let _guessDoi = await guessDoi(this.referenceEditCsl.csl);
        this.isLoading = false;
        this.displayItems = _guessDoi;
        this.isFindMoreDisplay = false;
      }
      console.log("guess", this.displayItems);
    },
    async guessDoi() {
      this.isFindMoreDisplay = false;
      this.isLoading = true;
      let _guessDoi = await guessDoi(this.referenceEditCsl.csl);
      this.isLoading = false;
      this.displayItems = _guessDoi;
    },
    updateCsl() {
      let project = this.referenceEditCsl.data.project;
      let itemName = this.referenceEditCsl.label;
      let csl = JSON.parse(this.cslString);
      updateCsl(project, itemName, csl);
      this.$emit("close");
    },
    async select(item) {
      this.isLoading = true;
      if (item.source == "pubmed") {
        this.cslUpdated = item.csl;
        this.parsedUpdated = parseCsl(this.cslUpdated);
        this.cslString = JSON.stringify(this.cslUpdated);
      }
      if (item.source == "doi") {
        let doi = item.doi;
        let finalCsl = await fetchFinalCsl(doi);
        this.cslUpdated = finalCsl;
        this.parsedUpdated = parseCsl(this.cslUpdated);
        this.cslString = JSON.stringify(this.cslUpdated);
      }
      this.isLoading = false;
      this.findOpen = false;
    },
  },
};
</script>

