function getPmidXhr(doi) {
    //use doi to get pmid
    return new Promise((resolve, reject) => {
        var xhr = new XMLHttpRequest();
        var responseURL
        xhr.open('GET', `https://pubmed.ncbi.nlm.nih.gov/?term=${doi}[Location ID]`, true);
        xhr.onreadystatechange = function () {
            if (this.readyState == 4 && this.status == 200) {
                responseURL = xhr.responseURL;
                var pmid = responseURL.match(/(?<=https:\/\/pubmed.ncbi.nlm.nih.gov\/)\d+/)
                if (pmid == null) {
                    reject('error')
                    // throw new Error('cannot find pmid')
                } else {
                    resolve(pmid[0])
                }
            } else if (this.readyState == 4 && this.status != 200) {
                reject('error')
            }
        };
        xhr.send();
    })
}

export async function fetchPmidCsl(pmid) {
    try {
        let res = await retry(function () {
            return $.ajax(
                `https://api.ncbi.nlm.nih.gov/lit/ctxp/v1/pubmed/?format=csl&id=${pmid}`, {
                timeout: 10000
            })
        })
        return res
    } catch (e) {
        throw new Error('cannot find Pmid csl')
    }
}

export async function fetchDoiCsl(doi) {
    try {
        let res = await retry(function () {
            return $.ajax(
                `https://data.crossref.org/${doi}`,
                {
                    headers: {
                        accept: "application/vnd.citationstyles.csl+json"
                    },
                    timeout: 10000

                })
        })
        return res
    } catch (e) {
        throw new Error('cannot find Doi csl')
    }
}
