export async function fetchZoteroCsl(doi) {
    try {
        let res = await retry(function () {
            return $.ajax(
                "https://t0guvf0w17.execute-api.us-east-1.amazonaws.com/Prod/search",
                {
                    contentType: "text/plain",
                    type: "POST",
                    data: doi,
                    timeout: 10000

                })
        })
        let csl = convert_to_csl(res[0])
        return csl
    } catch (e) {
        throw new Error('cannot find Zotero csl')
    }
}

export async function fetchFinalCsl(doi) {
    let pmid, finalCsl;
    try {
        pmid = await fetchPmid(doi);
    } catch (e) {
        pmid = "NA";
    }
    // console.log("in", pmid);
    if (pmid == "NA") {
        try {
            if (doi.startsWith("10.") == false) throw new Error("");
            var doiCsl = await fetchDoiCsl(doi);
            finalCsl = doiCsl;
        } catch {
            finalCsl = "NA";
        }
    } else {
        try {
            var pmidCsl = await fetchPmidCsl(pmid);
            finalCsl = pmidCsl;
        } catch {
            try {
                if (doi.startsWith("10.") == false) throw new Error("");
                var doiCsl = await fetchDoiCsl(doi);
                finalCsl = doiCsl;
            } catch (e) {
                console.log(e);
                finalCsl = "NA";
            }
        }
    }
    return finalCsl
}

export async function guessDoi(csl) {
    //https://api.crossref.org/works?query=query.bibliographic=psoriasis+2015&query.author=Boehncke+Wolf-Henning+Sch%C3%B6n+Michael%20P&rows=3
    let parsed = parseCsl(csl)
    let author = parsed.author3.replaceAll(';', " ")
    author = author == "NA" ? "" : author
    let title = parsed.title == "NA" ? '' : parsed.title
    let year = parsed.year == 'NA' ? '' : parsed.year
    let url = `https://api.crossref.org/works?query.bibliographic=${title} ${year}&query.author=${author}&rows=3`
    let response = await $.ajax(url, {
        success: function (response) {
            return response
        }
    })
    if (response.message == undefined) return {}
    var ret = []
    for (let item of response.message.items) {
        let itemParsed = parseCsl(item)
        ret.push({ doi: itemParsed.doi.toString(), pmid: itemParsed.pmid.toString(), title: itemParsed.title.toString(), year: itemParsed.year.toString(), author: itemParsed.author.toString(), containerTitle: itemParsed.containerTitle.toString(), source: 'doi', csl: item })
    }
    // console.log(ret)
    return ret
    // console.log('aa', response)
    // console.log(parsed)
}

export async function guessPmid(csl) {
    // Psoriasis[title]AND%20Boehncke,%20Wolf-Henning%20AND%20Schön,%20Michael%20P[Author]AND%202015[Date%20-%20Publication]
    let parsed = parseCsl(csl)
    let author = parsed.author3.replaceAll(';', " ")
    author = author == "NA" ? "" : author
    let title = parsed.title == "NA" ? '' : parsed.title
    let year = parsed.year == 'NA' ? '' : parsed.year
    // let url = `https://eutils.ncbi.nlm.nih.gov/entrez/eutils/esearch.fcgi?db=pubmed&term=${title}[Title]AND ${author}[Author]AND ${year}[Date - Publication]`//
    // let response = await $.ajax(url, {
    //     success: function (response) {
    //         return response
    //     }
    // })
    // let pmidList = $('IdList Id', response).map((x, y) => { return $(y).text() }).get().slice(0, 3)
    try { var analysedPmid = await getPmidXhr2(author, title, year) } catch { return 'error' }
    let pmidList = [analysedPmid]
    var pmidCsl = {}
    try {
        for (var pmid of pmidList) {
            let x = fetchPmidCsl(pmid)
            pmidCsl[pmid] = await x
        }
    } catch {
        console.log(pmidCsl)
        return 'error'
    }
    var ret = []
    for (var [pmid, item] of Object.entries(pmidCsl)) {
        let itemParsed = parseCsl(item)
        ret.push({ doi: itemParsed.doi.toString(), pmid: itemParsed.pmid.toString(), title: itemParsed.title.toString(), year: itemParsed.year.toString(), author: itemParsed.author.toString(), containerTitle: itemParsed.containerTitle.toString(), source: 'pubmed', csl: item })
    }
    return ret
}

function getPmidXhr2(title, author, year) {
    //use title author year to get PMID
    return new Promise((resolve, reject) => {
        var xhr = new XMLHttpRequest();
        var responseURL
        xhr.open('GET', `https://pubmed.ncbi.nlm.nih.gov/?term=${title}[Title]AND ${author}[Author]AND ${year}[Date - Publication]`, true);
        xhr.onreadystatechange = function () {
            if (this.readyState == 4 && this.status == 200) {
                responseURL = xhr.responseURL;
                var pmid = responseURL.match(/(?<=https:\/\/pubmed.ncbi.nlm.nih.gov\/)\d+/)
                if (pmid == null) {
                    reject('error')
                    // throw new Error('cannot find pmid')
                } else {
                    resolve(pmid[0])
                }
            } else if (this.readyState == 4 && this.status != 200) {
                reject('error')
            }
        };
        xhr.send();
    })
}

function retry(fn, retries = 3, err = null, i = 1) {
    if (i > retries) {
        return Promise.reject(err);
    }
    return fn().catch(err => {
        return retry(fn, retries, err, i + 1);
    });
}