import ifqbt from "../json/ifqbt_2021.json";

export function parseCsl(csl) {
  let authorNameList = catchErr(function () {
    let _authorNameList = []
    for (var i = 0; i < csl.author.length; i++) {
      let author = csl.author[i];
      let family = author.family == undefined ? "" : author.family;
      let given = author.given == undefined ? "" : author.given;
      _authorNameList.push(`${family} ${given}`)
    }
    return _authorNameList
  }, 'authorNameList')
  let authorString = authorNameList != 'NA' ? authorNameList.join('; ') : "NA"
  let authorString3 = authorNameList != 'NA' ? authorNameList.slice(0, 3).join('; ') : "NA"
  //   let imfq = this.get_pmid_imfq(journal, this.ifqbt);
  let journal = catchErr(() => { return csl["container-title-short"] }, 'containerTitleShort')
  let imfq = get_pmid_imfq(journal, ifqbt);
  return {
    // project: data.project,
    title: catchErr(() => { return csl["title"] }, 'title'),
    containerTitle: catchErr(() => { return csl["container-title"] }, 'containerTitle'),
    containerTitleShort: catchErr(() => { return csl["container-title-short"] }, 'containerTitleShort'),
    author: authorString,
    authors_string: authorString,
    author3: authorString3,
    firstAuthor: catchErr(() => { return `${csl.author[0].family}, ${csl.author[0].given}` }, 'firstAuthor'),
    firstAuthorGiven: catchErr(() => { return csl.author[0].given }, 'firstAuthorGiven'),
    firstAuthorFamily: catchErr(() => { return csl.author[0].family }, 'firstAuthorFamily'),
    lastAuthor: catchErr(() => {
      return `${csl.author[csl.author.length - 1].family}, ${csl.author[csl.author.length - 1].given
        }`
    }, 'lastAuthor'),
    lastAuthorGiven: catchErr(() => {
      return csl.author[csl.author.length - 1].given
    }, 'lastAuthorGiven'),
    lastAuthorFamily: catchErr(() => {
      return csl.author[csl.author.length - 1].family
    }, 'lastAuthorFamily'),
    pmid: catchErr(() => { return csl.PMID }, 'PMID'),
    pmcid: catchErr(() => { return csl.PMCID }, 'PMCID'),
    doi: catchErr(() => { return csl.DOI }, 'DOI'),
    year: catchErr(() => { return csl.issued["date-parts"][0][0] }, 'year'),
    imf: imfq.imf,
    jcr: imfq.jcr,
    cas: imfq.cas,
    top: imfq.top,
  };
}

export function parseData(data) {
  let updateTimeString = catchErr(function () {
    let date = new Date(data.updateTime);
    let updateTimeString = `${date.getFullYear()}/${date.getMonth() + 1
      }/${date.getDate()} ${date.getHours()}:${("0" + date.getMinutes()).slice(
        -2
      )}:${("0" + date.getSeconds()).slice(-2)}`;
    return updateTimeString
  }, "updateTimeString", '')
  return {
    project: catchErr(() => { return data.project }, 'redFlag', 'NA'),
    redFlag: catchErr(() => { return data.redFlag }, 'redFlag', false),
    blueFlag: catchErr(() => { return data.blueFlag }, 'blueFlag', false),
    star: catchErr(() => { return data.rating }, 'star', 0),
    tag: catchErr(() => { return data.tag }, 'tag', []),
    note: catchErr(() => { return data.note }, 'updateTime', ''),
    updateTime: catchErr(() => { return data.updateTime }, 'updateTime', ''),
    updateTimeString: updateTimeString,
  }
}

export function parse(item) {
  let parsedCsl = parseCsl(item.csl)
  let parsedData = parseData(item.data)
  let parsed = { ...parsedCsl, ...parsedData }
  return parsed
}
function get_pmid_imfq(journal) {
  //get imfq if cant find both, both return as -1
  let temp;
  let imfq = {};
  imfq["imf"] = "NA";
  imfq["jcr"] = "NA";
  imfq["cas"] = "NA";
  imfq["top"] = "NA";
  temp = ifqbt.filter(function (x) {
    return x.journal == journal;
  });
  if (temp.length != 0) {
    imfq["imf"] = temp[0].IF;
    imfq["jcr"] = temp[0].Q;
    imfq["cas"] = temp[0].B;
    imfq["top"] = temp[0].T;
  }
  // this.$set(this.journalImfqbtData, journal, imfq);
  return imfq;
}

function catchErr(fn, name, errorReturn = "NA") {
  try {
    let ret = fn()
    if (ret == undefined || ret == null || ret == "") throw new Error(``)
    return ret
  } catch {
    // console.log(new Error(`Error in parse ${name}`))
    return errorReturn
  }
}