<template>
  <v-dialog
    id="dialogDelete"
    v-model="referenceDeleteOpen"
    v-if="referenceDeleteOpen"
    max-width="800"
    persistent
  >
    <v-card>
      <v-card-title class="error white--text">
        <span>Delete Reference</span>
      </v-card-title>
      <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
        <v-container>
          <v-row>
            <v-col cols="12" class="pt-3 pb-1 px-1">
              <span class="primary--text text-subtitle-1">{{
                `Do you want to delete '${referenceDelete.item.csl.title}' from project: ${referenceDelete.item.data.project}?`
              }}</span>
            </v-col>
            <v-checkbox
              v-model="deletePdf"
              v-if="referenceDelete.item.data.downloadId != undefined"
              label="Delete the linked PDF file"
              color="error"
            >
            </v-checkbox>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-3" color="error" @click="del()">
          Delete
          <v-icon right>mdi-delete</v-icon>
        </v-btn>
        <v-btn class="mr-6" color="primary" @click="$emit('close')">
          Close
          <v-icon right>mdi-close-circle</v-icon>
        </v-btn>
        <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { delItem } from "./setProjectData";
export default {
  name: "referenceDelete",
  props: {
    referenceDeleteOpen: Boolean,
    referenceDelete: Object,
    allTags: Array,
    options: Object,
  },
  data() {
    // let x = this.referenceEdit.item.data.tag;
    return {
      deletePdf: true,
    };
  },
  computed: {},
  methods: {
    del() {
      let project = this.referenceDelete.item.data.project;
      let itemName = this.referenceDelete.name;
      //   console.log(project, itemName);
      delItem(project, itemName);
      if (
        this.deletePdf == true &&
        this.referenceDelete.item.data.downloadId != undefined
      ) {
        chrome.downloads.removeFile(
          this.referenceDelete.item.data.downloadId,
          () => {}
        );
      }
      this.$emit("close");
    },
  },
  watch: {},
};
</script>

