<template>
  <div>
    <v-card>
      <v-toolbar color="primary" dark dense>
        <v-spacer />
        <v-toolbar-title>自定义增添杂志至过滤器</v-toolbar-title>
        <v-spacer />
      </v-toolbar>
      <v-card-text style="background-color: #dbe0eb">
        <v-autocomplete
          v-model="model"
          :items="pubmed"
          item-text="description"
          item-value="abb"
          label="查询杂志信息"
          placeholder="请输入杂志全称、杂志缩写、ISSN、eISSN查询"
          prepend-icon="mdi-database-search"
          background-color="#dbe0eb"
          hide-no-data
          hide-selected
          return-object
          spellcheck="false"
        >
          <!-- :loading="isLoading"
          :search-input.sync="search"-->
          <template v-slot:append-outer>
            <v-btn
              :disabled="
                !model ||
                models.includes(model.abb) ||
                filteredImportanceArea.includes(model.abb) ||
                del.includes(model.abb)
              "
              color="success"
              @click="add"
              class="mr-4"
            >
              添加
              <v-icon right>mdi-plus-circle</v-icon>
            </v-btn>
            <v-btn :disabled="!model" color="error" @click="model = null">
              清除
              <v-icon right>mdi-close-circle</v-icon>
            </v-btn>
            <!-- <v-slide-x-reverse-transition mode="out-in">
              <v-icon
                :key="`icon-${isEditing}`"
                :color="isEditing ? 'success' : 'info'"
                @click="isEditing = !isEditing"
                v-text="isEditing ? 'mdi-check-outline' : 'mdi-circle-edit-outline'"
              ></v-icon>
            </v-slide-x-reverse-transition>-->
          </template>
        </v-autocomplete>
      </v-card-text>
      <v-divider></v-divider>
      <v-expand-transition>
        <v-list v-if="model" style="background-color: #dbe0eb">
          <v-list-item
            v-for="(value, name, i) in fields"
            :key="i"
            class="py-0"
            style="min-height: 30px"
          >
            <v-list-item-content class="py-0">
              <v-list-item-title style="white-space: normal">{{
                cn(name) + ": " + value
              }}</v-list-item-title>
              <!-- <span style="display: inline-block">{{ name + ":" }}</span>
              <span style="display:inline-block; text-align-left">{{
                value
              }}</span> -->
              <!-- <v-list-item-subtitle v-text="name"></v-list-item-subtitle> -->
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-expand-transition>
      <v-card v-if="models.length != 0" color="primary" dark tile>
        <v-container class="pa-0">
          <v-row align="center">
            <v-col class="col-2" justify="center">
              <p class="text-center ma-0">
                <v-icon class="mx-2">mdi-plus-box-multiple</v-icon
                >自定义增添至过滤器杂志
              </p>
            </v-col>
            <v-col class="col-10" justify="center">
              <v-chip
                v-for="(value, key) in models"
                :key="key"
                close
                color="blue-grey"
                text-color="grey lighten-2"
                class="ma-1"
                @click:close="close(value)"
                @click="chipClick(value)"
                >{{ value }}</v-chip
              >
              <!-- @click:close="$emit('eventClose',value)"
              @eventClose="close($event)"-->
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-card>
  </div>
</template>

<script>
import pubmed_abb from "../json/pubmed_abb_data.json";
import filter_data from "../json/filter_data.json";
import { mapGetters } from "vuex";
// mapState,
export default {
  name: "addBar",

  data: () => ({
    // isLoading: false,
    model: null,
  }),

  computed: {
    pubmed() {
      return pubmed_abb.map(function (i) {
        i.description = `${i.pubmed_journal} (${i.abb})`;
        if (i.pubmed_issn != "NA")
          i.description += `  [ISSN: ${i.pubmed_issn}]`;
        if (i.pubmed_eissn != "NA")
          i.description += `  [eISSN: ${i.pubmed_eissn}]`;
        return i;
      });
    },
    fields() {
      var field = {};
      field["Journal"] = this.model["pubmed_journal"];
      field["Journal Abbreviation"] = this.model["abb"];
      field["ISSN"] = this.model["pubmed_issn"];
      field["eISSN"] = this.model["pubmed_eissn"];

      var filter_abbs = filter_data.filter((i) => i.abb === this.model.abb);
      console.log("filter_abbs", filter_abbs);
      var filter_abb = filter_abbs[0];
      if (filter_abbs.length > 0) {
        field["SCIE Indexed"] = "收录";
        field["Impact Factor"] = filter_abb["IF"];
        field["JCR Quartile"] = filter_abb["Q"];
        field["CAS Block"] = filter_abb["B"];
        field["Top/Non-Top Journal Classification"] =
          filter_abb["T"] == 1 ? "顶级刊物" : "非顶级刊物";
        let areaCategoryArray = [];
        for (var i of filter_abbs) {
          areaCategoryArray.push(`${i.class_cn}/${i.subclass_cn}`);
        }
        field["Subject Area/Category"] = areaCategoryArray.join(" ; ");
      } else {
        field["SCIE Indexed"] = "未收录";
      }
      console.log(field);
      return field;
    },
    // ...mapState({
    //   filter: state => state.filter
    // }),
    // models() {
    //   return this.filter.add;
    // }
    ...mapGetters({
      models: "getAdd",
      del: "getDel",
      filteredImportanceArea: "filteredImportanceArea",
    }),
  },
  methods: {
    cn(i) {
      if (i == "Journal") return "杂志全称";
      if (i == "Journal Abbreviation") return "杂志缩写";
      if (i == "SCIE Indexed") return "SCIE是否收录";
      if (i == "Impact Factor") return "影响因子";
      if (i == "JCR Quartile") return "JCR分区";
      if (i == "CAS Block") return "CAS分区";
      if (i == "Top/Non-Top Journal Classification") return "顶级/非顶级刊物";
      if (i == "Subject Area/Category") return "杂志分类";
      return i;
    },
    add() {
      //   console.log("models", this.models);
      //   var x = this.models;
      //   x.push(this.model.abb);
      this.$store.commit("setFilter", {
        param: "add",
        value: this.models.concat(this.model.abb),
        // value: [...new Set(this.models.concat([this.model.abb]))]
      });
    },
    close(closeAbb) {
      this.$store.commit("setFilter", {
        param: "add",
        value: this.models.filter((val) => {
          return val != closeAbb;
        }),
      });
    },
    chipClick(clickAbb) {
      this.model = this.pubmed.filter((val) => {
        return val.abb === clickAbb;
      })[0];
    },
  },
  watch: {
    // search() {
    //   // Items have already been loaded
    //   if (this.items.length > 0) return;
    //   // Items have already been requested
    //   if (this.isLoading) return;
    //   this.isLoading = true;
    // Lazily load input items
    //   fetch("https://api.publicapis.org/entries")
    //     .then(res => res.json())
    //     .then(res => {
    //       const { count, entries } = res;
    //       this.count = count;
    //       this.entries = entries;
    //     })
    //     .catch(err => {
    //       console.log(err);
    //     })
    //     .finally(() => (this.isLoading = false));
    // }
  },
};
</script>

<style></style>
