import Vue from "vue";
import Vuex from "vuex";
// import nifqbtJson from "../json/n_ifqbt_json.json";
import titleJson from "../json/n_area_json.json";
import filterJson from "../json/filter_data.json";
import pubmedJson from "../json/pubmed_abb_data.json";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    filter: {
      name: "",
      author: "",
      description: "",
      importanceMeasurement: {
        JCR: [],
        CAS: [],
        TOP: [],
        IMF: [0, 300]
      },
      areaCategory: {},
      add: [],
      del: [],
      customize: "",
      edition: ""
    },
    result: {
      nImportanceArea: [],
      filteredImportance: null,
      filteredAreaCategory: null,
      filteredImportanceArea: null,
      filtered: null,
      filteredTable: null,
      filteredIssn: null,
    },
    isLoading: false
  },
  mutations: {
    // ini: function (state) {
    //   Vue.set(state.filter, "name", "")
    //   Vue.set(state.filter, 'author', "")
    //   Vue.set(state.filter, 'description', "")
    //   state.filter.importanceMeasurement = {
    //     JCR: [],
    //     CAS: [],
    //     TOP: [],
    //     IMF: [0, 300]
    //   };
    // },
    setFilter: function (state, payload) {
      if (payload.param == "iniImportanceMeasurement") {
        state.filter.importanceMeasurement = {
          JCR: [],
          CAS: [],
          TOP: [],
          IMF: [0, 300]
        };
        return
      }
      if (payload.param[0] == "importanceMeasurement") {
        state.filter.importanceMeasurement = {
          JCR: [],
          CAS: [],
          TOP: [],
          IMF: [0, 300]
        };
      }
      if (typeof payload.param != "string" && payload.param.length == 2) {
        //for areaCategory set
        Vue.set(
          state.filter[payload.param[0]],
          payload.param[1],
          payload.value
        );
        return
        // state.filter.[payload.param[0]][payload.param[1]] = payload.value;
      }
      if (typeof payload.value == "string") {
        Vue.set(state.filter, payload.param, payload.value)
        return
      }
      Vue.set(state.filter, payload.param, [...new Set(payload.value)]);
      // state.users.push({name})
      // state.filter[payload.param[0]].push(payload.value)
      // Vue.set(state.filter, payload.param, typeof (payload.value) == "string" ? [payload.value] : payload.value);
      // state.filter.[payload.param[0]] = payload.value
    },
    setResult: function (state, payload) {
      for (const name in payload) {
        Vue.set(state.result, name, payload[name])
      }
    },
    toggleLoad: function (state, payload) {
      Vue.set(state, 'isLoading', payload)
    }
  },
  getters: {
    getAdd: function (state) {
      return state.filter.add;
    },
    getDel: function (state) {
      return state.filter.del;
    },
    getJCR(state) {
      return state.filter.importanceMeasurement.JCR;
    },
    getCAS(state) {
      return state.filter.importanceMeasurement.CAS;
    },
    getTOP(state) {
      return state.filter.importanceMeasurement.TOP;
    },
    getIMF(state) {
      return state.filter.importanceMeasurement.IMF;
    },
    getAreaCategory(state) {
      return state.filter.areaCategory;
    },
    nImportanceArea(state) {
      return state.result.nImportanceArea
    },
    filteredImportance(state) {
      return state.result.filteredImportance;
    },
    filteredAreaCategory(state) {
      return state.result.filteredAreaCategory;
    },
    filteredImportanceArea(state) {
      return state.result.filteredImportanceArea;
    },
    filtered(state) {
      return state.result.filtered;
    },
    filteredTable(state) {
      return state.result.filteredTable;
    },
    filteredIssn(state) {
      return state.result.filteredIssn;
    },
    filteredSend(state, getters) {
      if (state.filter.customize == "") {
        return getters.filteredIssn.join("|")
      } else {
        return `${state.filter.customize} AND (${getters.filteredIssn.join("|")})`
      }
    },
    filterMini(state) {
      var filterMini = filterMinify(state)
      return filterMini;
    },
  },
  actions: {
    load({ commit, dispatch }, filterMinified) {
      var filterMini = JSON.parse(filterMinified)
      var filter = {}
      filter.name = filterMini.name
      commit("setFilter", { param: 'name', value: filter.name });
      for (var i of ['author', 'description', 'add', 'del', 'customize']) {
        if (filterMini[i] == undefined) {
          if (['add', 'del'].includes(i)) {
            filter[i] = []
          } else {
            filter[i] = ""
          }
        } else {
          filter[i] = filterMini[i]
        }
        commit("setFilter", { param: i, value: filter[i] });
      }

      if (Object.keys(filterMini.IM).length == 0) {
        commit("setFilter", { param: ['importanceMeasurement', 'IMF'], value: [0, 300] });
      } else {
        commit("setFilter", { param: ['importanceMeasurement', Object.entries(filterMini.IM)[0][0]], value: Object.entries(filterMini.IM)[0][1] });
      }

      var classNameArray = titleJson.map(i => i.class)
      filter.areaCategory = {}
      for (var className of classNameArray) {
        if (filterMini.AC[className] == undefined) {
          filter.areaCategory[className] = []
          continue
        }

        let subclassArray = titleJson.filter(val => val.class == className)[0].subclasses.map(val => val.subclass)
        // console.log('deminify', subclassArray)
        if (filterMini.AC[className] == "all") {
          filter.areaCategory[className] = subclassArray
          continue
        }
        let subclass = []
        for (var subclassIndex of filterMini.AC[className]) {
          subclass.push(subclassArray[subclassIndex])
        }
        filter.areaCategory[className] = subclass
      }
      console.log(filter)

      for (var item of titleJson) {
        commit("setFilter", {
          param: ["areaCategory", item.class],
          value: filter.areaCategory[item.class]
        });
      }
      dispatch('update')
    },
    init({ commit, getters }) {
      // var item;
      // commit("ini")
      commit("setFilter", { param: 'name', value: "" });
      commit("setFilter", { param: 'author', value: "" });
      commit("setFilter", { param: 'description', value: "" });
      commit("setFilter", { param: 'iniImportanceMeasurement' })
      commit("setFilter", { param: 'add', value: [] });
      commit("setFilter", { param: 'del', value: [] });
      for (var item of titleJson) {
        commit("setFilter", {
          param: ["areaCategory", item.class],
          value: []
        });
      }
      let _filteredImportance = filteredImportance(getters)
      let _filteredAreaCategory = filteredAreaCategory(getters)
      let _filteredImportanceArea = filteredImportanceArea(_filteredImportance, _filteredAreaCategory)
      let _filtered = filtered(getters, _filteredImportanceArea)
      let _filteredTable = filteredTable(_filtered, _filteredImportanceArea)
      let _filteredIssn = filteredIssn(_filtered)
      let _nImportanceArea = nImportanceArea(getters)
      var updateResult = {
        nImportanceArea: _nImportanceArea,
        filteredImportance: _filteredImportance,
        filteredAreaCategory: _filteredAreaCategory,
        filteredImportanceArea: _filteredImportanceArea,
        filtered: _filtered,
        filteredTable: _filteredTable,
        filteredIssn: _filteredIssn,
      }
      commit('setResult', updateResult)
    },
    async update({ commit, getters }) {
      // var tic = Date.now()
      return new Promise(
        function (resolve) {
          // console.log(Date.now())
          let _filteredImportance = filteredImportance(getters)
          let _filteredAreaCategory = filteredAreaCategory(getters)
          let _filteredImportanceArea = filteredImportanceArea(_filteredImportance, _filteredAreaCategory)
          let _filtered = filtered(getters, _filteredImportanceArea)
          let _filteredTable = filteredTable(_filtered, _filteredImportanceArea)
          let _filteredIssn = filteredIssn(_filtered)
          let _nImportanceArea = nImportanceArea(getters)

          let updateResult = {
            nImportanceArea: _nImportanceArea,
            filteredImportance: _filteredImportance,
            filteredAreaCategory: _filteredAreaCategory,
            filteredImportanceArea: _filteredImportanceArea,
            filtered: _filtered,
            filteredTable: _filteredTable,
            filteredIssn: _filteredIssn,
          }
          commit('setResult', updateResult)
          // commit('toggleLoad', false)
          // console.log(Date.now(), updateResult) 
          resolve()
        }
      )
      // console.log(Date.now() - tic)
    },
    async updateAdd({ commit, state, getters }, payload) {
      // console.log('dif', payload)
      var diff = payload.value
      if (payload.param == "more") {
        return new Promise(
          function (resolve) {
            let _filtered = getters.filtered.concat(diff)
            let _filteredTable = state.result.filteredTable.
              concat(filteredTable(diff, state.result.filteredImportanceArea))
            let _filteredIssn = filteredIssn(_filtered)
            let updateResult = {
              filtered: _filtered,
              filteredTable: _filteredTable,
              filteredIssn: _filteredIssn,
            }
            commit('setResult', updateResult)
            // commit('toggleLoad', false)
            // console.log(Date.now(), updateResult)
            resolve()
          })
      }
      if (payload.param == "less") {
        if (getters.filteredImportanceArea.includes(diff[0])) {
          return new Promise(resolve => resolve())
        }
        return new Promise(
          function (resolve) {
            let _filtered = getters.filtered.filter(val => val != diff[0])
            let _filteredTable = state.result.filteredTable.filter(val => val.journal_abb != diff[0])
            let _filteredIssn = filteredIssn(_filtered)
            let updateResult = {
              filtered: _filtered,
              filteredTable: _filteredTable,
              filteredIssn: _filteredIssn,
            }
            commit('setResult', updateResult)
            commit('toggleLoad', false)
            // console.log(Date.now(), updateResult)
            resolve()
          })
      }
    },
    async updateDel({ commit, state, getters }, payload) {
      // console.log('dif', payload)
      var diff = payload.value
      if (payload.param == "more") {
        return new Promise(
          function (resolve) {
            let _filtered = getters.filtered.filter(val => val != diff[0])
            let _filteredTable = state.result.filteredTable.filter(val => val.journal_abb != diff[0])
            let _filteredIssn = filteredIssn(_filtered)
            let updateResult = {
              filtered: _filtered,
              filteredTable: _filteredTable,
              filteredIssn: _filteredIssn,
            }
            commit('setResult', updateResult)
            // commit('toggleLoad', false)
            // console.log(Date.now(), updateResult)
            resolve()
          })
      }
      if (payload.param == "less") {
        if (!getters.filteredImportanceArea.includes(diff[0])) {
          return new Promise(resolve => resolve())
        }
        return new Promise(
          function (resolve) {
            let _filtered = getters.filtered.concat(diff)
            let _filteredTable = state.result.filteredTable
              .concat(filteredTable(diff, state.result.filteredImportanceArea))
            let _filteredIssn = filteredIssn(_filtered)
            let updateResult = {
              filtered: _filtered,
              filteredTable: _filteredTable,
              filteredIssn: _filteredIssn,
            }
            commit('setResult', updateResult)
            // commit('toggleLoad', false)
            // console.log(Date.now(), updateResult)
            resolve()
          })
      }
    }
  },
  modules: {}
});

const scieJournal = [...new Set(filterJson.map(val => { return val.abb; }))];

function filteredImportance(getters) {
  var filteredImportance = filterJson.map(val => {
    return val.abb;
  });
  if (getters.getJCR.length > 0)
    filteredImportance = filterJson
      .filter(val => {
        return getters.getJCR.includes(val.Q);
      })
      .map(val => {
        return val.abb;
      });
  if (getters.getCAS.length > 0)
    filteredImportance = filterJson
      .filter(val => {
        return getters.getCAS.includes(val.B);
      })
      .map(val => {
        return val.abb;
      });
  if (getters.getTOP.length > 0)
    filteredImportance = filterJson
      .filter(val => {
        return getters.getTOP.includes(
          val.T == 1 ? "Top Journal" : "Non-Top Journal"
        );
      })
      .map(val => {
        return val.abb;
      });
  if (getters.getIMF[0] != 0 || getters.getIMF[1] != 300)
    filteredImportance = filterJson
      .filter(val => {
        return getters.getIMF[0] <= val.IF && val.IF <= getters.getIMF[1];
      })
      .map(val => {
        return val.abb;
      });
  return [...new Set(filteredImportance)];
}
function filteredAreaCategory(getters) {
  var filteredAreaCategory = [];
  // console.log(getters.getAreaCategory)
  if (
    Object.values(getters.getAreaCategory).filter(val => {
      return val.length > 0;
    }).length == 0
  ) {
    filteredAreaCategory = filterJson.map(val => {
      return val.abb;
    });
    return [...new Set(filteredAreaCategory)];
  }
  // var filteredAreaCategory = filterJson.map(val => { return val.abb });
  for (var journal of filterJson) {
    if (
      Object.values(getters.getAreaCategory[journal.class]).includes(
        journal.subclass
      )
    )
      filteredAreaCategory.push(journal.abb);
  }
  return [...new Set(filteredAreaCategory)];
}
function filteredImportanceArea(_filteredImportance, _filteredAreaCategory) {
  return _filteredImportance.filter(val => {
    return _filteredAreaCategory.indexOf(val) > -1;
  });
}
function filtered(getters, _filteredImportanceArea) {
  return [...new Set(_filteredImportanceArea
    .filter(val => { return !getters.getDel.includes(val) })
    .concat(getters.getAdd))];
}
function filteredTable(_filtered, _filteredImportanceArea) {
  var filteredTable = [];
  for (const journal of _filtered) {
    if (scieJournal.includes(journal)) {
      var journal_info = filterJson.filter(val => {
        return val.abb == journal;
      })[0];
      filteredTable.push({
        journal: journal_info.pubmed_journal,
        journal_abb: journal_info.abb,
        issn: journal_info.pubmed_issn,
        eissn: journal_info.pubmed_eissn,
        sci: true,
        imf: journal_info.IF,
        jcr: journal_info.Q,
        cas: journal_info.B,
        top: journal_info.T == 1 ? "Top Journal" : "Non-Top Journal",
        top_cn: journal_info.T == 1 ? "顶级刊物" : "非顶级刊物",
        filtered: _filteredImportanceArea.includes(journal)
          ? true
          : false
      });
    } else {
      journal_info = pubmedJson.filter(val => {
        return val.abb == journal;
      })[0];
      filteredTable.push({
        journal: journal_info.pubmed_journal,
        journal_abb: journal_info.abb,
        issn: journal_info.pubmed_issn,
        eissn: journal_info.pubmed_eissn,
        sci: false,
        imf: "NA",
        jcr: "NA",
        cas: "NA",
        top: "NA",
        filtered: _filteredImportanceArea.includes(journal)
          ? true
          : false
      });
    }
  }
  return filteredTable;
}
function filteredIssn(_filtered) {
  // console.log(pubmedJson
  //   .filter(val => { return getters.filtered.includes(val.abb) })
  //   .map(val => { return val.issn }))
  return pubmedJson
    .filter(val => { return _filtered.includes(val.abb) })
    .map(val => { return val.issn })
}
function filterMinify(state) {
  // console.log(JSON.stringify(state.filter).length)
  let filter = state.filter
  let filterMini = {}
  if (filter.name.length == 0) return 'Error: Filter name is required'
  filterMini.name = filter.name
  for (var i of ['author', 'description', 'add', 'del', 'customize']) {
    if (filter[i].length != 0) filterMini[i] = filter[i]
  }
  filterMini.IM = {}
  if (filter.importanceMeasurement.JCR.length != 0) filterMini.IM.JCR = filter.importanceMeasurement.JCR
  if (filter.importanceMeasurement.CAS.length != 0) filterMini.IM.CAS = filter.importanceMeasurement.CAS
  if (filter.importanceMeasurement.TOP.length != 0) filterMini.IM.TOP = filter.importanceMeasurement.TOP
  if (filter.importanceMeasurement.IMF[0] != 0 || filter.importanceMeasurement.IMF[1] != 300)
    filterMini.IM.IMF = filter.importanceMeasurement.IMF

  filterMini.AC = {}
  // classArray = titleJson.map(val => val.class)
  for (var className in filter.areaCategory) {
    if (filter.areaCategory[className].length == 0) continue
    let subclassMini = []
    let subclassArray = titleJson.filter(val => val.class == className)[0].subclasses.map(val => val.subclass)
    console.log(className, subclassArray)
    for (var subclassName of filter.areaCategory[className]) {
      subclassMini.push(subclassArray.indexOf(subclassName))
    }
    if (subclassMini.length == subclassArray.length) subclassMini = "all"
    filterMini.AC[className] = subclassMini
  }
  filterMini["no_journal"] = state.result.filtered.length
  return JSON.stringify(filterMini)
}
function nImportanceArea(getters) {
  // var count = {
  //   JCR: { Q1: 0, Q2: 0, Q3: 0, Q4: 0, NA: 0 },
  //   CAS: { B1: 0, B2: 0, B3: 0, B4: 0, NA: 0 },
  //   TOP: { 'Top Journal': 0, 'Non-Top Journal': 0 },
  //   IMF: 0
  // }
  var count = {}
  // var classJournalName = {}
  for (var journal of filterJson) {
    let qRename = journal.T == 1 ? 'Top Journal' : 'Non-Top Journal'
    let activateIMF = getters.getJCR.length == 0 && getters.getCAS.length == 0 && getters.getTOP.length == 0
    let isIncluded = false
    if (activateIMF) {
      isIncluded = getters.getIMF[0] <= journal.IF && journal.IF <= getters.getIMF[1]
    } else {
      isIncluded = getters.getJCR.includes(journal.Q) || getters.getCAS.includes(journal.B) || getters.getTOP.includes(qRename)
    }
    if (isIncluded) {
      if (count[journal.class] == undefined) {
        count[journal.class] = {}
        // classJournalName[journal.class] = []
      }
      if (count[journal.class][journal.subclass] == undefined) {
        count[journal.class][journal.subclass] = 1
      } else {
        count[journal.class][journal.subclass] += 1
      }
      // classJournalName[journal.class].push(journal.abb)
    }
    // var nClass = {}
    //   for (var i in classJournalName) {
    //     nClass[i] = [...new Set(classJournalName[i])].length
    // }//tooooooooooooooooo slow deperecated
    // count["JCR"][journal.Q] += 1
    // count["CAS"][journal.B] += 1
    // let qRename = journal.T == 1 ? 'Top Journal' : 'Non-Top Journal'
    // count["TOP"][qRename] += 1
    // if (getters.getIMF[0] <= journal.IF && journal.IF <= getters.getIMF[1]) count['IMF'] += 1
  }
  // console.log('count', count)
  return count
}