<template>
  <div>
    <v-dialog
      v-model="exportArticleOpen"
      v-if="exportArticleOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Export Article</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="6" class="pb-0">
                <v-select
                  v-model="exportArticleSelect"
                  :items="exportArticleSelectList"
                  label="Export Project"
                ></v-select>
              </v-col>
              <v-col cols="5" class="pb-0">
                <v-select
                  v-model="referenceType"
                  :items="referenceTypeList"
                  label="Reference Type"
                ></v-select>
              </v-col>
              <v-col cols="1" class="pb-0 d-flex flex-column justify-center">
                <v-icon
                  class="primary--text pb-1"
                  style="cursor: pointer"
                  @click="referenceCopy"
                  >mdi-content-copy</v-icon
                >
              </v-col>
              <v-col cols="12" class="px-3 py-0">
                <v-textarea
                  v-model="reference"
                  label="Reference"
                  counter
                  filled
                  no-resize
                  spellcheck="false"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-3" color="primary" @click="export2File">
            Export to file
            <!-- <v-icon right>mdi-database-import</v-icon> -->
          </v-btn>
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="isCopySuccess" timeout="3000" top color="success">
      Successfully Copy to Clipboard

      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="isCopySuccess = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { get_bibliography } from "./citation.js";
export default {
  name: "exportArticle",
  props: {
    exportArticleOpen: Boolean,
    project: String,
    projectDataAll: Object,
    selectedArticles: Array,
    projectDataFiltered: Object,
  },
  data: function () {
    return {
      exportArticleSelect: "",
      exportArticleSelectList: [],
      referenceType: "BIBTEX",
      referenceTypeList: ["BIBTEX", "RIS", "CSL-JSON"],
      reference: "",
      finalCsls: [],
      isCopySuccess: false,
    };
  },
  watch: {
    exportArticleOpen(n) {
      if (n == true) {
        let exportArticleSelectList =
          this.projectDataAll._projectSummary.projectList.slice();
        exportArticleSelectList.unshift("SELECTED ARTICLES");
        this.exportArticleSelectList = exportArticleSelectList;
        if (this.selectedArticles.length == 0) {
          this.exportArticleSelect = this.project;
        } else {
          this.exportArticleSelect = "SELECTED ARTICLES";
        }
        this.getFinalCsls();
        this.getBibliography();
      }
    },
    exportArticleSelect(n) {
      this.getFinalCsls();
      this.getBibliography();
    },
    referenceType() {
      this.getBibliography();
    },
  },
  methods: {
    referenceCopy() {
      let str_plain = this.reference;
      function listener(e) {
        // e.clipboardData.setData("text/html", str_html);
        e.clipboardData.setData("text/plain", str_plain);
        e.preventDefault();
      }
      document.addEventListener("copy", listener);
      document.execCommand("copy");
      document.removeEventListener("copy", listener);
      this.isCopySuccess = true;
    },
    getBibliography() {
      this.reference = get_bibliography(this.finalCsls, this.referenceType);
    },
    getFinalCsls() {
      let finalCsls = [];
      var _this = this;
      if (_this.exportArticleSelect != "SELECTED ARTICLES") {
        var items = this.projectDataAll[_this.exportArticleSelect]["items"];
        console.log(items);
        for (var [label, item] of Object.entries(items)) {
          finalCsls.push(item.csl);
        }
      } else {
        for (var [label, item] of Object.entries(this.projectDataFiltered)) {
          if (_this.selectedArticles.indexOf(label) != -1) {
            finalCsls.push(item.csl);
          }
        }
      }
      this.finalCsls = finalCsls;
    },
    export2File() {
      let data = this.reference;
      let file = new Blob([data], { type: "text/plain" });
      let filename = `${this.referenceType}.txt`;
      var url = URL.createObjectURL(file);
      chrome.downloads.download({
        url: url,
        filename: filename,
        saveAs: true,
      });
    },
  },
};
</script>

