<template>
  <v-list class="py-0">
    <!-- <v-list-item
      @click.stop="toggleCompleteSelection"
      :input-value="allNodesSelected"
      active-class="primary white--text"
    >
      <v-list-item-action>
        <v-checkbox
          :indeterminate="someNodesSelected"
          :input-value="allNodesSelected"
          @click.stop="toggleCompleteSelection"
          color="white"
        />
      </v-list-item-action>
      <v-list-item-content>
        <v-list-item-title>Select All</v-list-item-title>
      </v-list-item-content>
    </v-list-item>-->

    <v-list-item-group v-model="selected_nodes" multiple>
      <v-list-item
        v-for="(node, index) in nodes"
        :key="index"
        :value="node"
        active-class="background-color:'#b8c2d7'"
      >
        <template v-slot:default="{ active }">
          <v-list-item-action>
            <v-checkbox :input-value="active" color="#20558a" />
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title v-text="nodesDisplay[index]"></v-list-item-title>
          </v-list-item-content>
        </template>
      </v-list-item>
    </v-list-item-group>
  </v-list>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "importanceMeasurement",
  props: {
    nodesClass: String,
    nodesName: String,
    nodes: Array,
    nodesDisplay: Array,
  },
  data() {
    return {};
  },
  computed: {
    ...mapState({
      filter: (state) => state.filter,
    }),
    selected_nodes: {
      get: function () {
        return this.filter[this.nodesClass][this.nodesName];
      },
      set: function (val) {
        this.$store.commit("setFilter", {
          param: [this.nodesClass, this.nodesName],
          value: val,
        });
      },
    },
  },
  methods: {},
  watch: {},
};
</script>
