<template>
  <div>
    <v-dialog
      v-model="referenceSuppOpen"
      v-if="referenceSuppOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Open Supplementary Material</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="">
                <span class="primary--text font-weight-bold text-button"
                  >Supplementary Material</span
                ></v-col
              >
              <v-col cols="12" class="pt-0">
                <div v-for="(item, index) in suppItem" :key="index">
                  <div
                    class="d-flex flex-row hover"
                    v-if="suppItemExists[item.suppLabel] == true"
                  >
                    <div class="col-9 py-1">
                      <span class="grey--text font-weight-bold">{{
                        item.suppLabel
                      }}</span>
                    </div>
                    <div class="col-3 py-1 d-flex flex-row justify-end px-1">
                      <v-icon
                        class="primary--text text--lighten-2"
                        style="cursor: pointer"
                        left
                        @click="open(item)"
                        title="Open"
                        >mdi-book-open</v-icon
                      >
                      <v-icon
                        class="primary--text text--lighten-2"
                        style="cursor: pointer"
                        left
                        title="Show in folder"
                        @click="openFolder(item)"
                        >mdi-folder-open</v-icon
                      >
                      <!-- <v-icon
                        class="info--text"
                        style="cursor: pointer"
                        left
                        @click="
                          editProject = item;
                          renameProjectOpen = true;
                          projectRenamed = '';
                        "
                        >mdi-pencil</v-icon
                      >-->
                      <v-icon
                        class="error--text"
                        style="cursor: pointer"
                        @click="
                          deleteSuppOpen = true;
                          deleteSupp = item;
                        "
                        >mdi-delete</v-icon
                      >
                    </div>
                  </div>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="deleteSuppOpen" max-width="800" persistent>
      <v-card>
        <v-card-title class="error white--text">
          <span>Delete Supplement Material</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="">
                <span class="primary--text text-subtitle-1">{{
                  `Do you want to delete supplement material: ${deleteSupp.suppLabel}?`
                }}</span>
                <v-checkbox
                  v-model="deleteFile"
                  label="Delete the linked file"
                  color="error"
                >
                </v-checkbox>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-3"
            color="error"
            @click="
              del(deleteSupp);
              deleteSuppOpen = false;
            "
          >
            Delete
            <v-icon right>mdi-delete</v-icon>
          </v-btn>
          <v-btn class="mr-6" color="primary" @click="deleteSuppOpen = false">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { delItemSecondary } from "./setProjectData";
export default {
  name: "referenceSupp",
  props: {
    referenceSuppOpen: Boolean,
    referenceSupp: Object,
  },
  data() {
    return {
      suppItem: {},
      suppItemExists: {},
      deleteSuppOpen: false,
      deleteSupp: {},
      deleteFile: true,
    };
  },
  watch: {
    referenceSuppOpen(n) {
      if (n == true) {
        // this.suppItem = this.referenceSupp.data.supplement;
        var _this = this;
        this.suppItem = Object.values(this.referenceSupp.data.supplement);
        this.suppItemExists = {};
        for (var key of Object.keys(this.referenceSupp.data.supplement)) {
          _this.$set(_this.suppItemExists, key, true);
        }
        this.deleteFile = true;
        // console.log(this.suppItem);
      }
    },
    suppItem: {
      deep: true,
      handler(n, o) {
        if (this.suppItem.length == 0) {
          this.$emit("close");
        }
      },
    },
  },
  methods: {
    open(item) {
      chrome.downloads.open(item.downloadId);
    },
    openFolder(item) {
      chrome.downloads.show(item.downloadId);
    },
    del(item) {
      if (this.deleteFile) {
        chrome.downloads.removeFile(item.downloadId, () => {});
      }
      this.suppItem = this.suppItem.filter((x) => {
        return x.suppLabel != item.suppLabel;
      });
      let project = this.referenceSupp.data.project;
      let itemName = this.referenceSupp.label;
      delItemSecondary(project, itemName, "supplement", item.suppLabel);
    },
  },
  beforeUpdate() {
    let _this = this;
    if (this.suppItem == undefined) return;
    var project = this.referenceSupp.data.project;
    var itemName = this.referenceSupp.label;
    setInterval(async function () {
      for (var item of _this.suppItem) {
        var exists = await new Promise((resolve) => {
          chrome.downloads.search({ id: item.downloadId }, function (result) {
            resolve(result[0].exists);
          });
        });
        if (exists == false) {
          delItemSecondary(project, itemName, "supplement", item.suppLabel);
          //    _this.suppItem[item.suppLabel].exists = false;
          _this.suppItem = _this.suppItem.filter((x) => {
            return x.suppLabel != item.suppLabel;
          });
          console.log(_this.suppItem);
          _this.$set(_this.suppItemExists, item.suppLabel, false);
        } else {
          _this.$set(_this.suppItemExists, item.suppLabel, true);
        }
      }
    }, 1000);
  },
};
</script>

<style scoped>
.hover {
  background-color: white;
  cursor: default !important;
}
.hover:hover {
  background-color: gainsboro;
}
</style>