<template>
  <div>
    <v-dialog
      v-model="importArticleOpen"
      v-if="importArticleOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Import Article</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="6" class="pb-0">
                <v-select
                  v-model="projectAdd"
                  :items="projectList"
                  label="Add to Project"
                ></v-select>
              </v-col>
              <v-col cols="6" class="pb-0">
                <v-select
                  v-model="referenceType"
                  :items="referenceTypeList"
                  label="Reference Type"
                ></v-select>
              </v-col>
              <v-col cols="12" class="px-3 py-0" v-show="isAnalysing != true">
                <v-textarea
                  v-model="reference"
                  label="Reference"
                  counter
                  filled
                  no-resize
                  spellcheck="false"
                ></v-textarea>
              </v-col>
              <v-col cols="12" class="px-3" v-if="isAnalysing == true">
                <v-progress-linear
                  :buffer-value="analyseProgressBuffer"
                  color="primary"
                  stream
                  :value="analyseProgress"
                  height="25"
                  ><template v-slot:default="{ value }">
                    <span
                      class="error--text text--lighten-1 font-weight-bold"
                      >{{
                        `Analysing Reference ${successNumber}/${importNumber} Please Wait`
                      }}</span
                    >
                  </template></v-progress-linear
                >
              </v-col>
              <!-- <v-col cols="1" class="d-flex justify-center">
                <span>{{ `${sucessNumber}/${importNumber}` }}</span>
              </v-col> -->
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-3"
            color="primary"
            @click="$refs.fileInput.click()"
            v-if="isAnalysing != true"
          >
            Read From File
            <!-- <v-icon right>mdi-book-open</v-icon> -->
          </v-btn>
          <v-btn
            class="mr-3"
            color="primary"
            @click="importArticle"
            v-if="isAnalysing != true"
          >
            Import
            <v-icon right>mdi-database-import</v-icon>
          </v-btn>
          <v-btn
            class="mr-3"
            color="error"
            @click="stopImport"
            v-if="isAnalysing == true"
          >
            Stop
            <v-icon right>mdi-alert-octagon</v-icon>
          </v-btn>
          <v-btn
            class="mr-6"
            color="primary"
            @click="$emit('close', { project: projectAdd })"
            v-if="isAnalysing != true"
          >
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="importArticleConfirmOpen"
      v-if="importArticleConfirmOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="success white--text">
          <span>Confirm</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="px-3 pt-2 pb-0">
                <v-select
                  v-model="projectAdd"
                  :items="projectList"
                  label="Add to Project"
                ></v-select>
              </v-col>
              <v-col cols="12" class="px-3 py-0">
                <span class="primary--text text-subtitle-1">{{
                  `Do you want to add ${importNumber} articles to Project: ${projectAdd}?`
                }}</span>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-6" color="success" @click="confirm">
            Confirm
            <v-icon right>mdi-check</v-icon>
          </v-btn>
          <v-btn
            class="mr-6"
            color="primary"
            @click="importArticleConfirmOpen = false"
          >
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="isConfirmSuccess" timeout="10000" top color="success">
      {{ confirmSuccessMessage }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="isConfirmSuccess = false"
        >
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
    <v-snackbar v-model="isError" timeout="10000" top color="warning">
      {{ warningMessage }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="isError = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
    <input
      ref="fileInput"
      type="file"
      :accept="acceptType"
      @change="fileChange"
      style="display: none"
    />
  </div>
</template>

<script>
import { get_csl } from "./citation.js";
import { fetchFinalCsl } from "./fetchData";
import { addImportArticle } from "./setProjectData";
export default {
  name: "importArticle",
  props: {
    importArticleOpen: Boolean,
    project: String,
    projectDataAll: Object,
  },
  components: {
    // newArticleAdd,
  },
  data: function () {
    return {
      projectAdd: "",
      referenceTypeList: ["BIBTEX", "RIS", "CSL-JSON"],
      referenceType: "BIBTEX",
      reference: "",
      isAnalysing: false,
      importNumber: undefined,
      successNumber: undefined,
      alreadyExistsNumber: 0,
      finalCsls: [],
      importSeq: 0,
      importArticleConfirmOpen: false,
      isConfirmSuccess: false,
      confirmSuccessMessage: "",
      isError: false,
      warningMessage: "",
      //   importArticleProgressOpen: false,
    };
  },
  computed: {
    projectList() {
      return this.projectDataAll["_projectSummary"]["projectList"];
    },
    analyseProgress() {
      if (this.importNumber == 0) return 0;
      return (this.successNumber / this.importNumber) * 100;
    },
    analyseProgressBuffer() {
      if (this.importNumber == 0) return 0;
      return ((this.successNumber + 1) / this.importNumber) * 100;
    },
    acceptType() {
      if (this.referenceType == "BIBTEX") return ".txt,.bib";
      if (this.referenceType == "RIS") return ".txt,.ris";
      if (this.referenceType == "CSL-JSON") return ".txt";
    },
  },
  watch: {
    importArticleOpen(n) {
      if (n == false) return;
      this.projectAdd = this.project;
      this.reference = "";
    },
    // reference() {
    //   let csl = get_csl(this.reference, this.referenceType);
    //   console.log(csl.cslObject);
    // },
  },
  methods: {
    async importArticle() {
      if (this.reference.trim() == "") {
        this.isError = true;
        this.warningMessage = "No reference detected";
        return;
      }
      try {
        var csls = get_csl(this.reference, this.referenceType).cslObject;
        console.log(csls);
      } catch {
        this.isError = true;
        this.warningMessage = "Fail to parse reference";
        return;
      }
      var _this = this;
      this.isAnalysing = true;
      this.finalCsls = [];
      this.importSeq += 1;
      this.importNumber = csls.length;
      this.successNumber = 0;
      var oldSeq = _this.importSeq * 1; //deep copy of importSeq for determine if stop is clicked
      for (var csl of csls) {
        // console.log(oldSeq, _this.importSeq);
        if (oldSeq != _this.importSeq) continue; //an id to determine if stop is clicked
        var finalCsl;
        if (csl.DOI == undefined) {
          finalCsl = "NA";
        } else {
          finalCsl = await fetchFinalCsl(csl.DOI);
        }
        // console.log(finalCsl, oldSeq, _this.importSeq);
        if (oldSeq != _this.importSeq) continue;
        if (finalCsl == "NA") {
          _this.finalCsls.push(csl);
        } else {
          _this.finalCsls.push(finalCsl);
        }
        _this.successNumber += 1;
      }
      if (oldSeq != _this.importSeq) return;
      this.isAnalysing = false;
      this.importArticleConfirmOpen = true;
      //   console.log("final", this.finalCsls);
    },
    stopImport() {
      this.finalCsls = [];
      this.importSeq += 1;
      this.successNumber = 0;
      this.importNumber = 0;
      this.isAnalysing = false;
    },
    async confirm() {
      console.log(this.finalCsls);
      var ret = await addImportArticle(this.projectAdd, this.finalCsls);
      //   console.log(ret);
      var confirmSuccessMessagePart1 = "",
        confirmSuccessMessagePart2 = "";
      confirmSuccessMessagePart1 = `Add ${ret.successAddNumber} article(s) to Project: ${this.projectAdd}`;
      if (ret.alreadyExitsNumber != 0) {
        confirmSuccessMessagePart2 = `Info: ${ret.alreadyExitsNumber} article(s) already exist in Project: ${this.projectAdd}`;
      }
      this.confirmSuccessMessage =
        confirmSuccessMessagePart1 + "\n" + confirmSuccessMessagePart2;
      this.importArticleConfirmOpen = false;
      this.isConfirmSuccess = true;
    },
    fileChange(event) {
      if (event.target.files[0] == undefined) return;
      console.log(event.target.files[0]);
      let file = event.target.files[0];
      let reader = new FileReader();
      var _this = this;

      // event fired when file reading finished
      reader.addEventListener("load", function (e) {
        let text = e.target.result;
        _this.reference = text;
        // console.log(text);
      });

      // event fired when file reading failed
      reader.addEventListener("error", function () {
        _this.isError = true;
        _this.warningMessage = "Fail to read file";
        return;
      });

      // read file as text file
      reader.readAsText(file);
    },
  },
};
</script>

<style>
.v-snack__content {
  white-space: pre-line !important;
}
</style>
