<template>
  <div>
    <v-dialog
      id="dialogAttach"
      v-model="referenceAttachSuppOpen"
      v-if="referenceAttachSuppOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Attach Supplementary Material</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="pt-3 pb-1">
                <v-text-field
                  v-model="label"
                  label="Supplementary Material Label"
                  prepend-icon="mdi-label-outline"
                  spellcheck="false"
                  clearable
                >
                </v-text-field>
              </v-col>
              <v-col cols="12" class="pt-3 pb-1">
                <v-text-field
                  v-model="urlInput"
                  label="Supplementary Material URL"
                  prepend-icon="mdi-link"
                  spellcheck="false"
                  clearable
                  ><template v-slot:append-outer>
                    <v-btn dark color="success" small fab @click="linkUrl">
                      <v-icon dark>mdi-link-plus</v-icon>
                    </v-btn>
                  </template>
                </v-text-field>
              </v-col>
              <v-col cols="12" class="pt-3 pb-1">
                <v-file-input
                  label="Upload Supplementary Material"
                  v-model="pdfInput"
                  ><template v-slot:append-outer>
                    <v-btn dark color="success" small fab @click="linkPdf">
                      <v-icon dark>mdi-link-plus</v-icon>
                    </v-btn>
                  </template></v-file-input
                >
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <!-- <v-btn class="mr-3" color="error" @click="del()">
          Delete
          <v-icon right>mdi-delete</v-icon>
        </v-btn> -->
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar
      v-model="alert.showAlert"
      :timeout="alert.timeout"
      top
      :color="alert.color"
    >
      {{ alert.message }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="alert.showAlert = false"
        >
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { downloadSupp } from "./downloadArticle";
export default {
  name: "referenceAttachSupp",
  props: {
    referenceAttachSuppOpen: Boolean,
    referenceAttachSupp: Object,
  },
  data() {
    return {
      label: "",
      urlInput: "",
      pdfInput: undefined,
      alert: {
        showAlert: false,
        color: "success",
        meassage: "",
        timeout: 3000,
      },
    };
  },
  watch: {
    referenceAttachSuppOpen(n) {
      if (n == true) {
        this.urlInput = "";
        this.pdfInput = undefined;
      }
    },
    label() {
      if (this.label == null) {
        this.label = "";
      }
    },
    pdfInput: {
      deep: true,
      handler: function () {
        console.log(this.pdfInput);
        if (this.pdfInput == undefined) return;
        if (this.label == null || this.label.trim() == "") {
          this.label = this.pdfInput.name.substring(
            0,
            this.pdfInput.name.lastIndexOf(".")
          );
        }
      },
    },
  },
  methods: {
    async linkUrl() {
      // if (this.label == "") {
      //   this.alert.message = "Please set a label for supplementary material";
      //   this.alert.color = "warning";
      //   this.alert.showAlert = true;
      //   return;
      // }
      let isSuccess = await downloadSupp(
        this.urlInput,
        this.referenceAttachSupp,
        this.label
      );
      if (isSuccess) {
        this.alert.message = "Success save supplementary matrial";
        this.alert.color = "success";
        this.alert.showAlert = true;
      } else {
        this.alert.message = "Fail save supplementary matrial";
        this.alert.color = "warning";
        this.alert.showAlert = true;
      }
      this.renew();
      //   this.$emit("close");
    },
    async linkPdf() {
      // if (this.label == "") {
      //   this.alert.message = "Please set a label for supplementary material";
      //   this.alert.color = "warning";
      //   this.alert.showAlert = true;
      //   return;
      // }
      let url = URL.createObjectURL(this.pdfInput);
      //   console.log(this.pdfInput, this.referenceAttachSupp);
      let isSuccess = await downloadSupp(
        url,
        this.referenceAttachSupp,
        this.label
      );
      if (isSuccess) {
        this.alert.message = "Success save supplementary material";
        this.alert.color = "success";
        this.alert.showAlert = true;
      } else {
        this.alert.message = "Fail save supplementary material";
        this.alert.color = "warning";
        this.alert.showAlert = true;
      }
      this.renew();
    },
    renew() {
      this.pdfInput = undefined;
      this.label = "";
      this.urlInput = "";
    },
  },
};
</script>

<style>
</style>