<template>
  <div>
    <v-dialog
      v-model="batchDownloadOpen"
      v-if="batchDownloadOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Batch Download Article</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 400px; overflow-y: auto">
          <v-container>
            <v-stepper v-model="step" vertical class="elevation-0">
              <v-stepper-step
                :complete="step > 1"
                step="1"
                style="cursor: pointer"
                @click="step = 1"
              >
                Downloads Settings
                <!-- <small>Summarize if needed</small> -->
              </v-stepper-step>

              <v-stepper-content step="1">
                <v-card color="grey lighten-1" class="mb-3">
                  <v-img
                    v-if="isEdge"
                    class="shrink mr-2"
                    contain
                    src="../assets/download-setting-edge-en.png"
                    width="1000"
                  />
                  <v-img
                    v-if="!isEdge"
                    class="shrink mr-2"
                    contain
                    src="../assets/download-setting-chrome-en.png"
                    width="1000"
                  />
                </v-card>
                <p v-if="isEdge" class="primary--text text-body-2">
                  1. Go to the downloads settings by typing
                  'edge://settings/downloads<span
                    style="cursor: pointer"
                    @click="copy(`edge://settings/downloads`)"
                    >🗍</span
                  >' in browser address bar.
                </p>
                <p v-if="!isEdge" class="primary--text text-body-2">
                  1. Go to the downloads settings by typing
                  'chrome://settings/downloads<span
                    style="cursor: pointer"
                    @click="copy(`chrome://settings/downloads`)"
                    >🗍</span
                  >' in browser address bar.
                </p>
                <p class="primary--text text-body-2">
                  2. Set a location for downloaded articles.
                </p>
                <p v-if="isEdge" class="error--text">
                  3. <b>DISABLE</b> the switch named 'Ask me what to do with
                  each download'.
                </p>
                <p v-if="!isEdge" class="error--text">
                  3. <b>DISABLE</b> the switch named 'Ask where to save each
                  file before downloading'.
                </p>
                <v-btn color="primary" @click="step = 2">
                  Downloads settings have been set correctly
                </v-btn>
              </v-stepper-content>

              <v-stepper-step
                :complete="step > 2"
                step="2"
                style="cursor: pointer"
                @click="step = 2"
              >
                Select a project and Download
              </v-stepper-step>

              <v-stepper-content step="2" class="pt-3">
                <v-select
                  v-model="projectSelect"
                  :items="projectSelectList"
                  label="Download all articles avaliable in "
                ></v-select>
                <v-btn class="d-block" color="primary" @click="download">
                  Download
                </v-btn>
              </v-stepper-content>
            </v-stepper>
            <ad />
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-3"
            color="primary"
            @click="downloadDetailOpen = true"
          >
            Last Download Detail
            <v-icon right>mdi-format-list-bulleted</v-icon>
          </v-btn>
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="downloadDetailOpen"
      v-if="downloadDetailOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Download Article Detail</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 400px; overflow-y: auto">
          <v-container>
            <div class="pb-3 d-absolute">
              <v-progress-linear
                color="primary"
                stream
                :value="
                  ((downloadFailNumber + downloadSuccessNumber) /
                    downloadNumber) *
                  100
                "
                height="25"
                ><template>
                  <span class="white--text text--lighten-1 font-weight-bold">{{
                    `Project: [${projectSelect}] Downloading ${downloadNumber} articles [`
                  }}</span>
                  <span class="success--text text--lighten-1 font-weight-bold">
                    {{ `${downloadSuccessNumber} successs` }}</span
                  >
                  <span
                    class="warning--text text--lighten-1 font-weight-bold"
                    v-if="downloadDownloadingNumber != 0"
                    style="white-space: pre"
                  >
                    {{ ` ${downloadDownloadingNumber} downloading` }}</span
                  >
                  <span
                    class="info--text text--lighten-1 font-weight-bold"
                    v-if="downloadWaitingNumber != 0"
                    style="white-space: pre"
                  >
                    {{ ` ${downloadWaitingNumber} wait` }}</span
                  >
                  <span
                    class="error--text text--lighten-1 font-weight-bold"
                    v-if="downloadFailNumber != 0"
                    style="white-space: pre"
                  >
                    {{ ` ${downloadFailNumber} fail` }}</span
                  >
                  <span class="white--text text--lighten-1 font-weight-bold">{{
                    `]`
                  }}</span>
                </template></v-progress-linear
              >
            </div>
            <div>
              <v-expansion-panels v-model="downloadDetailPanel" multiple>
                <v-expansion-panel v-show="downloadWaitingNumber != 0">
                  <v-expansion-panel-header>{{
                    `Waiting [${downloadWaitingNumber}]`
                  }}</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <div v-for="(item, index) in downloadItems" :key="index">
                      <div
                        class="white--text text-subtitle-2 pa-2 my-1 rounded-xl"
                        style="
                          overflow: hidden;
                          white-space: nowrap;
                          text-overflow: ellipsis;
                        "
                        :title="item.csl.title"
                        :style="backgroundColor(item)"
                        v-if="downloadItemStatus[item.label] == 'Waiting'"
                      >
                        {{
                          `[${downloadItemStatus[item.label]}]  ${
                            item.csl.title
                          }`
                        }}
                      </div>
                    </div>
                  </v-expansion-panel-content>
                </v-expansion-panel>
                <v-expansion-panel v-show="downloadDownloadingNumber != 0">
                  <v-expansion-panel-header>{{
                    `Downloading [${downloadDownloadingNumber}]`
                  }}</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <div v-for="(item, index) in downloadItems" :key="index">
                      <div
                        class="white--text text-subtitle-2 pa-2 my-1 rounded-xl"
                        style="
                          overflow: hidden;
                          white-space: nowrap;
                          text-overflow: ellipsis;
                        "
                        :title="item.csl.title"
                        :style="backgroundColor(item)"
                        v-if="downloadItemStatus[item.label] == 'Downloading'"
                      >
                        {{
                          `[${downloadItemStatus[item.label]}]  ${
                            item.csl.title
                          }`
                        }}
                      </div>
                    </div>
                  </v-expansion-panel-content>
                </v-expansion-panel>
                <v-expansion-panel v-show="downloadSuccessNumber != 0">
                  <v-expansion-panel-header>{{
                    `Success [${downloadSuccessNumber}]`
                  }}</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <div v-for="(item, index) in downloadItems" :key="index">
                      <div
                        class="white--text text-subtitle-2 pa-2 my-1 rounded-xl"
                        style="
                          overflow: hidden;
                          white-space: nowrap;
                          text-overflow: ellipsis;
                        "
                        :title="item.csl.title"
                        :style="backgroundColor(item)"
                        v-if="downloadItemStatus[item.label] == 'Success'"
                      >
                        {{
                          `[${downloadItemStatus[item.label]}]  ${
                            item.csl.title
                          }`
                        }}
                      </div>
                    </div>
                  </v-expansion-panel-content>
                </v-expansion-panel>
                <v-expansion-panel v-show="downloadFailNumber != 0">
                  <v-expansion-panel-header>{{
                    `Fail [${downloadFailNumber}]`
                  }}</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <div v-for="(item, index) in downloadItems" :key="index">
                      <div
                        class="white--text text-subtitle-2 pa-2 my-1 rounded-xl"
                        style="
                          overflow: hidden;
                          white-space: nowrap;
                          text-overflow: ellipsis;
                        "
                        :title="item.csl.title"
                        :style="backgroundColor(item)"
                        v-if="downloadItemStatus[item.label] == 'Fail'"
                      >
                        {{
                          `[${downloadItemStatus[item.label]}]  ${
                            item.csl.title
                          }`
                        }}
                      </div>
                    </div>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
              <ad class="pt-4" />
            </div>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <!-- <v-btn class="mr-3" color="error">
            
            stop
            <v-icon right>mdi-alert-octagon</v-icon>
          </v-btn> -->
          <v-btn class="mr-6" color="primary" @click="hideDetail">
            <!-- @click="hide" -->
            hide
            <v-icon right>mdi-eye-off</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="downloadSuccessOpen"
      v-if="downloadSuccessOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="success white--text">
          <span>Download Complete</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 400px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="pt-3 pb-1 px-1">
                <span class="primary--text text-subtitle-1">{{
                  `EasyPubMed has helped you download ${downloadSuccessNumber} article(s) for Project: ${projectSelect}. Since EasyPubMed is completely free,  would you consider supporting us a cup of coffee($5), a box of snacks($10) or a good lunch($20)? We will feel great encouraged.`
                }}</span>
              </v-col>
              <v-col cols="12" class="pt-3 pb-1 px-1">
                <span class="primary--text text-subtitle-1"
                  >Welcome to use
                  <a
                    href="https://www.paypal.com/paypalme/easypubmed"
                    target="_blank"
                    >Paypal</a
                  >
                  or donate via QR code (Alipay, WeChatpay or QQpay).</span
                >
              </v-col>
              <v-col
                cols="12"
                class="pt-2 pb-0 px-1 d-flex flex-row justify-space-between"
              >
                <v-img
                  src="../assets/donation.png"
                  width="60px"
                  contain
                  @click="
                    link(
                      `https://github.com/naivenaive/EasyPubMed/blob/master/EasyPubMed%20User%20Manual.md#donation`
                    )
                  "
                  style="cursor: pointer"
                ></v-img>
                <v-img
                  src="../assets/author.png"
                  width="60px"
                  contain
                  @click="
                    link(
                      `https://github.com/naivenaive/EasyPubMed/blob/master/EasyPubMed%20User%20Manual.md#donation`
                    )
                  "
                  style="cursor: pointer"
                ></v-img>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-6"
            color="primary"
            @click="downloadSuccessOpen = false"
          >
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="isCopySuccess" timeout="3000" top color="success">
      Successfully Copy to Clipboard

      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="isCopySuccess = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import ad from "./ad";
import { downloadArticle } from "./downloadArticle";
import { getScihubPdf } from "./getScihubPdf";
import { parse } from "./parse";
export default {
  name: "batchDownload",
  components: {
    ad,
  },
  props: {
    batchDownloadOpen: Boolean,
    project: String,
    projectDataAll: Object,
    selectedArticles: Array,
    projectDataFiltered: Object,
    options: Object,
  },
  data() {
    return {
      step: 1,
      projectSelect: "",
      projectSelectList: [],
      downloadDetailOpen: false,
      downloadSuccessOpen: false,
      downloadItems: {},
      downloadNumber: 0,
      downloadSuccessNumber: 0,
      downloadFailNumber: 0,
      downloadWaitingNumber: 0,
      downloadDownloadingNumber: 0,
      downloadItemStatus: {},
      isDownloading: false,
      downloadComplete: false,
      downloadDetailPanel: [],
      isCopySuccess: false,
    };
  },
  computed: {
    isEdge() {
      var isChrome =
        !!window.chrome &&
        (!!window.chrome.webstore || !!window.chrome.runtime);
      // Edge (based on chromium) detection
      var isEdgeChromium = isChrome && navigator.userAgent.indexOf("Edg") != -1;
      return isEdgeChromium;
    },
  },
  watch: {
    batchDownloadOpen(n) {
      if (n == true) {
        this.step = 1;
        let projectSelectList =
          this.projectDataAll._projectSummary.projectList.slice();
        // projectSelectList.unshift("ALL PROJECT");
        this.projectSelectList = projectSelectList;
        this.projectSelect = this.project;
        console.log("a", this.isDownloading, this.downloadDetailOpen);
        if (this.isDownloading == true) {
          this.downloadDetailOpen = true;
        }
        if (this.downloadComplete == true) {
          this.downloadDetailOpen = true;
          this.downloadSuccessOpen = true;
        }
      }
    },
  },
  methods: {
    link(url) {
      window.open(url);
    },
    copy(text) {
      function listener(e) {
        // e.clipboardData.setData("text/html", str_html);
        e.clipboardData.setData("text/plain", text);
        e.preventDefault();
      }
      document.addEventListener("copy", listener);
      document.execCommand("copy");
      document.removeEventListener("copy", listener);
      this.isCopySuccess = true;
    },
    hideDetail() {
      this.downloadDetailOpen = false;
      if (this.isDownloading == true) this.$emit("close");
      if (this.downloadComplete == true) {
        this.$emit("close");
        this.downloadComplete = false;
      }
    },
    backgroundColor(item) {
      let status = this.downloadItemStatus[item.label];
      let color;
      if (status == "Success") {
        color = "#4caf50";
      } else if (status == "Waiting") {
        color = "#698fc5";
      } else if (status == "Fail") {
        color = "#ff7a76";
      } else if (status == "Downloading") {
        color = "#ffa72d";
      }
      return { "background-color": color };
    },
    async download() {
      this.$emit("startDownload");
      this.downloadDetailOpen = true;
      this.isDownloading = true;
      let downloadItems = this.projectDataAll[this.projectSelect].items;
      this.downloadItems = downloadItems;
      this.downloadSuccessNumber = 0;
      this.downloadFailNumber = 0;
      this.downloadNumber = 0;
      this.downloadWaitingNumber = 0;
      this.downloadDownloadingNumber = 0;
      this.downloadItemStatus = {};
      var _this = this;
      // let isExistsList = await this.isExistsList(downloadItems);
      for (var item of Object.values(downloadItems)) {
        _this.downloadNumber += 1;
        _this.downloadItemStatus[item.label] = "Waiting";
        _this.downloadWaitingNumber += 1;
      }
      var i = 0;
      for (let item of Object.values(downloadItems)) {
        let isExists = await _this.isExists(item);
        if (isExists == true) {
          _this.downloadItemStatus[item.label] = "Success";
          _this.downloadSuccessNumber += 1;
          _this.downloadWaitingNumber -= 1;
          if (
            _this.downloadFailNumber + _this.downloadSuccessNumber ==
            _this.downloadNumber
          ) {
            _this.isDownloading = false;
            _this.downloadComplete = true;
            _this.downloadSuccessOpen = true;
            _this.$emit("downloadComplete");
          }
          continue;
        }
        i += 1;
        // console.log(Date.now());
        // _this._downloadCore(item);
        setTimeout(function () {
          _this._downloadCore(item);
        }, 3000 * i);
      }
    },
    async _downloadCore(item) {
      var _this = this;
      let parsed = parse(item);
      let url = await getScihubPdf(
        parsed.pmid,
        parsed.doi,
        this.options.selectedURL
      );
      if (item.data.fulltextUrl != undefined && url.pdfURL == "error") {
        url.pdfURL = item.data.fulltextUrl;
      }
      if (url.pdfURL != "error") {
        this.downloadItemStatus[item.label] = "Downloading";
        this.downloadDownloadingNumber += 1;
        this.downloadWaitingNumber -= 1;
        let isSuccess = await downloadArticle(url.pdfURL, item);
        console.log(isSuccess);
        if (isSuccess == true) {
          _this.downloadItemStatus[item.label] = "Success";
          _this.downloadSuccessNumber += 1;
          _this.downloadDownloadingNumber -= 1;
        } else {
          _this.downloadItemStatus[item.label] = "Fail";
          _this.downloadFailNumber += 1;
          _this.downloadDownloadingNumber -= 1;
        }
      } else {
        _this.downloadItemStatus[item.label] = "Fail";
        _this.downloadFailNumber += 1;
        _this.downloadWaitingNumber -= 1;
      }
      if (
        _this.downloadFailNumber + _this.downloadSuccessNumber ==
        _this.downloadNumber
      ) {
        this.isDownloading = false;
        this.downloadComplete = true;
        this.downloadSuccessOpen = true;
        this.$emit("downloadComplete");
      }
    },
    async isExistsList(downloadItems) {
      var isExistsList = {};
      var _this = this;
      for (var item of Object.values(downloadItems)) {
        isExistsList[item.label] = await _this.isExists(item);
      }
      // console.log("isExistsList", isExistsList);
      return isExistsList;
    },
    async isExists(item) {
      if (item.data.downloadId == undefined) {
        return false;
      }
      return new Promise((resolve) => {
        chrome.downloads.search(
          { id: item.data.downloadId },
          function (result) {
            // console.log(result);
            resolve(result[0].exists);
          }
        );
      });
    },
  },
};
</script>

