<template>
  <div>
    <v-hover v-slot:default="{ hover }">
      <v-footer height="auto" class="teal lighten-1" padless app bordered fluid>
        <v-row class="d-flex">
          <v-col
            class="d-flex flex-row teal lighten-1 py-1 my-0 justify-center"
            cols="12"
          >
            <!-- <v-icon @click="lock=!lock" dark v-text="lock?'mdi-chevron-down':'mdi-chevron-up'"></v-icon> -->
            <v-icon
              @click="lock = !lock"
              dark
              v-text="lock ? 'mdi-lock' : 'mdi-lock-open'"
              :tag="lock ? 'Lock footbar' : 'Unlock footbar'"
            ></v-icon>
          </v-col>
          <v-col
            cols="4"
            class="d-flex flex-column justify-space-around"
            v-if="expand || lock || hover"
          >
            <v-row class="d-flex flex-row justify-space-around">
              <v-col cols="6" class="pa-1">
                <v-textarea
                  label="过滤器名称"
                  v-model="name"
                  prepend-inner-icon="mdi-filter-outline"
                  append-icon="mdi-pencil"
                  counter
                  maxlength="50"
                  rows="1"
                  dark
                  no-resize
                  required
                  class="mx-3"
                  @click:append.stop="dialogEdit = true"
                  spellcheck="false"
                  @change="set('name', name)"
                ></v-textarea>
                <!-- :placeholder="name" -->
                <!-- :rules="name.length==0?'pl':true" -->
              </v-col>
              <v-col cols="6" class="pa-1">
                <v-textarea
                  label="作者"
                  prepend-inner-icon="mdi-account-outline"
                  append-icon="mdi-pencil"
                  v-model="author"
                  counter="50"
                  maxlength="50"
                  rows="1"
                  no-resize
                  dark
                  class="mx-3"
                  @click:append.stop="dialogEdit = true"
                  spellcheck="false"
                  @change="set('author', author)"
                ></v-textarea>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12" class="px-1 py-0">
                <v-textarea
                  label="过滤器描述信息"
                  prepend-inner-icon="mdi-text-box-outline"
                  v-model="description"
                  append-icon="mdi-pencil"
                  counter="200"
                  maxlength="200"
                  no-resize
                  rows="1"
                  dark
                  class="mx-3"
                  @click:append.stop="dialogEdit = true"
                  spellcheck="false"
                  @change="set('description', description)"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-col>
          <v-col
            cols="2"
            class="d-flex flex-column justify-space-around align-start"
            v-if="expand || lock || hover"
          >
            <!-- <v-progress-circular
            :indeterminate="indeterminate"
            size="90"
            :value="filteredLength>400?100:filteredLength/4"
            width="15"
            :color="filteredLength>400?'red darken-4':'white'"
            >{{filteredLength}}</v-progress-circular>-->
            <p class="ma-0 text-center white--text font-weight-black">
              过滤器信息
            </p>
            <p class="ma-0 text-left white--text font-weight-medium">
              {{ `过滤器筛选杂志数: ${filteredLength}` }}
            </p>
            <p class="ma-0 pl-2 text-left white--text font-weight-medium">
              {{ `根据杂志重要性筛选杂志数: ${filteredImportanceLength}` }}
            </p>
            <p class="ma-0 pl-2 text-left white--text font-weight-medium">
              {{ `根据杂志分类筛选杂志数: ${filteredAreaCategoryLength}` }}
            </p>

            <p class="ma-0 text-left white--text font-weight-medium">
              {{ `自定义增添杂志数: ${addLength}` }}
            </p>
            <p class="ma-0 text-left white--text font-weight-medium">
              {{ `自定义删除杂志数: ${delLength}` }}
            </p>
            <p class="ma-0 text-left white--text font-weight-medium">
              {{ `自定义检索式长度: ${customizeLength}` }}
            </p>
            <!-- <p class="text-button text-center ma-0 white--text">Filtered Journals</p> -->
          </v-col>

          <v-col
            cols="2"
            class="d-flex flex-column justify-space-around align-center"
            v-if="expand || lock || hover"
          >
            <v-progress-circular
              :indeterminate="indeterminate"
              size="90"
              :value="selectedLength > 400 ? 100 : selectedLength / 4"
              width="15"
              :color="selectedLength > 400 ? 'red darken-4' : 'white'"
              >{{ selectedLength }}</v-progress-circular
            >
            <p class="text-button text-center ma-0 white--text">选中杂志数</p>
          </v-col>
          <v-col
            cols="2"
            class="d-flex flex-column justify-space-around align-center"
            v-if="expand || lock || hover"
          >
            <v-progress-circular
              :indeterminate="indeterminate"
              size="90"
              :value="filteredSendLength > 4000 ? 100 : filteredSendLength / 40"
              width="15"
              :color="filteredSendLength > 4000 ? 'red darken-4' : 'white'"
              >{{ filteredSendLength }}</v-progress-circular
            >
            <p class="text-button text-center ma-0 white--text">检索式长度</p>
          </v-col>
          <v-col
            cols="2"
            class="d-flex flex-row flex-wrap align-content-space-between px-0 mr-0"
            v-if="expand || lock || hover"
          >
            <v-btn
              color="primary lighten-2"
              class="col-12 px-0"
              @click.stop="dialogSendToPubmed = true"
            >
              发送过滤器至PubMed
              <v-icon right>mdi-send</v-icon>
            </v-btn>

            <!-- <div class="d-flex flex-row justify-start"> -->
            <div class="col-6 d-flex justify-center pa-0">
              <v-btn
                color="primary lighten-2"
                class="px-3 mr-1"
                @click="saveFilterCode()"
              >
                保存
                <v-icon right>mdi-content-save</v-icon>
              </v-btn>
            </div>
            <div class="col-6 d-flex justify-start pa-0">
              <v-btn
                color="primary lighten-2"
                class="px-3"
                @click="dialogShare = true"
              >
                分享
                <v-icon right>mdi-share</v-icon>
              </v-btn>
            </div>
            <!-- </div> -->
            <div class="col-6 d-flex justify-center pa-0">
              <v-btn
                color="primary lighten-2"
                class="px-3 mr-1"
                @click="dialogLoad = true"
              >
                载入
                <v-icon right>mdi-text-box-plus-outline</v-icon>
              </v-btn>
            </div>
            <div class="col-6 d-flex justify-start pa-0">
              <v-btn
                color="primary lighten-2"
                class="px-3"
                @click.stop="reset()"
              >
                重置
                <v-icon right>mdi-refresh</v-icon>
              </v-btn>
            </div>
          </v-col>

          <v-col cols="12" class="py-0 my-0" v-if="expand">
            <v-divider class="ma-0 pa-0"></v-divider>
          </v-col>
          <v-col
            class="teal lighten-1 py-0 my-0 text-center grey--text text--lighten-1 font-weight-thin"
            cols="12"
            v-if="expand"
            >{{ `EasyPubMed版本：${version}` }}</v-col
          >
        </v-row>
      </v-footer>
    </v-hover>
    <v-dialog id="dialogEdit" v-model="dialogEdit" max-width="500" persistent>
      <v-card>
        <v-card-title class="primary white--text">
          <span>编辑过滤器信息</span>
        </v-card-title>
        <v-card-text class="pb-0">
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-textarea
                  label="过滤器名称"
                  v-model="name"
                  prepend-inner-icon="mdi-filter-outline"
                  clearable
                  counter
                  maxlength="50"
                  auto-grow
                  rows="1"
                  class="pa-0"
                  spellcheck="false"
                ></v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  v-model="author"
                  label="作者"
                  prepend-inner-icon="mdi-account-outline"
                  clearable
                  counter
                  maxlength="50"
                  auto-grow
                  rows="1"
                  class="pa-0"
                  spellcheck="false"
                ></v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  v-model="description"
                  label="过滤器描述信息"
                  prepend-inner-icon="mdi-text-box-outline"
                  clearable
                  counter
                  maxlength="200"
                  auto-grow
                  rows="1"
                  class="pa-0"
                  spellcheck="false"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-6" color="primary" @click="dialogEdit = false">
            关闭
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      id="dialogSendToPubmed"
      v-model="dialogSendToPubmed"
      max-width="600"
      scrollable
      persistent
    >
      <v-card tile>
        <v-card-title class="primary white--text">
          <span>发送检索式至PubMed</span>
        </v-card-title>
        <v-card-text class="pa-0">
          <v-stepper v-model="e6" vertical class="elevation-0">
            <v-stepper-step
              :complete="e6 > 1"
              step="1"
              style="cursor: pointer"
              @click="e6 = 1"
            >
              登录PubMed账号
              <!-- <small>Summarize if needed</small> -->
            </v-stepper-step>

            <v-stepper-content step="1">
              <v-card color="grey lighten-1" class="mb-3">
                <v-img
                  alt="EP Logo"
                  class="shrink mr-2"
                  contain
                  src="../assets/pubmed_login.png"
                  transition="scale-transition"
                  width="1000"
                />
              </v-card>
              <p class="primary--text text-body-2">
                请登录
                <a href="https://pubmed.ncbi.nlm.nih.gov/" target="_blank"
                  >PubMed</a
                >账号。如果您尚未获得PubMed账号，请先注册。
              </p>
              <v-btn color="primary" @click="e6 = 2"> 我已登录PubMed </v-btn>
            </v-stepper-content>

            <v-stepper-step
              :complete="e6 > 2"
              step="2"
              style="cursor: pointer"
              @click="e6 = 2"
            >
              过滤器命名
            </v-stepper-step>

            <v-stepper-content step="2" class="pt-3">
              <p class="primary--text text-body-2">
                <v-icon class="warning--text">mdi-alert</v-icon
                >请确认此过滤器名称<b>未</b>出现在您现有<a
                  href="https://www.ncbi.nlm.nih.gov/myncbi/filters/"
                  target="_blank"
                  >PubMed过滤器列表</a
                >中。如名称重复，过滤器将被PubMed重新命名。
              </p>
              <v-textarea
                label="过滤器名称"
                v-model="queryName"
                placeholder="建议使用英文名称以免识别错误"
                clearable
                counter
                maxlength="50"
                auto-grow
                rows="1"
                spellcheck="false"
              ></v-textarea>
              <p
                class="d-inline-block primary--text pa-1 text-body-2 hover"
                style="border-style: solid; border-width: 1px"
                @click="queryName = name"
              >
                <v-icon small class="mr-2 primary--text"
                  >mdi-filter-outline</v-icon
                >使用已设定过滤器名称
              </p>

              <v-btn
                class="d-block"
                color="primary"
                :disabled="queryName == undefined || queryName == ''"
                @click="e6 = 3"
              >
                继续
              </v-btn>
            </v-stepper-content>

            <v-stepper-step
              :complete="e6 > 3"
              step="3"
              style="cursor: pointer"
              @click="e6 = 3"
            >
              提交过滤器至PubMed
            </v-stepper-step>

            <v-stepper-content step="3">
              <p
                :class="
                  queryName == undefined || queryName == ''
                    ? 'primary--text text-body-2 error--text'
                    : 'primary--text text-body-2'
                "
              >
                {{
                  `过滤器名称: ${
                    queryName == undefined || queryName == ""
                      ? "您必须提供过滤器名称"
                      : queryName
                  }`
                }}
              </p>
              <v-textarea
                class="mx-0"
                label="PubMed检索式"
                v-model="filteredSend"
                outlined
                filled
                readonly
                no-resize
                counter="4000"
                rows="4"
              ></v-textarea>
              <p
                class="error--text text-body-2"
                v-show="filteredSend.length > 4000"
              >
                根据
                <a
                  href="https://www.ncbi.nlm.nih.gov/books/NBK53591/"
                  target="_blank"
                  class="warning--text"
                  >PubMed规定</a
                >，检索式长度不能超过4000个字符。
              </p>
              <v-btn
                class="d-block"
                :color="filteredSend.length < 4000 ? 'primary' : 'error'"
                @click="
                  sendToPubmed();
                  e6 = 4;
                "
                v-text="filteredSend.length < 4000 ? '提交' : '坚持提交'"
                :disabled="queryName == undefined || queryName == ''"
              >
              </v-btn>
            </v-stepper-content>

            <v-stepper-step step="4" style="cursor: pointer" @click="e6 = 4">
              确认并激活过滤器
            </v-stepper-step>

            <v-stepper-content step="4">
              <v-card color="grey lighten-1" class="mb-3">
                <v-img
                  class="shrink mr-2"
                  contain
                  src="../assets/pubmed_activate.png"
                  transition="scale-transition"
                  width="800"
                />
              </v-card>
              <p class="primary--text text-body-2">
                请确认此过滤器已成功发送到<a
                  href="https://www.ncbi.nlm.nih.gov/myncbi/filters/"
                  target="_blank"
                  >PubMed过滤器列表</a
                >中并<b>激活</b>此过滤器。正确激活后您可以在PubMed搜索界面左上角看到此过滤器。
              </p>
              <v-img
                class="shrink mr-2"
                contain
                src="../assets/filterManagerSendPubmedSuccess2_cn.png"
                transition="scale-transition"
                width="800"
              />
            </v-stepper-content>
          </v-stepper>
        </v-card-text>
        <v-divider />
        <v-card-actions>
          <v-spacer />
          <v-btn
            class="mr-2"
            color="primary"
            @click="
              dialogSendToPubmed = false;
              e6 = 1;
              queryName = '';
            "
          >
            关闭
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
      <v-snackbar v-model="snackbar" :color="snackbarType" top>
        {{ snackbarText }}
        <template v-slot:action="{ attrs }">
          <v-icon
            class="mr-3"
            small
            color="white"
            v-bind="attrs"
            @click="snackbar = false"
            >mdi-close</v-icon
          >
        </template>
      </v-snackbar>
    </v-dialog>
    <v-dialog id="dialogLoad" v-model="dialogLoad" max-width="800" persistent>
      <v-card>
        <v-card-title class="primary white--text">
          <span>载入过滤器</span>
        </v-card-title>
        <v-card-text class="pb-0">
          <v-autocomplete
            v-model="filterSelected"
            :items="filterCodesItems"
            color="blue-grey lighten-2"
            label="请选择过滤器"
            spellcheck="false"
          >
            <template v-slot:item="{ item }">
              <v-list-item-content style="max-width: 650px">
                <v-list-item-title v-html="`${item.value}`"></v-list-item-title>
                <v-list-item-subtitle
                  v-html="
                    `作者: ${item.author}; 选中杂志数: ${item.no_journal}`
                  "
                ></v-list-item-subtitle>
                <v-list-item-subtitle
                  v-html="`描述信息:${item.description}`"
                ></v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-icon class="ml-4">
                <v-icon
                  class="error--text hover"
                  @click.stop="removeFilterCode(item)"
                >
                  mdi-close-circle</v-icon
                >
              </v-list-item-icon>
            </template>
          </v-autocomplete>

          <v-textarea
            label="过滤器编码"
            v-model="filterLoad"
            placeholder="输入过滤器编码。您可以在论坛中找到更多的过滤器编码"
            filled
            outlined
            rows="4"
            no-resize
            spellcheck="false"
          ></v-textarea>
          <v-divider />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-2" color="success" @click.stop="load()">
            载入
            <v-icon right>mdi-text-box-plus-outline</v-icon>
          </v-btn>
          <v-btn
            class="mr-2"
            color="primary"
            @click.stop="
              link('https://github.com/naivenaive/EasyPubMed/issues/3')
            "
          >
            获取更多过滤器编码
            <v-icon right>mdi-magnify</v-icon>
          </v-btn>
          <v-btn
            class="mr-2"
            color="primary"
            @click.stop="
              filterSelected = '';
              filterLoad = '';
            "
          >
            重置
            <v-icon right>mdi-refresh</v-icon>
          </v-btn>

          <v-btn
            class="mr-4"
            color="primary"
            @click="
              dialogLoad = false;
              filterSelected = '';
              filterLoad = '';
            "
          >
            关闭
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog id="dialogShare" v-model="dialogShare" max-width="600" persistent>
      <v-card>
        <v-card-title class="primary white--text">
          <span>分享过滤器编码</span>
        </v-card-title>
        <v-card-text class="pt-3 pb-0">
          <v-textarea
            id="shareText"
            label="过滤器编码"
            v-model="filteredJson"
            filled
            readonly
            hide-details
            outlined
            rows="4"
            no-resize
            spellcheck="false"
            class="pb-3"
          ></v-textarea>
          <v-divider />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-2"
            color="success"
            @click.stop="
              copyFilterCode();
              link('https://github.com/naivenaive/EasyPubMed/issues/3');
            "
          >
            分享
            <v-icon right>mdi-share</v-icon>
          </v-btn>
          <v-btn class="mr-2" color="primary" @click.stop="copyFilterCode()">
            复制过滤器编码
            <v-icon right>mdi-content-copy</v-icon>
          </v-btn>
          <v-btn class="mr-2" color="primary" @click="dialogShare = false">
            关闭
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      id="loading"
      v-model="isLoading"
      hide-overlay
      persistent
      width="500"
    >
      <v-card color="primary" dark>
        <v-card-text>
          Loading...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="isCopySuccess" timeout="3000" top color="success">
      已成功复制过滤器编码至剪贴板
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="isCopySuccess = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
    <v-dialog
      id="dialogSaveError"
      v-model="dialogSaveError"
      max-width="600"
      persistent
    >
      <v-card>
        <v-card-title class="error white--text">
          <span>保存过滤器出现问题</span>
        </v-card-title>
        <v-card-text class="pt-3 pb-0">
          <p class="text-body-1 font-weight-medium">请确认已为过滤器命名</p>
          <p class="text-body-1 font-weight-medium">
            请确认已保存过滤器数量未超过50
          </p>
          <v-divider />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-2" color="primary" @click="dialogSaveError = false">
            关闭
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      id="dialogSaveAssert"
      v-model="dialogSaveAssert"
      max-width="600"
      persistent
    >
      <v-card>
        <v-card-title class="warning white--text">
          <span>请确认</span>
        </v-card-title>
        <v-card-text class="pt-3 pb-0">
          <p class="text-body-1 font-weight-medium">
            {{ `${name} 已存在。您希望覆盖原过滤器吗？` }}
          </p>
          <v-divider />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-2"
            color="primary"
            @click="
              saveFilterCode((overwrite = true));
              dialogSaveAssert = false;
            "
          >
            覆盖
            <!-- <v-icon right>mdi-close-circle</v-icon> -->
          </v-btn>
          <v-btn class="mr-2" color="primary" @click="dialogSaveAssert = false">
            关闭
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      id="dialogSaveSuccess"
      v-model="dialogSaveSuccess"
      max-width="600"
      persistent
    >
      <v-card>
        <v-card-title class="success white--text">
          <span>已成功保存过滤器</span>
        </v-card-title>
        <v-card-text class="pt-3 pb-0">
          <p class="text-body-1 font-weight-medium">
            您希望将此过滤器与其他研究人员分享吗?
          </p>
          <v-divider />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-2"
            color="primary"
            @click.stop="
              dialogSaveSuccess = false;
              dialogShare = true;
            "
          >
            我希望分享
            <v-icon right>mdi-share</v-icon>
          </v-btn>
          <v-btn
            class="mr-2"
            color="primary"
            @click="dialogSaveSuccess = false"
          >
            关闭
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- <v-snackbar v-model="saveSuccess" timeout="3000" top color="success">
      Successfully save filter Code. Do you want to share your filter with other
      researchers?
      <template v-slot:action="{ attrs }">
        <v-btn
          color="primary"
          class="font-weight-black white--text"
          v-bind="attrs"
          @click="isCopySuccess = false"
        >
          I want to share my code
        </v-btn>
        <v-btn color="blue" text v-bind="attrs" @click="isCopySuccess = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar> -->
  </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";

export default {
  name: "Footer",
  data() {
    return {
      e6: 1,
      queryName: "",
      expand: true,
      lock: true,
      dialogEdit: false,
      dialogSendToPubmed: false,
      dialogLoad: false,
      filterCodes: {},
      filterCodesItems: {},
      filterSelected: "",
      filterLoad: "",
      dialogShare: false,
      snackbar: false,
      snackbarType: "",
      snackbarText: "Success",
      isLoading: false,
      isCopySuccess: false,
      dialogSaveError: false,
      dialogSaveAssert: false,
      dialogSaveSuccess: false,
    };
  },
  computed: {
    ...mapState({
      filter: (state) => state.filter,
      indeterminate: (state) => state.isLoading,
      selectedLength: (state) => state.result.filtered.length,
      filteredImportanceLength: (state) =>
        state.result.filteredImportance.length,
      filteredAreaCategoryLength: (state) =>
        state.result.filteredAreaCategory.length,
      filteredLength: (state) => state.result.filteredImportanceArea.length,
      addLength: (state) => state.filter.add.length,
      delLength: (state) => state.filter.del.length,
      customizeLength: (state) => state.filter.customize.length,
    }),

    name: {
      get() {
        return this.filter.name;
      },
      set(val) {
        this.$store.commit("setFilter", {
          param: "name",
          value: val,
        });
      },
    },
    author: {
      get() {
        return this.filter.author;
      },
      set(val) {
        this.$store.commit("setFilter", {
          param: "author",
          value: val,
        });
      },
    },
    description: {
      get() {
        return this.filter.description;
      },
      set(val) {
        this.$store.commit("setFilter", {
          param: "description",
          value: val,
        });
      },
    },
    filteredSend() {
      return this.$store.getters.filteredSend;
    },
    filteredSendLength() {
      return this.$store.getters.filteredSend.length;
    },
    filteredJson() {
      return this.$store.getters.filterMini;
    },
    version() {
      var manifestData = chrome.runtime.getManifest();
      return manifestData.version;
    },
  },
  methods: {
    saveFilterCode(overwrite = false) {
      if (this.name == "" || Object.keys(this.filterCodes).length > 50) {
        this.dialogSaveError = true;
        return;
      }
      if (
        Object.keys(this.filterCodes).includes(this.name) &&
        overwrite == false
      ) {
        this.dialogSaveAssert = true;
        return;
      }
      Vue.set(this.filterCodes, this.name, this.filteredJson);
      this.dialogSaveSuccess = true;
    },
    removeFilterCode(item) {
      // console.log(item);
      Vue.delete(this.filterCodes, item.value);
      // this.filterCodes = Object.keys(this.filterCodes).filter(i=> i!=item.value)
      // delete this.filterCodes[item.value];
      // console.log(this.filterCodes);
    },
    link(url) {
      // console.log("url", url);
      window.open(url);
    },
    copyFilterCode() {
      var aux = document.querySelector("#shareText");
      aux.select();
      document.execCommand("copy");
      this.isCopySuccess = true;
    },
    load() {
      // var _this = this;
      // this.isLoading = true;
      this.$store.dispatch("load", this.filterLoad);
      // this.isLoading = false;
      // setTimeout(function () {
      //   _this.$store.dispatch("load", _this.filterLoad);
      //   _this.isLoading = false;
      // }, 1000);
    },
    reset() {
      this.$store.commit("toggleLoad", true);

      setTimeout(() => {
        this.$store.dispatch("init");
        // this.$store.commit("toggleLoad", false);
        // this.name = this.$store.state.filter.name;
        // this.author = this.$store.state.filter.author;
        // this.description = this.$store.state.filter.description;
      }, 500);
    },
    sendToPubmed() {
      var _this = this;
      let params = {
        p$site: "myncbi",
        p$rq:
          "PAFAppLayout.AppController.Page.myncbiApp_Filters.MyNCBI_Filters_Portlet:SaveNewFilter_XHR",
        Db: "pubmed",
        custom_filter_name: this.queryName,
        custom_filter_query: this.$store.getters.filteredSend,
        old_name: "",
        selFilters: "",
        selCustom: "",
      };
      // this.snackbarText = "Sending request. Please wait";
      // this.snackbarType = "warning";
      // this.snackbar = true;
      chrome.runtime.sendMessage(
        { command: "query_to_pubmed", params: params },
        function (response) {
          // _this.snackbarText =
          //   response == "success"
          //     ? "Succeed submitting query"
          //     : "Fail in submitting query";
          // _this.snackbarType = response == "success" ? "success" : "error";
          // _this.snackbar = true;
          // _this.e6 = 4;
          console.log("success", response);
        }
      );
    },
    set(param, val) {
      this.$store.commit("setFilter", {
        param: param,
        value: val,
      });
    },
    scroll() {
      window.onscroll = () => {
        let bottomOfWindow =
          Math.max(
            window.pageYOffset,
            document.documentElement.scrollTop,
            document.body.scrollTop
          ) +
            window.innerHeight >
          document.documentElement.offsetHeight - 150;
        // console.log(
        //   bottomOfWindow,
        //   window.pageYOffset,
        //   document.documentElement.scrollTop,
        //   document.body.scrollTop,
        //   window.innerHeight,
        //   document.documentElement.offsetHeight
        // );
        this.expand = bottomOfWindow ? true : false;
      };
    },
  },
  watch: {
    filterSelected(n) {
      this.filterLoad = this.filterCodes[n];
    },
    filterCodes: {
      deep: true,
      handler: function (n) {
        const default_filter_codes = {
          "B1/Med&Bio": `{"name":"B1/Med&Bio","author":"Default","description":"B1 journals in Medicine and Biology","IM":{"CAS":["B1"]},"AC":{"MEDICINE":"all","BIOLOGY":"all"},"no_journal":295}`,
          "IF>6/Med": `{"name":"IF>6/Med","author":"Default","description":" Journals with impact factor over 6 in Medicine","IM":{"IMF":[6,300]},"AC":{"MEDICINE":"all"},"no_journal":300}`,
          "Top/Med": `{"name":"Top/Med","author":"Default","description":"Top Journals in Medicine","IM":{"TOP":["Top Journal"]},"AC":{"MEDICINE":"all"},"no_journal":376}`,
        };
        // console.log("n", n, Object.keys(n), Object.keys(n).length);
        if (Object.keys(n).length == 0) {
          this.filterCodes = default_filter_codes;
          n = default_filter_codes;
        }
        chrome.storage.sync.set({ filter_codes: n }, () => {});
        let ret = [];
        for (var item of Object.values(n)) {
          let i = {};
          let parsedItem = JSON.parse(item);
          // console.log(parsedItem);
          i.value = parsedItem.name;
          i.author = parsedItem.author == undefined ? "NA" : parsedItem.author;
          i.description =
            parsedItem.description == undefined ? "NA" : parsedItem.description;
          i.no_journal =
            parsedItem.no_journal == undefined ? "NA" : parsedItem.no_journal;
          i.text = `${i.value} [Author: ${i.author}; Journal No: ${i.no_journal}]`;
          ret.push(i);
        }
        this.filterCodesItems = ret;
        // console.log("filtercodesitems", this.filterCodesItems);
      },
    },
  },
  mounted() {
    this.scroll();
    var _this = this;
    chrome.storage.sync.get(["filter_codes"], (result) => {
      // console.log("result", result);
      _this.filterCodes = result["filter_codes"];
    });
  },
};
</script>

<style>
.hover:hover {
  background-color: #eeeeee;
  cursor: pointer;
}
</style>