<template>
  <v-dialog
    id="newArticleAdd"
    v-model="newArticleAddOpen"
    v-if="newArticleAddOpen"
    max-width="800"
    persistent
  >
    <v-card>
      <v-card-title class="primary white--text">
        <span>Add New Article</span>
      </v-card-title>
      <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
        <v-container>
          <v-row>
            <!-- <v-col cols="12">{{ addItem }}</v-col> -->
            <v-col cols="12" v-if="isFetchingCsl == true">
              <v-progress-linear
                :active="isFetchingCsl == true"
                indeterminate
                class=""
                height="6"
                rounded
              ></v-progress-linear>
            </v-col>
            <div v-if="isFetchingCsl == false" class="pa-0 col-12 col">
              <div class="d-flex flex-wrap">
                <v-col
                  cols="12"
                  class="pt-3 pb-1 px-1"
                  style="
                    display: inline-block;
                    max-height: 40px;
                    overflow-y: auto;
                  "
                >
                  <span
                    class="primary white--text mr-2 px-2"
                    style="
                      display: inline-block;
                      width: 70px;
                      text-align: center;
                    "
                    >Title</span
                  >
                  <span>{{ title }}</span>
                </v-col>
                <v-col cols="12" class="pa-1">
                  <span
                    class="primary white--text mr-2 px-2"
                    style="
                      display: inline-block;
                      width: 70px;
                      text-align: center;
                    "
                    >Journal</span
                  >
                  <span>{{ journal }}</span>
                </v-col>
                <v-col cols="12" class="pa-1">
                  <span
                    class="primary white--text mr-2 px-2"
                    style="
                      display: inline-block;
                      width: 70px;
                      text-align: center;
                    "
                    >Year</span
                  >
                  <span>{{ year }}</span>
                </v-col>
                <v-col
                  cols="12"
                  class="pa-1"
                  style="
                    display: inline-block;
                    max-height: 40px;
                    overflow-y: auto;
                  "
                >
                  <span
                    class="primary white--text mr-2 px-2"
                    style="
                      display: inline-block;
                      width: 70px;
                      text-align: center;
                    "
                    >Author</span
                  >
                  <span>{{ author }}</span>
                </v-col>
                <v-col cols="12" class="pa-1">
                  <span
                    class="primary white--text mr-2 px-2"
                    style="
                      display: inline-block;
                      width: 70px;
                      text-align: center;
                    "
                    >PMID</span
                  >
                  <span>{{ pmid }}</span>
                </v-col>
                <v-col cols="12" class="pa-1">
                  <span
                    class="primary white--text mr-2 px-2"
                    style="
                      display: inline-block;
                      width: 70px;
                      text-align: center;
                    "
                    >DOI</span
                  >
                  <span>{{ doi }}</span>
                </v-col>
                <v-col cols="6" class="pa-1 align-self-center">
                  <span
                    class="primary white--text mr-2 px-2"
                    style="
                      display: inline-block;
                      width: 70px;
                      text-align: center;
                    "
                    >Rating</span
                  >
                  <v-rating
                    class="d-inline"
                    color="orange"
                    hover
                    length="5"
                    size="25"
                    v-model="star"
                  ></v-rating>
                  <v-icon
                    @click="renewStar"
                    style="cursor: pointer"
                    class="ml-2"
                    >mdi-autorenew</v-icon
                  >
                </v-col>
                <v-col cols="1" class="py-0 align-self-center">
                  <v-checkbox
                    v-model="blueFlag"
                    color="blue"
                    hide-details
                    class="mt-0"
                  >
                    <template v-slot:prepend>
                      <v-icon color="blue">mdi-flag</v-icon>
                    </template>
                  </v-checkbox> </v-col
                ><v-col cols="1" class="py-1 align-self-center">
                  <v-checkbox
                    v-model="redFlag"
                    color="red"
                    hide-details
                    class="mt-0"
                  >
                    <template v-slot:prepend>
                      <v-icon color="red">mdi-flag</v-icon>
                    </template>
                  </v-checkbox>
                </v-col>
                <v-col
                  cols="6"
                  class="pa-1 align-self-center"
                  style="max-height: 80px; overflow-y: auto"
                >
                  <div class="d-block align-self-center">
                    <span
                      class="primary white--text mr-2 px-2 d-inline-block"
                      style="
                        display: inline-block;
                        width: 70px;
                        text-align: center;
                      "
                      >Tag</span
                    >
                    <span
                      v-for="(tag, index) in tags()"
                      :key="index"
                      class="rounded-pill blue white--text px-2 mb-1 mx-1 pa-1 d-inline-block"
                      @click="del(tag)"
                      style="cursor: pointer"
                    >
                      {{ tag }}
                    </span>
                  </div>
                </v-col>
                <v-col cols="6" class="py-0"
                  ><v-autocomplete
                    class="mr-2"
                    :items="tagsInSelect"
                    :search-input.sync="tagInput"
                    v-model="selectedTags"
                    label="Add Tag"
                    multiple
                    @input="input"
                    append-outer-icon="mdi-plus"
                    hide-no-data
                    @click:append-outer="addTag"
                    @keyup.enter="addTag"
                  >
                    <template v-slot:item="data">
                      <v-list-item-content>
                        <v-list-item-title
                          v-html="data.item"
                        ></v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </v-autocomplete>
                </v-col>

                <v-col cols="12" class="px-1 py-3">
                  <v-textarea
                    class="d-inline pa-0"
                    v-model="note"
                    label="Note"
                    counter
                    auto-grow
                    rows="1"
                    spellcheck="false"
                  ></v-textarea>
                </v-col>
              </div>
            </div>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-3" color="success" @click="addNewArticle">
          Add
          <v-icon right>mdi-plus-circle</v-icon>
        </v-btn>
        <v-btn
          class="mr-6"
          color="primary"
          @click="
            $emit('close');
            renew();
          "
        >
          Close
          <v-icon right>mdi-close-circle</v-icon>
        </v-btn>
        <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { addNewArticle } from "./setProjectData";
import {
  fetchPmid,
  fetchPmidCsl,
  fetchDoiCsl,
  fetchZoteroCsl,
} from "./fetchData";
import { parseCsl } from "./parse";
var $ = require("jquery");
export default {
  name: "newArticleAdd",
  props: {
    newArticleAddOpen: Boolean,
    addItem: Object,
    project: String,
    projectDataAll: Object,
  },
  data() {
    return {
      isFetchingCsl: false,
      finalCsl: {},
      star: 0,
      blueFlag: false,
      redFlag: false,
      note: "",
      tagsEdit: [],
      tagInput: "",
      selectedTags: [],
    };
  },
  computed: {
    // parsed() {
    //   if (this.finalCsl.lenhth == 0) return;
    //   return this.parse({ csl: this.finalCsl });
    // },
    title() {
      return this.parsed().title;
    },
    journal() {
      return this.parsed().containerTitleShort;
    },
    year() {
      return this.parsed().year;
    },
    author() {
      return this.parsed().author;
    },
    pmid() {
      return this.parsed().pmid;
    },
    doi() {
      return this.parsed().doi;
    },
    tagsInSelect() {
      var _this = this;
      let allTags = this.projectDataAll[this.project].summary.tags;
      return allTags.filter((x) => {
        return _this.tagsEdit.indexOf(x) == -1;
      });
    },
  },
  watch: {
    addItem: function () {
      this.fetchFinalCsl();
    },
  },
  methods: {
    addNewArticle() {
      let project = this.project;
      let csl = this.finalCsl;
      let data = {
        project: this.project,
        blueFlag: this.blueFlag,
        redFlag: this.redFlag,
        tag: this.tagsEdit,
        rating: this.star,
        note: this.note,
        updateTime: Date.now(),
      };
      let item = { csl: csl, data: data };
      console.log(project, item);
      addNewArticle(project, item);
      this.$emit("close");
      this.renew();
    },
    renewStar() {
      this.star = 0;
    },
    tags() {
      if (this.tagsEdit.length == 0) {
        return ["No Tag"];
      }
      return this.tagsEdit;
    },
    input(tag) {
      console.log(tag);
      this.tagsEdit.unshift(tag[0]);
      this.selectedTags = [];
    },
    addTag() {
      if (this.tagInput == null || this.tagInput.trim() == "") {
        this.tagInput = "";
        return;
      }
      if (this.tagsEdit.indexOf(this.tagInput) != -1) {
        this.tagInput = "";
        return;
      }
      this.tagsEdit.unshift(this.tagInput);
      this.selectedTags = [];
      this.tagInput = "";
    },
    del(tag) {
      if (tag == "No Tag") return;
      this.tagsEdit = this.tagsEdit.filter((x) => {
        return x != tag;
      });
    },
    // parse(item) {
    //   //   console.log("referenceEdit", referenceEdit);
    //   // let item = referenceEdit.item;
    //   let journal = item.csl["container-title-short"];
    //   let authors_string = "";
    //   if (item.csl.author != undefined) {
    //     for (var i = 0; i < item.csl.author.length; i++) {
    //       let author = item.csl.author[i];
    //       let family = author.family == undefined ? "" : author.family;
    //       let given = author.given == undefined ? "" : author.given;
    //       let author_string =
    //         i == item.csl.author.length - 1
    //           ? `${family} ${given}`
    //           : `${family} ${given}; `;
    //       authors_string += author_string;
    //     }
    //   }
    //   //   let imfq = this.get_pmid_imfq(journal, this.ifqbt);
    //   return {
    //     // project: item.data.project,
    //     title: item.csl["title"],
    //     containerTitle: item.csl["container-title"],
    //     containerTitleShort: item.csl["container-title-short"],
    //     author: authors_string,
    //     firstAuthor:
    //       item.csl.author != undefined
    //         ? `${item.csl.author[0].family},${item.csl.author[0].given}`
    //         : "",
    //     firstAuthorGiven:
    //       item.csl.author != undefined ? item.csl.author[0].given : "",
    //     firstAuthorFamily:
    //       item.csl.author != undefined ? item.csl.author[0].family : "",
    //     lastAuthor:
    //       item.csl.author != undefined
    //         ? `${item.csl.author[item.csl.author.length - 1].family},${
    //             item.csl.author[item.csl.author.length - 1].given
    //           }`
    //         : "",
    //     lastAuthorGiven:
    //       item.csl.author != undefined
    //         ? item.csl.author[item.csl.author.length - 1].given
    //         : "",
    //     lastAuthorFamily:
    //       item.csl.author != undefined
    //         ? item.csl.author[item.csl.author.length - 1].family
    //         : "",
    //     pmid: item.csl.PMID == undefined ? "NA" : item.csl.PMID,
    //     pmcid: item.csl.PMCID == undefined ? "NA" : item.csl.PMCID,
    //     doi: item.csl.DOI == undefined ? "NA" : item.csl.DOI,
    //     year:
    //       item.csl.accessed == undefined
    //         ? "NA"
    //         : item.csl.accessed["date-parts"][0][0],
    //   };
    // },
    parse(item) {
      let parsedCsl = parseCsl(item.csl);
      // let parsedData = parseData(item.data);
      let ret = { ...parsedCsl };
      // console.log(ret);
      return ret;
    },
    parsed() {
      if (Object.keys(this.finalCsl).length == 0) return {};
      return this.parse({ csl: this.finalCsl });
    },
    async fetchFinalCsl() {
      this.renew();
      this.finalCsl = {};
      this.isFetchingCsl = true;
      let addItem = this.addItem;
      let type;
      if (addItem.csl == undefined) {
        type = "NA";
      } else {
        type = Object.keys(addItem.csl)[0];
      }
      if (type == "pmid") {
        this.finalCsl = addItem.csl.pmid;
        this.isFetchingCsl = false;
      }
      if (type == "doi") {
        let doi = addItem.DOI;
        let doiCsl = addItem.csl.doi;
        let pmid;
        try {
          pmid = await fetchPmid(doi);
        } catch (e) {
          pmid = "NA";
        }
        console.log("in", pmid);
        if (pmid == "NA") {
          this.finalCsl = doiCsl;
          this.isFetchingCsl = false;
        } else {
          try {
            var pmidCsl = await fetchPmidCsl(pmid);
            this.finalCsl = pmidCsl;
            this.isFetchingCsl = false;
          } catch (e) {
            this.finalCsl = doiCsl;
            this.isFetchingCsl = false;
          }
        }
      }
      if (type == "zotero") {
        let doi = addItem.DOI;
        let zoteroCsl = addItem.csl.zotero;
        try {
          if (doi.startsWith("10.") == false) throw new Error("");
          pmid = await fetchPmid(doi);
        } catch (e) {
          pmid = "NA";
        }
        console.log("in", pmid);

        if (pmid == "NA") {
          try {
            if (doi.startsWith("10.") == false) throw new Error("");
            var doiCsl = await fetchDoiCsl(doi);
            this.finalCsl = doiCsl;
            this.isFetchingCsl = false;
          } catch {
            this.finalCsl = zoteroCsl;
            this.isFetchingCsl = false;
          }
        } else {
          try {
            var pmidCsl = await fetchPmidCsl(pmid);
            this.finalCsl = pmidCsl;
            this.isFetchingCsl = false;
          } catch {
            try {
              if (doi.startsWith("10.") == false) throw new Error("");
              var doiCsl = await fetchDoiCsl(doi);
              this.finalCsl = doiCsl;
              this.isFetchingCsl = false;
            } catch (e) {
              console.log(e);
              this.finalCsl = zoteroCsl;
              this.isFetchingCsl = false;
            }
          }
        }
      }
      if (type == "NA") {
        let doi = addItem.DOI;
        var pmid;
        try {
          if (doi.startsWith("10.") == false) throw new Error("");
          pmid = await fetchPmid(doi);
        } catch (e) {
          pmid = "NA";
        }
        console.log("in", pmid);

        if (pmid == "NA") {
          try {
            if (doi.startsWith("10.") == false) throw new Error("");
            var doiCsl = await fetchDoiCsl(doi);
            this.finalCsl = doiCsl;
            this.isFetchingCsl = false;
          } catch {
            try {
              var zoteroCsl = await fetchZoteroCsl(doi);
              this.finalCsl = zoteroCsl;
              this.isFetchingCsl = false;
            } catch {
              console.log("not FOund");
              this.isFetchingCsl = false;
            }
          }
        } else {
          try {
            var pmidCsl = await fetchPmidCsl(pmid);
            this.finalCsl = pmidCsl;
            this.isFetchingCsl = false;
          } catch {
            try {
              if (doi.startsWith("10.") == false) throw new Error("");
              var doiCsl = await fetchDoiCsl(doi);
              this.finalCsl = doiCsl;
              this.isFetchingCsl = false;
            } catch {
              try {
                var zoteroCsl = await fetchZoteroCsl(doi);
                this.finalCsl = zoteroCsl;
                this.isFetchingCsl = false;
              } catch {
                console.log("not FOund");
                this.isFetchingCsl = false;
              }
            }
          }
        }
      }
    },
    renew() {
      this.star = 0;
      this.blueFlag = false;
      this.redFlag = false;
      this.note = "";
      this.tagsEdit = [];
      this.tagInput = "";
      this.selectedTags = [];
    },
  },
};
</script>

<style>
.v-menu__content.theme--light.v-menu__content--fixed.menuable__content__active.v-autocomplete__content {
  max-height: 200px !important;
}
</style>