<template>
  <v-dialog
    id="dialogEdit"
    v-model="referenceEditOpen"
    v-if="referenceEditOpen"
    max-width="800"
    persistent
  >
    <v-card>
      <v-card-title class="primary white--text">
        <span>Edit Reference</span>
      </v-card-title>
      <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
        <v-container>
          <v-row>
            <v-col
              cols="12"
              class="pt-3 pb-1 px-1"
              style="display: inline-block; max-height: 40px; overflow-y: auto"
            >
              <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >Title</span
              >
              <span>{{ title }}</span>
            </v-col>
            <v-col cols="12" class="pa-1">
              <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >Journal</span
              >
              <span>{{ journal }}</span>
            </v-col>
            <v-col cols="12" class="pa-1">
              <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >Year</span
              >
              <span>{{ year }}</span>
            </v-col>
            <v-col
              cols="12"
              class="pa-1"
              style="display: inline-block; max-height: 40px; overflow-y: auto"
            >
              <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >Author</span
              >
              <span>{{ author }}</span>
            </v-col>
            <v-col cols="12" class="pa-1">
              <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >PMID</span
              >
              <span>{{ pmid }}</span>
            </v-col>
            <v-col cols="12" class="pa-1">
              <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >DOI</span
              >
              <span>{{ doi }}</span>
              <!-- <span>{{ tags }}</span>
              <span>{{ options }}</span> -->
            </v-col>

            <v-col cols="6" class="pa-1 align-self-center">
              <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >Rating</span
              >
              <v-rating
                class="d-inline"
                color="orange"
                hover
                length="5"
                size="25"
                v-model="starEdit"
              ></v-rating>
              <v-icon @click="renewStar" style="cursor: pointer" class="ml-2"
                >mdi-autorenew</v-icon
              >
            </v-col>
            <v-col cols="1" class="py-0 align-self-center">
              <v-checkbox
                v-model="blueFlag"
                color="blue"
                hide-details
                class="mt-0"
              >
                <template v-slot:prepend>
                  <v-icon color="blue">mdi-flag</v-icon>
                </template>
              </v-checkbox> </v-col
            ><v-col cols="1" class="py-1 align-self-center">
              <v-checkbox
                v-model="redFlag"
                color="red"
                hide-details
                class="mt-0"
              >
                <template v-slot:prepend>
                  <v-icon color="red">mdi-flag</v-icon>
                </template>
              </v-checkbox>
            </v-col>
            <v-col
              cols="6"
              class="pa-1 align-self-center"
              style="max-height: 80px; overflow-y: auto"
            >
              <div class="d-block align-self-center">
                <span
                  class="primary white--text mr-2 px-2 d-inline-block"
                  style="display: inline-block; width: 70px; text-align: center"
                  >Tag</span
                >
                <span
                  v-for="(tag, index) in tags()"
                  :key="index"
                  class="
                    rounded-pill
                    blue
                    white--text
                    px-2
                    mb-1
                    mx-1
                    pa-1
                    d-inline-block
                  "
                  @click="del(tag)"
                  style="cursor: pointer"
                >
                  {{ tag }}
                </span>
              </div>
            </v-col>
            <v-col cols="6" class="py-0"
              ><v-autocomplete
                class="mr-2"
                :items="tagsInSelect"
                :search-input.sync="tagInput"
                v-model="selectedTags"
                label="Add Tag"
                multiple
                @input="input"
                append-outer-icon="mdi-plus"
                hide-no-data
                @click:append-outer="addTag"
                @keyup.enter="addTag"
              >
                <template v-slot:item="data">
                  <v-list-item-content>
                    <v-list-item-title v-html="data.item"></v-list-item-title>
                  </v-list-item-content>
                </template>
              </v-autocomplete>
            </v-col>

            <v-col cols="12" class="px-1 py-3">
              <!-- <span
                class="primary white--text mr-2 px-2"
                style="display: inline-block; width: 70px; text-align: center"
                >Rating</span
              > -->
              <v-textarea
                class="d-inline pa-0"
                v-model="note"
                label="Note"
                counter
                auto-grow
                rows="1"
                spellcheck="false"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="mr-3" color="success" @click="update">
          Update
          <v-icon right>mdi-plus-circle</v-icon>
        </v-btn>
        <v-btn class="mr-6" color="primary" @click="$emit('close')">
          Close
          <v-icon right>mdi-close-circle</v-icon>
        </v-btn>
        <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { update, updateItem } from "./setProjectData";
import { parse } from "./parse";
export default {
  name: "referenceEdit",
  props: {
    referenceEditOpen: Boolean,
    referenceEdit: Object,
    allTags: Array,
    options: Object,
  },
  data() {
    // let x = this.referenceEdit.item.data.tag;
    return {
      noteEdit: undefined,
      blueFlagEdit: undefined,
      redFlagEdit: undefined,
      tagsEdit: undefined,
      starEdit: undefined,
      selectedTags: [],
      tagInput: "",
    };
  },
  computed: {
    parsed() {
      return this.parse(this.referenceEdit);
    },
    title() {
      return this.parsed.title;
    },
    journal() {
      return this.parsed.containerTitleShort;
    },
    year() {
      return this.parsed.year;
    },
    author() {
      return this.parsed.author;
    },
    pmid() {
      return this.parsed.pmid;
    },
    doi() {
      return this.parsed.doi;
    },
    note: {
      get: function () {
        return this.referenceEdit.item.data.note;
      },
      set: function (value) {
        this.noteEdit = value;
      },
    },
    redFlag: {
      get: function () {
        return this.referenceEdit.item.data.redFlag;
      },
      set: function (value) {
        this.redFlagEdit = value;
      },
    },
    blueFlag: {
      get: function () {
        return this.referenceEdit.item.data.blueFlag;
      },
      set: function (value) {
        this.blueFlagEdit = value;
      },
    },
    tagsInSelect() {
      var _this = this;
      return this.allTags.filter((x) => {
        return _this.tagsEdit.indexOf(x) == -1;
      });
    },
    // star: {
    //   get() {
    //     return this.parsed.star;
    //   },
    //   set(val) {
    //     this.starEdit = val;
    //   },
    // },
  },
  methods: {
    update() {
      let project = this.parsed.project;
      let itemName = this.referenceEdit.name;
      let item = this.referenceEdit.item;
      if (this.blueFlagEdit != undefined) {
        item.data.blueFlag = this.blueFlagEdit;
      }
      if (this.redFlagEdit != undefined) {
        item.data.redFlag = this.redFlagEdit;
      }
      item.data.rating = this.starEdit;
      if (this.tagsEdit[0] == "No Tag") {
        item.data.tag = [];
      } else {
        item.data.tag = this.tagsEdit;
      }
      if (this.noteEdit != undefined) {
        item.data.note = this.noteEdit;
      }
      // item.data.updateTime = Date.now();
      console.log(item);
      // this.projectDataAll[project]["items"]["data"][itemName] = item;
      update(project, itemName, item);
      // updateItem(project, itemName, {
      //   blueFlag: item.data.blueFlag,
      //   redFlag: item.data.redFlag,
      //   rating: item.data.rating,
      //   tag: item.data.tag,
      //   note: item.data.note,
      // });
      this.$emit("close");
    },
    renewStar() {
      this.starEdit = 0;
    },
    addTag() {
      if (this.tagInput == null || this.tagInput.trim() == "") {
        this.tagInput = "";
        return;
      }
      if (this.tagsEdit.indexOf(this.tagInput) != -1) {
        this.tagInput = "";
        return;
      }
      this.tagsEdit = this.tagsEdit.filter((x) => {
        return x != "No Tag";
      });
      this.tagsEdit.unshift(this.tagInput);
      this.selectedTags = [];
      this.tagInput = "";
    },
    input(x) {
      console.log(x);
      this.tagsEdit.unshift(x[x.length - 1]);
      this.tagsEdit = this.tagsEdit.filter((x) => {
        return x != "No Tag";
      });
      this.selectedTags = [];
    },
    tags() {
      if (this.tagsEdit == undefined) {
        this.tagsEdit = this.referenceEdit.item.data.tag;
        if (this.tagsEdit.length == 0) {
          this.tagsEdit = ["No Tag"];
        }
      }
      return this.tagsEdit;
    },
    del(tag) {
      if (tag == "No Tag") return;
      this.tagsEdit = this.tagsEdit.filter((x) => {
        return x != tag;
      });
      if (this.tagsEdit.length == 0) {
        this.tagsEdit = ["No Tag"];
      }
    },
    parse(referenceEdit) {
      let item = referenceEdit.item;
      let ret = parse(item);
      return ret;
    },
  },
  watch: {
    // referenceEditOpen(n, o) {
    //   console.log("referenceeditopen", n, o);
    // },
    starEdit(n, o) {
      console.log("starEdit", n, o);
    },
    tagsEdit(n, o) {
      console.log("tagsEdit", n, o);
    },
    redFlagEdit(n, o) {
      console.log("red", n, o);
    },
    referenceEdit(n) {
      console.log(n);
      this.tagsEdit = n.item.data.tag;
      if (this.tagsEdit.length == 0) {
        this.tagsEdit = ["No Tag"];
      }
      this.starEdit = n.item.data.rating;
      this.noteEdit = n.item.data.note;
    },
    x(n, o) {
      console.log("x", n, o);
    },
    tagInput(n, o) {
      console.log("tagInput", n, o);
    },
  },
  updated() {
    if (this.starEdit == undefined) {
      this.starEdit = this.referenceEdit.item.data.rating;
    }

    // let x = this.parse(this.referenceEdit);
    // this.title = x.title;
    // this.journal = x.containerTitleShort;
    // this.pmid = x.pmid;
    // this.doi = x.doi;
    // this.author = x.author;
    // this.redFlag = this.referenceEdit.item.data.redFlag;
    // this.blueFlag = this.referenceEdit.item.data.blueFlag;
  },
};
</script>

<style>
.v-menu__content.theme--light.v-menu__content--fixed.menuable__content__active.v-autocomplete__content {
  max-height: 200px !important;
}
</style>