export function createProject(projectName) {
    chrome.storage.local.get(["projectData", "options"], function (result) {
        let projectData = result.projectData;
        let options = result.options
        projectData[projectName] = { items: {} }
        updateSummary(projectName, projectData, options)
        // _this.projectData = _this.projectDataAll[_this.project];
    })
}

export function deleteProject(projectName) {
    chrome.storage.local.get(["projectData", "options"], function (result) {
        let projectData = result.projectData;
        let options = result.options
        delete projectData[projectName]
        updateSummary(null, projectData, options)
        // _this.projectData = _this.projectDataAll[_this.project];
    })
}

export function renameProject(newProjectName, oldProjectName) {
    chrome.storage.local.get(["projectData", "options"], function (result) {
        let projectData = result.projectData;
        let options = result.options
        projectData[newProjectName] = projectData[oldProjectName]
        for (var [key, item] of Object.entries(projectData[newProjectName].items)) {
            item.data.project = newProjectName
        }
        delete projectData[oldProjectName]
        updateSummary(newProjectName, projectData, options)
        // _this.projectData = _this.projectDataAll[_this.project];
    })
}

export function addNewArticle(project, item) {
    chrome.storage.local.get(["projectData", "options"], async function (result) {
        let projectData = result.projectData;
        let options = result.options
        let label = await makeLabel(item)
        item.label = label
        item.csl.id = label
        projectData[project]['items'][label] = item
        updateSummary(project, projectData, options)
    })
}

export async function addImportArticle(project, csls) {

    let ret = await new Promise(resolve => {
        chrome.storage.local.get(["projectData", "options"], async function (result) {
            let projectData = result.projectData;
            let options = result.options
            let idsExist = Object.keys(projectData._projectSummary.idsSummary)
            let successAddNumber = 0
            let alreadyExitsNumber = 0
            // console.log(projectData, csls)
            for (var csl of csls) {
                let label = await makeLabel({ csl: csl }, { ids: idsExist })
                idsExist.push(label)
                csl.id = label
                if (projectData[project]['items'][label] == undefined) {
                    projectData[project]['items'][label] = {
                        csl: csl,
                        data: {
                            blueFlag: false,
                            note: "",
                            project: project,
                            rating: 0,
                            redFlag: false,
                            tag: [],
                            updateTime: Date.now()
                        },
                        label: label
                    }
                    successAddNumber += 1
                } else {
                    alreadyExitsNumber += 1
                }
            }
            updateSummary(project, projectData, options)
            resolve({ successAddNumber: successAddNumber, alreadyExitsNumber: alreadyExitsNumber })
        })
    })
    return ret
}

async function makeLabel(item, { ids = [] } = {}) {
    function makeid(length) {
        var result = '';
        var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for (var i = 0; i < length; i++) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
    }
    if (ids.length == 0) {
        ids = await new Promise(resolve => {
            chrome.storage.local.get(["projectData"], function (result) {
                let projectData = result.projectData;
                let ids = Object.keys(projectData._projectSummary.idsSummary)
                console.log(ids)
                resolve(ids)
            })
        })
    }
    let csl = item.csl
    if (csl.PMID != undefined) {
        return `pmid:${csl.PMID}`
    }
    if (csl.DOI != undefined) {
        return `doi:${csl.DOI}`
    }
    console.log(ids)
    let id = makeid(8)
    while (ids.indexOf(id) != -1) {
        id = makeid(8)
    }
    return id
    // return Date.now().toString() + Math.random().toString()
}

export function updateDeleteDownloadItemList(itemList) {
    chrome.storage.local.get(["projectData", "options"], function (result) {
        let projectData = result.projectData;
        let options = result.options
        for (var item of itemList) {
            delete projectData[item.data.project].items[item.label].data.downloadId
        }
        // console.log('updateDeleteDownloadItemList', item)
        updateSummary(null, projectData, options)
    })
}

export function updateSummaryAll(options) {
    chrome.storage.local.get(["projectData"], function (result) {
        let projectData = result.projectData;
        // console.log('updateDeleteDownloadItemList', item)
        for (var _project in projectData) {
            if (_project == "_projectSummary") continue
            updateSummary(_project, projectData, options)
        }
    })
}

function updateSummary(project, projectData, options) {
    function sortByFrequency(array) {
        var frequency = {};
        var sortAble = [];
        var newArr = [];
        array.forEach(function (value) {
            if (value in frequency)
                frequency[value] = frequency[value] + 1;
            else
                frequency[value] = 1;
        });
        for (var key in frequency) {
            sortAble.push([key, frequency[key]])
        }
        sortAble.sort(function (a, b) {
            return b[1] - a[1]
        })
        sortAble.forEach(function (obj) {
            // for(var i=0; i < obj[1]; i++){
            //     newArr.push(obj[0]);
            // }
            newArr.push(obj[0])
        })
        return { frequency: frequency, sortedArray: newArr };
    }
    if (project != null) {
        let items = projectData[project]['items']
        let ids = []
        let tagsRaw = []
        for (var item in items) {
            ids.push(item)
            tagsRaw = tagsRaw.concat(items[item].data.tag)
        }
        let tags = sortByFrequency(tagsRaw)
        let tagsWithDefault = tags.sortedArray
        for (var defaultTag of options.collect.defaultTags) {
            if (!tagsWithDefault.includes(defaultTag)) {
                tagsWithDefault.push(defaultTag)
            }
        }
        let summary = { tags: tagsWithDefault, frequency: tags.frequency, ids: ids }
        projectData[project]['summary'] = summary
    }

    let _projectSummary = {}
    let idsSummary = {}
    for (var _project in projectData) {
        if (_project == "_projectSummary") continue
        let projectIds = projectData[_project]['summary']['ids']
        for (var id of projectIds) {
            if (idsSummary[id] == undefined) {
                idsSummary[id] = [_project]
            } else {
                idsSummary[id].push(_project)
            }
        }
    }
    let projectList = Object.keys(projectData).filter(x => { return x != '_projectSummary' && x != "ALL PROJECT" })
    _projectSummary = { idsSummary: idsSummary, projectList: projectList }
    projectData['_projectSummary'] = _projectSummary
    // console.log(projectData)
    chrome.storage.local.set({ projectData: projectData }, () => { })
}