        </v-tab>
        <v-tab-item v-for="(item, name, key) in items" :key="key" :id="'tab-' + key">
          <allSelect :nodes="item" />
        </v-tab-item>

        <v-tab class="font-weight-bold" :href="`#tab-3`">
