        </v-tab-item>
      </v-tabs>
    </v-card>-->

    <v-card>
      <v-toolbar color="primary" dark dense>
        <v-spacer />
        <v-toolbar-title>根据杂志重要性筛选</v-toolbar-title>
        <v-spacer />
      </v-toolbar>
      <v-tabs vertical class="my-tabs">
        <v-tab
          class="font-weight-bold"
          v-for="(item, key) in $options.nifqbt"
          :key="key"
          :href="`#tab-${key}`"
          style="background-color: #dbe0eb"
          :style="jcrStyle(item.class)"
        >
          <v-icon
            left
            size="28"
            :style="jcrStyle(item.class)"
            v-text="'mdi-alpha-' + item.class[0].toLowerCase() + '-box-outline'"
          ></v-icon>
          {{ cn(item.class) }}
        </v-tab>
        <v-tab-item
          v-for="(item, key) in $options.nifqbt"
          :key="key"
          :id="'tab-' + key"
        >
          <!-- <allSelect
            nodesClass="importanceMeasurement"
            :nodesName="item.class.split(' ')[0].toUpperCase()"
            :nodes="itemArray(item.subclasses)"
            :nodesDisplay="itemArrayDisplay(item.subclasses)"
          />-->
          <importanceMeasurement
            nodesClass="importanceMeasurement"
            :nodesName="item.class.split(' ')[0].toUpperCase()"
            :nodes="itemArray(item.subclasses)"
            :nodesDisplay="itemArrayDisplay(item.subclasses)"
          />
        </v-tab-item>

        <v-tab
          class="font-weight-bold"
          :href="`#tab-3`"
          style="background-color: #dbe0eb"
          :style="imfStyle()"
        >
          <v-icon
            left
            size="28"
            v-text="'mdi-alpha-i-box-outline'"
            :style="imfStyle()"
          ></v-icon
          >影响因子
        </v-tab>
        <v-tab-item id="tab-3">
          <impactFactor />
        </v-tab-item>
      </v-tabs>
    </v-card>

    <v-card class="mt-6">
      <v-toolbar color="primary" dark dense>
        <v-spacer />
        <v-toolbar-title>根据杂志类别筛选</v-toolbar-title>
        <v-spacer />
      </v-toolbar>
      <v-tabs vertical class="my-tabs">
        <v-tab
          class="font-weight-bold"
          v-for="(item, key) in $options.areaCategory"
          :key="key"
          :href="`#tab-${key}`"
          style="background-color: #dbe0eb"
          :style="areaStyle(item.class)"
        >
          <v-icon
            left
            size="28"
            v-text="'mdi-hexagon-multiple-outline'"
            :style="areaStyle(item.class)"
          ></v-icon>
          {{ item.class_cn + " (" + item.n_class + ")" }}
        </v-tab>
        <v-tab-item
          v-for="(item, key) in $options.areaCategory"
          :key="key"
          :id="'tab-' + key"
        >
          <!-- <allSelect
            nodesClass="areaCategory"
            :nodesDisplay="itemArrayDisplayArea(item.class, item.subclasses)"
            :nodesName="item.class"
            :nodes="itemArray(item.subclasses)"
          />-->
          <div class="d-flex flex-row justify-center">
            <areaCategory nodesClass="areaCategory" :nodesName="item.class" />
          </div>
          <!-- :nodesDisplay="itemArrayDisplayArea(item.class, item.subclasses)"
          :nodes="itemArray(item.subclasses)"-->
        </v-tab-item>
      </v-tabs>
    </v-card>

    <addBar class="mt-6" />
    <filteredTable class="mt-6" />
    <!-- <filteredJson class="mt-6" /> -->
    <filteredSend class="mt-6" />
    <ad class="mt-6" />
    <Footer />
  </div>
</template>

<script>
import nifqbtJson from "../json/n_ifqbt_json.json";
import titleJson from "../json/n_area_json.json";
import { mapState } from "vuex";

// import allSelect from "./allSelect";
import importanceMeasurement from "./importanceMeasurement";
import areaCategory from "./areaCategory";
import impactFactor from "./impactFactor";
import addBar from "./addBar";
import filteredTable from "./filteredTable";
// import filteredJson from "./filteredJson";
import filteredSend from "./filteredSend";
import ad from "./ad";
import Footer from "./footer";
// import allSelectTab from "./allSelectTab";

export default {
  name: "meanCn",
  nifqbt: nifqbtJson,
  areaCategory: titleJson,
  components: {
    importanceMeasurement,
    areaCategory,
    // allSelect,
    impactFactor,
    addBar,
    // filteredJson,
    filteredTable,
    filteredSend,
    ad,
    Footer,
    // allSelectTab
  },
  data: function () {
    return {
      // items: {
      //   "JCR Quartile": ["Q1", "Q2", "Q3", "Q4"],
      //   "CAS Block": ["B1", "B2", "B3", "B4"],
      //   "Top Journal": ["Top Journal"]
      // }
    };
  },
  computed: {
    ...mapState({
      importanceMeasurement: (state) => state.filter.importanceMeasurement,
      areaCategory: (state) => state.filter.areaCategory,
      add: (state) => state.filter.add,
      del: (state) => state.filter.del,
      customize: (state) => state.filter.customize,
      nImportanceArea: (state) => state.result.nImportanceArea,
    }),
  },
  watch: {
    importanceMeasurement: {
      handler: function () {
        this.$store.commit("toggleLoad", true);
        // this.$store
        //   .dispatch("update")
        //   .then(this.$store.commit("toggleLoad", false));
        // let tic = Date.now();
        // console.log("a", tic);
        setTimeout(() => {
          this.$store
            .dispatch("update")
            .then(this.$store.commit("toggleLoad", false));
        }, 1000);
        // console.log("d", Date.now() - tic);
      },
      deep: true,
    },
    areaCategory: {
      handler: function () {
        this.$store.commit("toggleLoad", true);
        // this.$store
        //   .dispatch("update")
        //   .then(this.$store.commit("toggleLoad", false));
        setTimeout(() => {
          this.$store
            .dispatch("update")
            .then(this.$store.commit("toggleLoad", false));
        }, 1000);
      },
      deep: true,
    },
    add: {
      handler(newValue, oldvalue) {
        this.$store.commit("toggleLoad", true);
        if (newValue.length > oldvalue.length) {
          let diff = newValue.filter((x) => !oldvalue.includes(x));
          setTimeout(() => {
            this.$store
              .dispatch("updateAdd", { param: "more", value: diff })
              .then(this.$store.commit("toggleLoad", false));
          }, 200);
        } else {
          let diff = oldvalue.filter((x) => !newValue.includes(x));
          setTimeout(() => {
            this.$store
              .dispatch("updateAdd", { param: "less", value: diff })
              .then(this.$store.commit("toggleLoad", false));
          }, 200);
        }
      },
    },
    del: {
      handler(newValue, oldvalue) {
        this.$store.commit("toggleLoad", true);
        if (newValue.length > oldvalue.length) {
          let diff = newValue.filter((x) => !oldvalue.includes(x));
          setTimeout(() => {
            this.$store
              .dispatch("updateDel", { param: "more", value: diff })
              .then(this.$store.commit("toggleLoad", false));
          }, 100);
        } else {
          let diff = oldvalue.filter((x) => !newValue.includes(x));
          setTimeout(() => {
            this.$store
              .dispatch("updateDel", { param: "less", value: diff })
              .then(this.$store.commit("toggleLoad", false));
          }, 100);
        }
      },
    },
  },
  methods: {
    cn(i) {
      if (i == "JCR Quartile") return "JCR分区（JCR Quartile）";
      if (i == "CAS Block") return "中科院分区（CAS Block）";
      if (i == "Top Journal") return "顶级刊物/非顶级刊物";
    },
    jcrStyle: function (item) {
      let id = item.split(" ")[0].toUpperCase();
      // console.log(id, this.importanceMeasurement[id].length);
      if (this.importanceMeasurement[id].length != 0) {
        return { color: "#b71c1c" };
      } else {
        return {};
      }
    },
    imfStyle: function () {
      if (
        this.importanceMeasurement.IMF[0] != 0 ||
        this.importanceMeasurement.IMF[1] != 300
      ) {
        return { color: "#b71c1c" };
      } else {
        return {};
      }
    },
    areaStyle: function (item) {
      if (this.areaCategory[item].length != 0) {
        return { color: "#b71c1c" };
      } else {
        return {};
      }
    },
    itemArray: function (subclasses) {
      return subclasses.map(function (i) {
        return i.subclass;
      });
    },
    itemArrayDisplay: function (subclasses) {
      return subclasses.map(function (i) {
        let x = i.subclass;
        if (x == "Non-Top Journal") x = "非顶级刊物";
        if (x == "Top Journal") x = "顶级刊物";
        return x + " (" + i.n + ")";
      });
    },
    itemArrayDisplayArea: function (item, subclasses) {
      let x = this.$store.getters.nImportanceArea[item];
      return subclasses.map(function (i) {
        return i.subclass + " (" + x[i.subclass] + "/" + i.n + ")";
      });
    },
  },
  created() {
    this.$store.dispatch("init");
  },
};
</script>


<style lang="css" scoped>
/* @import url("https://fonts.googleapis.com/css2family=Poppins:wght@400;700&display=swap"); */
.my-tabs [role="tab"] {
  justify-content: flex-start;
  max-width: 800px;
  width: 430px;
}
