<template>
  <div>
    <v-dialog
      id="dialogEdit"
      v-model="referenceCitationOpen"
      max-width="800"
      persistent
    >
      <!-- v-model="referenceCitationOpen"
      v-if="referenceCitationOpen" -->
      <v-card>
        <v-card-title class="primary white--text">
          <span>Citation</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 400px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="8" class="pb-0">
                <v-autocomplete
                  v-model="_style1"
                  :items="cslSummary"
                  item-text="title"
                  item-value="csl_name"
                  color="primary"
                  label="Citation Style"
                  hide-details
                  return-object
                  spellcheck="false"
                ></v-autocomplete>
              </v-col>
              <v-col cols="4" class="pb-0">
                <v-select
                  v-model="_lang1"
                  :items="cslLocale"
                  item-text="label"
                  item-value="label"
                  color="primary"
                  label="Language"
                  hide-details
                  return-object
                ></v-select>
              </v-col>
              <v-col cols="12" class="pt-2 pb-1">
                <v-icon
                  left
                  class="primary--text"
                  style="cursor: pointer"
                  @click="citation_copy('inline1')"
                  >mdi-content-copy</v-icon
                >
                <span ref="inline1" v-html="inline1"></span>
              </v-col>
              <v-col cols="12" class="py-1">
                <v-icon
                  left
                  class="primary--text"
                  style="cursor: pointer"
                  @click="citation_copy('reference1')"
                  >mdi-content-copy</v-icon
                >
                <span ref="reference1" v-html="reference1"></span>
              </v-col>
              <v-col cols="8" class="pb-0">
                <v-autocomplete
                  v-model="_style2"
                  :items="cslSummary"
                  item-text="title"
                  item-value="csl_name"
                  color="primary"
                  label="Citation Style"
                  hide-details
                  return-object
                  spellcheck="false"
                ></v-autocomplete>
              </v-col>
              <v-col cols="4" class="pb-0">
                <v-select
                  v-model="_lang2"
                  :items="cslLocale"
                  item-text="label"
                  item-value="label"
                  color="primary"
                  label="Language"
                  hide-details
                  return-object
                ></v-select>
              </v-col>
              <v-col cols="12" class="pt-2 pb-1">
                <v-icon
                  left
                  class="primary--text"
                  style="cursor: pointer"
                  @click="citation_copy('inline2')"
                  >mdi-content-copy</v-icon
                >
                <span ref="inline2" v-html="inline2"></span>
              </v-col>
              <v-col cols="12" class="py-1">
                <v-icon
                  left
                  class="primary--text"
                  style="cursor: pointer"
                  @click="citation_copy('reference2')"
                  >mdi-content-copy</v-icon
                >
                <span ref="reference2" v-html="reference2"></span>
              </v-col>
              <v-col cols="11" class="py-1">
                <v-select
                  hide-details
                  v-model="bibFormat"
                  :items="bibFormats"
                  label="Bibliography Format"
                ></v-select>
              </v-col>
              <v-col cols="1" class="py-1 d-flex flex-column justify-end">
                <v-icon
                  class="primary--text pb-1"
                  style="cursor: pointer"
                  @click="citation_copy('bib')"
                  >mdi-content-copy</v-icon
                >
              </v-col>
              <v-col
                cols="12"
                class="pa-1 ma-2 rounded-lg"
                style="border: 1.5px solid #91919191"
              >
                <span ref="bib" style="white-space: pre-line">{{
                  bibFormatted
                }}</span>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="isCopySuccess" timeout="3000" top color="success">
      Successfully Copy to Clipboard

      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="isCopySuccess = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import cslSummary from "../json/csl_summary.json";
import cslLocale from "../json/csl_locale.json";
import { get_reference, get_bibliography } from "./citation";

cslSummary.unshift({
  title: "Powerpoint Reference",
  csl: "for-powerpoint",
  csl_name: "for-powerpoint",
});
cslSummary.unshift({
  title: "Quick Reference(First, Last Author)",
  csl: "quick-ref-first-last-author",
  csl_name: "quick-ref-first-last-author",
});
cslSummary.unshift({
  title: "Quick Reference(Last Author)",
  csl: "quick-ref-last-author",
  csl_name: "quick-ref-last-author",
});
cslLocale.unshift({ name: "auto", label: "Auto" });
// console.log(cslSummary);
export default {
  name: "referenceCitation",
  props: {
    referenceCitationOpen: Boolean,
    referenceCitation: Object,
    options: Object,
    journalImfqbtData: Object,
  },
  data() {
    return {
      cslSummary: cslSummary,
      cslLocale: cslLocale,
      style1: "",
      lang1: "",
      inline1: "<i>lll<i>",
      reference1: `<div class="csl-entry">
    <div class="csl-left-margin">1.</div><div class="csl-right-inline">Boehncke, W.-H. &amp; Schön, M. P. Psoriasis. <i>Lancet</i> <b>386</b>, 983–94 (2015).</div>
  </div>`,
      style2: "",
      lang2: "",
      inline2: "",
      reference2: "",
      bibFormats: ["CSL-JSON", "RIS", "BIBTEX"],
      bibFormat: "",
      bibFormatted: "",
      isCopySuccess: false,
    };
  },
  computed: {
    csls() {
      let csls = {};
      csls[this.referenceCitation.item.label] = this.referenceCitation.item.csl;
      return csls;
    },
    _style1: {
      get() {
        if (Object.keys(this.options).length == 0) return "NA";
        return this.options.citation[0][0];
      },
      set(n) {
        // console.log("aa", n);
        this.style1 = n.csl;
        let options = this.options;
        options.citation[0][0] = n.csl_name;
        chrome.storage.local.set({ options: options }, () => {});
      },
    },
    _lang1: {
      get() {
        if (Object.keys(this.options).length == 0) return "NA";
        return this.options.citation[0][1];
      },
      set(n) {
        this.lang1 = n.name;
        let options = this.options;
        options.citation[0][1] = n.label;
        chrome.storage.local.set({ options: options }, () => {});
      },
    },
    _style2: {
      get() {
        if (Object.keys(this.options).length == 0) return "NA";
        return this.options.citation[1][0];
      },
      set(n) {
        this.style2 = n.csl;
        let options = this.options;
        options.citation[1][0] = n.csl_name;
        chrome.storage.local.set({ options: options }, () => {});
      },
    },
    _lang2: {
      get() {
        if (Object.keys(this.options).length == 0) return "NA";
        return this.options.citation[1][1];
      },
      set(n) {
        this.lang2 = n.name;
        let options = this.options;
        options.citation[1][1] = n.label;
        chrome.storage.local.set({ options: options }, () => {});
      },
    },
  },
  watch: {
    style1() {
      this.inline1 = "Loading...";
      this.reference1 = "Loading...";
      this.update1();
    },
    lang1() {
      this.inline1 = "Loading...";
      this.reference1 = "Loading...";
      this.update1();
    },
    style2() {
      this.inline2 = "Loading...";
      this.reference2 = "Loading...";
      this.update2();
    },
    lang2() {
      this.inline2 = "Loading...";
      this.reference2 = "Loading...";
      this.update2();
    },
    bibFormat() {
      this.bibFormatted = "Loading...";
      this.updateBib();
    },
    async referenceCitationOpen() {
      if (this.referenceCitationOpen == false) return;
      this.inline1 = "Loading...";
      this.inline2 = "Loading...";
      this.reference1 = "Loading...";
      this.reference2 = "Loading...";
      this.bibFormatted = "Loading...";
      var _this = this;
      // await this.update1();
      // await this.update2();
      // await this.updateBib();
      setTimeout(async function () {
        await _this.update1();
        await _this.update2();
        await _this.updateBib();
      }, 10);
      // if (this.referenceCitationOpen == true) {
      //   setTimeout(function () {
      //     _this.update1();
      //     _this.update2();
      //     _this.updateBib();
      //   }, 1000);
      // }
    },
  },
  methods: {
    async update1() {
      if (this.style1 == "") {
        this.style1 = cslSummary.filter((x) => {
          return x.csl_name == this.options.citation[0][0];
        })[0].csl;
      }
      if (this.lang1 == "") {
        this.lang1 = cslLocale.filter((x) => {
          return x.label == this.options.citation[0][1];
        })[0].name;
      }
      // console.log(this.referenceCitation);
      let x = await get_reference(
        this.csls, //for csls citeproc
        this.style1,
        this.lang1,
        this.referenceCitation.item.label,
        { item: this.referenceCitation.item } //for item parse self-defiend style
      );
      // let x = await get_reference(
      //   this.csls,
      //   this.style1,
      //   this.lang1,
      //   this.referenceCitation.item.label,
      //   this.journalImfqbtData
      // );
      this.inline1 = x.inline;
      this.reference1 = x.bibliography;
    },
    async update2() {
      if (this.style2 == "") {
        this.style2 = cslSummary.filter((x) => {
          return x.csl_name == this.options.citation[1][0];
        })[0].csl;
      }
      if (this.lang2 == "") {
        this.lang2 = cslLocale.filter((x) => {
          return x.label == this.options.citation[1][1];
        })[0].name;
      }
      let x = await get_reference(
        this.csls, //for csls citeproc
        this.style2,
        this.lang2,
        this.referenceCitation.item.label,
        { item: this.referenceCitation.item } //for item parse
      );
      // let x = await get_reference(
      //   this.csls,
      //   this.style2,
      //   this.lang2,
      //   this.referenceCitation.item.label,
      //   this.journalImfqbtData
      // );
      this.inline2 = x.inline;
      this.reference2 = x.bibliography;
    },
    updateBib() {
      if (this.bibFormat == "") {
        this.bibFormat = this.options.citation[2][0];
        console.log("iniBib", this.bibFormat);
      }
      let bibFormat = this.bibFormat;
      let csls = this.csls;
      this.bibFormatted = get_bibliography(csls, bibFormat);
      let options = this.options;
      options.citation[2][0] = bibFormat;
      chrome.storage.local.set({ options: options }, () => {});
      //   console.log(this.bibFormatted);
    },
    citation_copy(dom) {
      // console.log("here");
      var str_html = this.$refs[dom].innerHTML.replaceAll(
        /\<div class=.*?\>|\<\/div\>/gi,
        ""
      );
      var str_plain = this.$refs[dom].textContent;
      // console.log(str_html, str_plain);
      function listener(e) {
        e.clipboardData.setData("text/html", str_html);
        e.clipboardData.setData("text/plain", str_plain);
        e.preventDefault();
      }
      document.addEventListener("copy", listener);
      document.execCommand("copy");
      document.removeEventListener("copy", listener);
      this.isCopySuccess = true;
      // console.log($('#bibliography-1 .csl-entry'))
    },
  },
};
</script>

<style>
.csl-entry {
  display: inline;
}
.csl-left-margin {
  display: inline;
}
.csl-right-inline {
  display: inline;
}
.v-list-item__title {
  white-space: normal !important;
}
.menuable__content__active {
  max-width: 400px;
}
</style>