<template>
  <v-dialog
    id="dialogAttach"
    v-model="referenceAttachOpen"
    v-if="referenceAttachOpen"
    max-width="800"
    persistent
  >
    <v-card>
      <v-card-title class="primary white--text">
        <span>Attach Full-text URL/PDF</span>
      </v-card-title>
      <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
        <v-container>
          <v-row>
            <v-col cols="12" class="pt-3 pb-1">
              <v-text-field
                v-model="urlInput"
                label="Input Full-text URL"
                prepend-icon="mdi-link"
                spellcheck="false"
                clearable
                @click:append-outer="linkUrl"
                @keyup.enter="linkUrl"
              >
                <!-- append-outer-icon="mdi-link" -->
                <template v-slot:append-outer>
                  <v-btn dark color="success" small fab @click="linkUrl">
                    <v-icon dark>mdi-link-plus</v-icon>
                  </v-btn>
                </template>
              </v-text-field>
            </v-col>
            <v-col cols="12" class="pt-3 pb-1">
              <v-file-input
                accept="application/pdf"
                label="Input Full-text PDF"
                append-outer-icon="mdi-link"
                v-model="pdfInput"
                ><template v-slot:append-outer>
                  <v-btn dark color="success" small fab @click="linkPdf">
                    <v-icon dark>mdi-link-plus</v-icon>
                  </v-btn>
                </template></v-file-input
              >
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <!-- <v-btn class="mr-3" color="error" @click="del()">
          Delete
          <v-icon right>mdi-delete</v-icon>
        </v-btn> -->
        <v-btn class="mr-6" color="primary" @click="$emit('close')">
          Close
          <v-icon right>mdi-close-circle</v-icon>
        </v-btn>
        <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { updateItem } from "./setProjectData";
import { downloadArticle } from "./downloadArticle";
export default {
  name: "referenceAttach",
  props: {
    referenceAttachOpen: Boolean,
    referenceAttach: Object,
  },
  data() {
    return {
      urlInput: "",
      pdfInput: {},
    };
  },
  watch: {
    referenceAttachOpen(n) {
      if (n == true) {
        this.urlInput = this.referenceAttach.data.fulltextUrl;
        this.pdfInput = undefined;
      }
    },
  },
  methods: {
    linkUrl() {
      console.log(this.urlInput, this.referenceAttach);
      updateItem(
        this.referenceAttach.data.project,
        this.referenceAttach.label,
        { fulltextUrl: this.urlInput }
      );
      downloadArticle(this.urlInput, this.referenceAttach);
      this.$emit("close");
    },
    linkPdf() {
      let url = URL.createObjectURL(this.pdfInput);
      downloadArticle(url, this.referenceAttach);
      // console.log(url);
      // console.log(this.pdfInput);
      this.$emit("close");
    },
  },
};
</script>

