<template>
  <div>
    <v-dialog
      v-model="settingsOpen"
      v-if="settingsOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Settings</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="pb-0">
                <v-select
                  v-model="fullTextFilenameStyle"
                  :items="fullTextFilenameStyles"
                  item-value="styleId"
                  item-text="name"
                  label="Select Filename Style for Full-text PDF"
                ></v-select>
              </v-col>
              <v-col cols="3" class="pt-0">
                <span class="primary white--text mr-2 px-2 rounded"
                  >Filename Example:</span
                >
              </v-col>
              <v-col cols="9" class="pt-0 px-0">
                <span class="primary--text">{{ filenameExample }}</span>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="tagInput"
                  label="Add Default Tag"
                  append-outer-icon="mdi-plus"
                  @click:append-outer="addTag"
                  @keyup.enter="addTag"
                >
                </v-text-field>
              </v-col>
              <v-col cols="3" class="pt-0">
                <span class="primary white--text mr-2 px-2 rounded"
                  >Default Tags:</span
                >
              </v-col>
              <v-col cols="9" class="px-0 pt-0">
                <div class="d-block align-self-center">
                  <span
                    v-for="(tag, index) in tagsDisplay"
                    :key="index"
                    class="rounded primary white--text px-2 mr-2 d-inline-block"
                    @click="del(tag)"
                    style="cursor: pointer"
                  >
                    {{ tag }}
                  </span>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-3" color="success" @click="set">
            Set
            <v-icon right>mdi-check</v-icon>
          </v-btn>
          <v-btn class="mr-6" color="primary" @click="close">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="isWarning" timeout="3000" top color="error">
      Please set a filename style for full-text PDF
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="isWarning = false">
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { makeFilename } from "./downloadArticle";
import { setOption } from "./setOption";
export default {
  name: "settings",
  props: {
    settingsOpen: Boolean,
    options: Object,
  },
  data() {
    return {
      fullTextFilenameStyle: undefined,
      fullTextFilenameStyles: [
        { styleId: 1, name: "Title" },
        {
          styleId: 2,
          name:
            "LastAuthorFamilyName-FirstAuthorFamilyName-Year-JournalName-Title",
        },
        { styleId: 3, name: "PMID" },
        { styleId: 4, name: "PMID-Title" },
      ],
      cslExample: {
        csl: {
          DOI: "10.1080/13548506.2018.1529323",
          ISSN: "1354-8506",
          PMID: "30293440",
          accessed: { "date-parts": [[2021, 3, 15]] },
          author: [
            { family: "Tian", given: "Zhen" },
            { family: "Huang", given: "Yike" },
            { family: "Yue", given: "Tao" },
            { family: "Zhou", given: "Jiaqing" },
            { family: "Tao", given: "Lu" },
            { family: "Han", given: "Ling" },
            { family: "Yan", given: "Kexiang" },
            { family: "Huang", given: "Qiong" },
            { family: "Zhang", given: "Zhenghua" },
            { family: "Shao", given: "Chunhong" },
          ],
          "container-title": "Psychology, health & medicine",
          "container-title-short": "Psychol Health Med",
          "epub-date": { "date-parts": [[2018, 10, 7]] },
          id: "pmid:30293440",
          issue: "3",
          issued: { "date-parts": [[2019, 3]] },
          page: "269-280",
          "publisher-place": "England",
          source: "PubMed",
          title:
            "A Chinese cross-sectional study on depression and anxiety symptoms in patients with psoriasis vulgaris",
          type: "article-journal",
          volume: "24",
        },
        data: {
          abstract:
            "Previous studies have reported a higher incidence of depression and anxiety in psoriasis patients compared to the general population, which has important implications for assessment and treatment. In this study, we determined the frequency of depression and anxiety in Chinese patients with psoriasis and its relationship with disease severity and other demographic variables. The study included 208 Chinese patients with psoriasis vulgaris. The Patient Health Questionnaire (PHQ-9) and Generalized Anxiety Disorder scale (GAD-7) were used to screen for depression and anxiety. The Psoriasis Area and Severity Index (PASI) was used to assess the severity of psoriasis. Of the 208 patients included in the study, 29 patients (13.9%) were positive for moderate-to-severe depression (PHQ-9 ≥ 10) and 22 patients (10.6%) were positive for anxiety (GAD-7 ≥ 10) symptoms. Both positive stress reactors who perceived stress as an exacerbating factor of psoriasis and moderate-to-severe psoriasis were found to be positive predictors for the presence of moderate-to-severe depression or anxiety symptoms while longer duration and late onset age played a protective role. In the sample of Chinese patients with psoriasis there was a clinically significant prevalence of depression and anxiety. Our study suggests that Chinese psoriasis patients should be screened for psychiatric comorbidities.",
          blueFlag: false,
          downloadId: 1800,
          fulltextUrl:
            "http://111.90.145.71/scimag/get.php?doi=10.1080/13548506.2018.1529323&key=9A48NX4SMLYR88FY",
          note: "",
          project: "My Favorite",
          rating: 0,
          redFlag: false,
          tag: [],
          updateTime: 1615801360299,
        },
        label: "pmid:30293440",
      },
      filenameExample: "",
      tagInput: "",
      tags: [],
      isWarning: false,
    };
  },
  computed: {
    tagsDisplay() {
      if (this.tags.length == 0) {
        return ["No Tag"];
      } else {
        return this.tags;
      }
    },
  },
  watch: {
    async settingsOpen(n) {
      if (n == true) {
        this.fullTextFilenameStyle = this.options.collect.fullTextFilenameStyle;
        let filename = await makeFilename(this.cslExample, {
          styleId: this.fullTextFilenameStyle,
        });
        this.filenameExample = filename.filenameShort;
        this.tags = this.options.collect.defaultTags;
      }
    },
    async fullTextFilenameStyle() {
      let filename = await makeFilename(this.cslExample, {
        styleId: this.fullTextFilenameStyle,
      });
      console.log(filename);
      this.filenameExample = filename.filenameShort;
    },
  },
  methods: {
    addTag() {
      if (this.tagInput.trim() == "") return;
      if (this.tags.indexOf(this.tagInput) == -1) {
        this.tags.push(this.tagInput);
      }
      this.tagInput = "";
    },
    del(tag) {
      this.tags = this.tags.filter((x) => {
        return x != tag;
      });
    },
    set() {
      if (this.fullTextFilenameStyle == undefined) {
        this.isWarning = true;
        return;
      }
      this.options.collect.fullTextFilenameStyle = this.fullTextFilenameStyle;
      this.options.collect.defaultTags = this.tags;
      setOption({ collect: this.options.collect });
      this.$emit("close");
    },
    close() {
      if (this.options.collect.fullTextFilenameStyle == undefined) {
        this.isWarning = true;
        return;
      }
      if (this.fullTextFilenameStyle == undefined) {
      } else {
        this.$emit("close");
      }
    },
  },
};
</script>

<style>
</style>