var $ = require("jquery")
var CSL = require('citeproc')
var Cite = require('citation-js')
// import { resolve } from "core-js/fn/promise"
import { parse } from "./parse"
function get_style(style) {
    return new Promise(function (resolve, reject) {
        chrome.runtime.sendMessage({ command: 'get_style', style: style },
            function (response) {
                // console.log('csl_xml', response)
                if (response != 'error') {
                    resolve(response)
                } else {
                    reject(new Error('error'))
                }
            })
    })
}

function get_locale(language) {
    var url = `data/locales/locales-${language}.xml`
    var language_url = chrome.runtime.getURL(url)
    return $.ajax(language_url, { async: false })
}

async function get_processor(csls, style, language) {
    var styleAsText
    styleAsText = await get_style(style)
    var citeprocSys = {
        retrieveLocale: function (lang) {
            var locale = get_locale(lang)
            return locale.responseText
        },
        retrieveItem: function (id) {
            return csls[id];
        }
    }
    // var locale = get_locale(language)
    // console.log(locale)
    var citeproc
    if (language == "auto") {
        var citeproc = new CSL.Engine(citeprocSys, styleAsText);
    } else {
        var citeproc = new CSL.Engine(citeprocSys, styleAsText, language, 1);
    }
    return citeproc;
}

export async function get_reference(csls, style, language, label, { item = {} } = {}) {
    // console.log('aa', journalImfqbtData)
    let selfDefinedStyle = ["quick-ref-last-author", "quick-ref-first-last-author", "for-powerpoint"]
    if (selfDefinedStyle.includes(style)) {
        let ret = await getSelfDefinedReference(item, style, language)
        return new Promise(resolve => {
            resolve(ret)
        })
    }

    // console.log('csls', csls)
    try {
        var citeproc = await get_processor(csls, style, language)
    } catch (e) {
        return Promise.reject(new Error("CSL style not found"))
    }
    // console.log('citeproc', citeproc)
    citeproc.updateItems([label])
    var citation = {
        citationID: "cite-1",
        citationItems: [
            { id: label }
        ],
        properties: {
            // "noteIndex": 0,
        }
    }
    // console.log(citation)
    var result = citeproc.processCitationCluster(citation, [], [])
    var bibResult = citeproc.makeBibliography();
    //   console.log(bibResult[1].join('\n'))
    // console.log('result', result[1][0][1])
    // console.log('bibresult', bibResult)
    return new Promise(resolve => {
        if (bibResult == false) {
            resolve({ inline: result[1][0][1], bibliography: "" })
        } else {
            resolve({ inline: result[1][0][1], bibliography: bibResult[1][0] })
        }
    })
}

async function getSelfDefinedReference(item, style, language) {
    let p = parse(item)
    let csl = item.csl
    if (style == "quick-ref-last-author") {
        let author_length = csl.author.length
        // let first_author_family =  csl.author[0].family
        let last_author_family = csl.author[`${author_length - 1}`].family
        // let author = author_length==1?last_author_family:`${first_author_family}, ${last_author_family}`
        let year = csl.issued['date-parts'][0][0]
        let journalAbb = csl['container-title-short']
        let imf = p.imf == "NA" ? "" : `IF:${p.imf}`
        let pmid = csl.PMID

        let inline = `(${last_author_family}, ${year}, ${csl['container-title-short']}[${imf}, PMID:${pmid}])`

        let monthLabel = { 1: 'Jan', 2: "Feb", 3: "Mar", 4: "Apr", 5: "May", 6: "Jun", 7: "Jul", 8: "Aug", 9: "Sep", 10: "Oct", 11: "Nov", 12: "Dec" }
        let month = csl.issued['date-parts'][0][1] == undefined ? "" : ` ${monthLabel[csl.issued['date-parts'][0][1]]}`
        let day = csl.issued['date-parts'][0][2] == undefined ? "" : ` ${csl.issued['date-parts'][0][2]}`
        let volume = csl.volume == undefined ? "" : `;${csl.volume}`
        let issue = csl.issue == undefined ? "" : `(${csl.issue})`
        let page = csl.page == undefined ? "" : `:${csl.page}`
        let bibliography = `<div class="csl-entry">${journalAbb}. ${year}${month}${day}${volume}${issue}${page}</div>`
        return new Promise(resolve => {
            resolve({ inline: inline, bibliography: bibliography })
        })
    }
    if (style == "quick-ref-first-last-author") {
        let author_length = csl.author.length
        let first_author_family = csl.author[0].family
        let last_author_family = csl.author[`${author_length - 1}`].family
        let author = author_length == 1 ? last_author_family : `${first_author_family}, ${last_author_family}`
        let year = csl.issued['date-parts'][0][0]
        let journalAbb = csl['container-title-short']
        let imf = p.imf == "NA" ? "" : `IF:${p.imf}`
        let pmid = csl.PMID
        let inline = `(${author}, ${year}, ${csl['container-title-short']}[${imf}, PMID:${pmid}])`

        let monthLabel = { 1: 'Jan', 2: "Feb", 3: "Mar", 4: "Apr", 5: "May", 6: "Jun", 7: "Jul", 8: "Aug", 9: "Sep", 10: "Oct", 11: "Nov", 12: "Dec" }
        let month = csl.issued['date-parts'][0][1] == undefined ? "" : ` ${monthLabel[csl.issued['date-parts'][0][1]]}`
        let day = csl.issued['date-parts'][0][2] == undefined ? "" : ` ${csl.issued['date-parts'][0][2]}`
        let volume = csl.volume == undefined ? "" : `;${csl.volume}`
        let issue = csl.issue == undefined ? "" : `(${csl.issue})`
        let page = csl.page == undefined ? "" : `:${csl.page}`
        let bibliography = `<div class="csl-entry">${csl['container-title-short']}. ${year}${month}${day}${volume}${issue}${page}</div>`
        return new Promise(resolve => {
            resolve({ inline: inline, bibliography: "" })
        })
    }
    if (style == "for-powerpoint") {
        let author_length = csl.author.length
        let first_author_family = author_length == 1 ? csl.author[0].family : `${csl.author[0].family} et al.`
        // let last_author_family = csl.author[`${author_length - 1}`].family
        // let author = author_length==1?last_author_family:`${first_author_family}, ${last_author_family}`
        let year = csl.issued['date-parts'][0][0]
        // let imf = bibs[pmid]['imf'] == "NA" ? "" : `IF:${bibs[pmid]['imf']}`
        let inline = `${first_author_family} ${csl['container-title-short']} ${year}`

        // let monthLabel = { 1: 'Jan', 2: "Feb", 3: "Mar", 4: "Apr", 5: "May", 6: "Jun", 7: "Jul", 8: "Aug", 9: "Sep", 10: "Oct", 11: "Nov", 12: "Dec" }
        // let month = csl.issued['date-parts'][0][1] == undefined ? "" : ` ${monthLabel[csl.issued['date-parts'][0][1]]}`
        // let day = csl.issued['date-parts'][0][2] == undefined ? "" : ` ${csl.issued['date-parts'][0][2]}`
        // let volume = csl.volume == undefined ? "" : `;${csl.volume}`
        // let issue = csl.issue == undefined ? "" : `(${csl.issue})`
        // let page = csl.page == undefined ? "" : `:${csl.page}`
        // let bibliography = `<div class="csl-entry">${csl['container-title-short']}. ${year}${month}${day}${volume}${issue}${page}</div>`
        return new Promise(resolve => {
            resolve({ inline: inline, bibliography: "" })
        })
    }
}

