function csl2bibtex(csl) {
    var bibtexObject = {}
    for (var i in csl) {
        let cslJson = new Cite(csl[i])
        let bibtexRaw = cslJson.format('bibtex')
        if (csl[i].hasOwnProperty('id')) {
            bibtexObject[i] = bibtexRaw.replace(/(?<=@.*{).*(?=,)/, csl[i]['id'])
        } else {
            bibtexObject[i] = bibtexRaw
        }
    }
    let output = ""
    for (var bibtex in bibtexObject) {
        output += bibtexObject[bibtex]
        // output += "\n"
    }
    return { bibtex: output, bibtexObject: bibtexObject }
}

function csl2ris(csl) {
    var risObject = {}
    for (var i in csl) {
        let cslJson = new Cite(csl[i])
        let risRaw = cslJson.format('ris')
        risObject[i] = risRaw
    }
    let output = ""
    for (var ris in risObject) {
        output += risObject[ris]
        output += "\n"
    }
    return { ris: output, risObject: risObject }
}

export function get_bibliography(csl, ref_style) {
    if (ref_style == "CSL-JSON") {
        return JSON.stringify(csl)
    } else if (ref_style == "BIBTEX") {
        return csl2bibtex(csl).bibtex
    } else if (ref_style == "RIS") {
        return csl2ris(csl).ris
    }
}

function bibtex2csl(bibtex) {
    debugger
    let cite = new Cite(bibtex, { forcetype: "@bibtex/text" })
    let csl = cite.data
    for (var i of csl) {
        if (i.hasOwnProperty('_graph')) {
            delete i['_graph']
        }
    }
    return { csl: JSON.stringify(csl), cslObject: csl }
}

function ris2csl(ris) {
    let cite = new Cite(ris, { forcetype: "ris" })
    let csl = cite.data
    for (var i of csl) {
        if (i.hasOwnProperty('_graph')) {
            delete i['_graph']
        }
    }
    return { csl: JSON.stringify(csl), cslObject: csl }
}

function csl2csl(cslString) {
    let cite = new Cite(cslString, { forcetype: "csl" })
    let csl = cite.data
    for (var i of csl) {
        if (i.hasOwnProperty('_graph')) {
            delete i['_graph']
        }
    }
    return { csl: JSON.stringify(csl), cslObject: csl }
}

export function get_csl(reference, ref_style) {
    if (ref_style == "CSL-JSON") {
        return csl2csl(reference)
    } else if (ref_style == "BIBTEX") {
        // console.log('BIBTEX')
        return bibtex2csl(reference)
    } else if (ref_style == "RIS") {
        return ris2csl(reference)
    }
}
