<template>
  <div>
    <v-dialog
      v-model="newArticleOpen"
      v-if="newArticleOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>New Article</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="3">
                <v-select
                  v-model="searchType"
                  :items="searchTypes"
                  label="Search Type"
                ></v-select>
              </v-col>
              <v-col cols="9" class="">
                <v-text-field
                  v-model="searchInput"
                  label="Search"
                  :placeholder="searchInputPlaceholder"
                  append-outer-icon="mdi-magnify"
                  clearable
                  spellcheck="false"
                  hide-details
                  @click:append-outer="doSearch"
                  @keyup.enter="doSearch"
                >
                </v-text-field>
              </v-col>
              <v-col v-if="isLoading" cols="12" class="pa-0">
                <v-progress-linear
                  :active="isLoading == true"
                  indeterminate
                  class=""
                  height="6"
                  rounded
                ></v-progress-linear>
              </v-col>
              <v-col cols="12" v-if="isLoading == false" class="pt-0">
                <div
                  v-for="(item, index) in searchResultForSelection"
                  :key="index"
                  class="py-1 px-2 hover"
                >
                  <div
                    class="text-body-2 font-weight-bold primary--text"
                    @click="
                      addItem = item;
                      newArticleAddOpen = true;
                    "
                    style="cursor: pointer"
                  >
                    {{ item.title }}
                  </div>
                  <div class="text-caption grey--text">
                    {{ item.description }}
                  </div>
                  <div class="text-caption grey--text">
                    {{ `DOI: ${item.DOI}` }}
                  </div>
                </div>
                <div
                  class="text-caption black--text px-2 hover mb-2"
                  style="cursor: pointer"
                  v-if="moreSearchURL != ''"
                  @click="moreSearch"
                >
                  More...
                </div>
                <v-progress-linear
                  v-if="isMoreLoading == true"
                  :active="isMoreLoading == true"
                  indeterminate
                  height="6"
                  rounded
                ></v-progress-linear>
              </v-col>
              <v-col
                cols="12"
                v-if="searchError"
                class="pt-0 text-body-2 font-weight-bold warning--text"
              >
                NOT FOUND
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <newArticleAdd
      :newArticleAddOpen="newArticleAddOpen"
      :addItem="addItem"
      :project="project"
      :projectDataAll="projectDataAll"
      v-on:close="newArticleAddOpen = false"
    ></newArticleAdd>
  </div>
</template>

<script>
var $ = require("jquery");
import { convert_to_csl } from "./zotero_csl";
import newArticleAdd from "./newArticleAdd";
import { parseCsl } from "./parse";

export default {
  name: "newArticle",
  props: {
    newArticleOpen: Boolean,
    project: String,
    projectDataAll: Object,
  },
  components: {
    newArticleAdd,
  },
  data() {
    return {
      searchInput: "",
      lastSearchInput: "",
      searchType: "PUBMED",
      searchTypes: ["PUBMED", "ZOTERO", "PMID", "DOI"],
      searchResultForSelection: [],
      isLoading: undefined,
      moreSearchURL: "",
      isMoreLoading: undefined,
      searchError: false,
      newArticleAddOpen: false,
      addItem: {},
      pubmedSearchTimes: 0,
    };
  },
  computed: {
    searchInputPlaceholder() {
      if (this.searchType == "ZOTERO") {
        return "Enter a URL, ISBN, DOI, PMID, arXiv ID, or title";
      }
      if (this.searchType == "PMID") {
        return "Enter a PMID";
      }
      if (this.searchType == "DOI") {
        return "Enter a DOI";
      }
      if (this.searchType == "PUBMED") {
        return "Search Pubmed";
      }
    },
  },
  methods: {
    doSearch() {
      this.isLoading = true;
      this.searchError = false;
      this.moreSearchURL = "";
      this.searchResultForSelection = [];
      this.pubmedSearchTimes = 0;
      if (this.searchType == "ZOTERO") {
        this.searchZotero(this.searchInput);
      }
      if (this.searchType == "PMID") {
        this.searchPMID(this.searchInput);
      }
      if (this.searchType == "DOI") {
        this.searchDOI(this.searchInput);
      }
      if (this.searchType == "PUBMED") {
        this.searchPubmed(this.searchInput);
      }
    },
    searchZotero(searchInput) {
      var _this = this;
      $.ajax(
        "https://t0guvf0w17.execute-api.us-east-1.amazonaws.com/Prod/search",
        {
          contentType: "text/plain",
          type: "POST",
          data: searchInput, //"10.1056/NEJMra041320", //"psoriasis",
          success: function (res) {
            if (res.length == 0) {
              _this.searchNotFound();
              return;
            }
            _this.searchResultForSelection = [];
            let csl = convert_to_csl(res[0]);
            // console.log(res, csl);
            if (csl.DOI == undefined) {
              _this.searchNotFound();
              return;
            }
            _this.searchResultForSelection = [];
            let display = _this.csl2Display(csl, "zotero");
            _this.searchResultForSelection.push(display);
            _this.isLoading = false;
            console.log("zotero_csl", display);
          },
          error: function (xhr, b, c) {
            if (xhr.status == 300) {
              (_this.searchError = false),
                (_this.searchResultForSelection = []);
              for (var [key, item] of Object.entries(xhr.responseJSON)) {
                let searchResult = {};
                searchResult = item;
                searchResult["DOI"] = key;
                _this.searchResultForSelection.push(searchResult);
              }
              _this.isLoading = false;
              console.log(_this.searchResultForSelection);
              let start = xhr
                .getResponseHeader("link")
                .match(/(?<=\=).*(?=>)/)[0];
              _this.moreSearchURL = `https://t0guvf0w17.execute-api.us-east-1.amazonaws.com/Prod/search?start=${start}`;
              _this.lastSearchInput = searchInput;
            } else {
              _this.searchNotFound();
              //   _this.moreSearchURL = "";
              //   _this.searchResultForSelection = [];
              //   _this.isLoading = false;
              //   _this.searchError = true;
            }
          },
        }
      );
    },
    moreSearch() {
      this.isMoreLoading = true;
      if (this.pubmedSearchTimes == 0) {
        this.moreSearchZotero();
      } else {
        this.moreSearchPubmed();
      }
    },
    moreSearchZotero() {
      var _this = this;
      $.ajax(_this.moreSearchURL, {
        contentType: "text/plain",
        type: "POST",
        data: _this.lastSearchInput, //"10.1056/NEJMra041320", //"psoriasis",
        success: function (res) {
          //   console.log(res);
        },
        error: function (xhr, b, c) {
          if (xhr.status == 300) {
            _this.searchError = false;
            for (var [key, item] of Object.entries(xhr.responseJSON)) {
              let searchResult = {};
              searchResult = item;
              searchResult["DOI"] = key;
              _this.searchResultForSelection.push(searchResult);
            }
            _this.isMoreLoading = false;
            console.log(_this.searchResultForSelection);
            let start = xhr
              .getResponseHeader("link")
              .match(/(?<=\=).*(?=>)/)[0];
            _this.moreSearchURL = `https://t0guvf0w17.execute-api.us-east-1.amazonaws.com/Prod/search?start=${start}`;
          } else {
            _this.moreSearchURL = "";
            _this.isMoreLoading = false;
          }
        },
      });
    },
    async searchPubmed(searchInput) {
      var _this = this;
      try {
        let res = await $.ajax(
          `https://eutils.ncbi.nlm.nih.gov/entrez/eutils/esearch.fcgi?db=pubmed&term=${searchInput}&sort=relevance&retstart=0&retmax=3`
        );
        let count = $("eSearchResult Count", res)[0].textContent;
        let idList = $("IdList Id", res)
          .map((i, x) => {
            return $(x).text();
          })
          .get();
        let resCsl = await $.ajax(
          `https://api.ncbi.nlm.nih.gov/lit/ctxp/v1/pubmed/?format=csl&id=${idList.join(
            ","
          )}`
        );
        _this.searchResultForSelection = [];
        if (idList.length == 1) {
          let display = _this.csl2Display(resCsl, "pmid");
          _this.searchResultForSelection.push(display);
        } else {
          for (var i in idList) {
            let display = _this.csl2Display(resCsl[i], "pmid");
            _this.searchResultForSelection.push(display);
          }
        }
        _this.isLoading = false;
        if (count > 3) {
          _this.moreSearchURL = `https://eutils.ncbi.nlm.nih.gov/entrez/eutils/esearch.fcgi?db=pubmed&term=${searchInput}&sort=relevance&retstart=3&retmax=3`;
          _this.lastSearchInput = searchInput;
          _this.pubmedSearchTimes = 1;
        }
      } catch {
        _this.searchNotFound();
      }
    },
    async moreSearchPubmed() {
      var _this = this;
      console.log(_this.lastSearchInput);
      try {
        let res = await $.ajax(_this.moreSearchURL);
        let count = $("eSearchResult Count", res)[0].textContent;
        let idList = $("IdList Id", res)
          .map((i, x) => {
            return $(x).text();
          })
          .get();
        let resCsl = await $.ajax(
          `https://api.ncbi.nlm.nih.gov/lit/ctxp/v1/pubmed/?format=csl&id=${idList.join(
            ","
          )}`
        );
        for (var i in idList) {
          let display = _this.csl2Display(resCsl[i], "pmid");
          _this.searchResultForSelection.push(display);
        }
        if (count > (parseInt(_this.pubmedSearchTimes) + 1) * 3) {
          _this.pubmedSearchTimes += 1;
          _this.moreSearchURL = `https://eutils.ncbi.nlm.nih.gov/entrez/eutils/esearch.fcgi?db=pubmed&term=${
            _this.lastSearchInput
          }&sort=relevance&retstart=${
            parseInt(_this.pubmedSearchTimes) * 3
          }&retmax=3`;
        } else {
          _this.moreSearchURL = "";
        }
        _this.isMoreLoading = false;
      } catch {
        this.isMoreLoading = false;
      }
    },
    searchPMID(searchInput) {
      var _this = this;
      $.ajax(
        `https://api.ncbi.nlm.nih.gov/lit/ctxp/v1/pubmed/?format=csl&id=${searchInput}`,
        {
          success: function (res) {
            if (res.length == 0) {
              _this.searchNotFound();
              return;
            }
            _this.searchResultForSelection = [];
            let display = _this.csl2Display(res, "pmid");
            console.log(display);
            _this.searchResultForSelection.push(display);
            _this.isLoading = false;
          },
          error: function () {
            _this.searchNotFound();
          },
        }
      );
    },
    searchDOI(searchInput) {
      var _this = this;

      $.ajax(`https://data.crossref.org/${searchInput}`, {
        headers: {
          accept: "application/vnd.citationstyles.csl+json",
          //   "accept-language":
          //     "zh-CN,zh;q=0.9,en;q=0.8,en-GB;q=0.7,en-US;q=0.6,zh-TW;q=0.5",
        },
        success: function (res) {
          console.log(res);
          if (res.length == 0) {
            _this.searchNotFound();
            return;
          }
          _this.searchResultForSelection = [];
          let display = _this.csl2Display(res, "doi");
          console.log(display);
          _this.searchResultForSelection.push(display);
          _this.isLoading = false;
        },
        error: function () {
          _this.searchNotFound();
        },
      });
    },
    csl2Display(csl, type) {
      // let name = [];
      // for (var i in csl.author) {
      //   if (i > 2) continue;
      //   if (csl.author[i].family == undefined) continue;
      //   name.push(csl.author[i].family);
      // }
      // let year = csl.issued == undefined ? "" : csl.issued["date-parts"][0][0];
      // let containerTitle = csl["container-title"];
      // let description = `${name.join(", ")} - ${year} - ${containerTitle}`;
      // let title = csl.title;
      // let DOI = csl.DOI == undefined ? "NA" : csl.DOI;
      let parsedCsl = parseCsl(csl);
      let description = `${parsedCsl.author3} - ${parsedCsl.year} - ${parsedCsl.containerTitle}`;
      let title = parsedCsl.title;
      let DOI = parsedCsl.doi;
      let ret = {
        description: description,
        DOI: DOI,
        title: title,
        csl: {},
      };
      ret.csl[type] = csl;
      return ret;
    },
    searchNotFound() {
      this.moreSearchURL = "";
      this.searchResultForSelection = [];
      this.isLoading = false;
      this.searchError = true;
    },
  },
  watch: {
    // searchType() {
    //   this.searchInput = "";
    // },
  },
};
</script>

<style scoped>
.hover {
  background-color: white;
  cursor: default !important;
}
.hover:hover {
  background-color: gainsboro;
}
</style>