<template>
  <div>
    <v-dialog
      v-model="projectEditOpen"
      v-if="projectEditOpen"
      max-width="800"
      persistent
    >
      <v-card>
        <v-card-title class="primary white--text">
          <span>Edit Project</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="">
                <v-text-field
                  v-model="projectAdd"
                  label="Add Project"
                  append-outer-icon="mdi-plus"
                  clearable
                  spellcheck="false"
                  hide-details
                  @click:append-outer="addProject"
                  @keyup.enter="addProject"
                >
                </v-text-field>
              </v-col>
              <v-col cols="12" class="py-0">
                <span class="primary--text font-weight-bold text-button"
                  >Project List</span
                ></v-col
              >
              <v-col cols="12" class="pt-0">
                <div v-for="(item, index) in projectList" :key="index">
                  <div class="d-flex flex-row hover">
                    <div class="col-10 py-1">
                      <span class="grey--text font-weight-bold">{{
                        item
                      }}</span>
                    </div>
                    <div class="col-2 py-1 d-flex flex-row justify-end px-1">
                      <v-icon
                        class="info--text"
                        style="cursor: pointer"
                        left
                        @click="
                          editProject = item;
                          renameProjectOpen = true;
                          projectRenamed = '';
                        "
                        >mdi-pencil</v-icon
                      >
                      <v-icon
                        class="error--text"
                        style="cursor: pointer"
                        @click="
                          editProject = item;
                          deleteProjectOpen = true;
                        "
                        >mdi-delete</v-icon
                      >
                    </div>
                  </div>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="mr-6" color="primary" @click="$emit('close')">
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="deleteProjectOpen" max-width="800" persistent>
      <v-card>
        <v-card-title class="error white--text">
          <span>Delete Project</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="">
                <span class="primary--text text-subtitle-1">{{
                  `Do you want to delete Project: ${editProject}?`
                }}</span>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-3"
            color="error"
            @click="
              deleteProject(editProject);
              deleteProjectOpen = false;
            "
          >
            Delete
            <v-icon right>mdi-delete</v-icon>
          </v-btn>
          <v-btn
            class="mr-6"
            color="primary"
            @click="deleteProjectOpen = false"
          >
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="renameProjectOpen" max-width="800" persistent>
      <v-card>
        <v-card-title class="info white--text">
          <span>{{ `Rename Project: ${editProject}` }}</span>
        </v-card-title>
        <v-card-text class="pb-0" style="max-height: 450px; overflow-y: auto">
          <v-container>
            <v-row>
              <v-col cols="12" class="">
                <v-text-field
                  v-model="projectRenamed"
                  :label="`Rename Project: ${editProject} To`"
                  clearable
                  spellcheck="false"
                  hide-details
                >
                </v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="mr-3"
            color="info"
            @click="renameProject(projectRenamed, editProject)"
          >
            Rename
            <v-icon right>mdi-pencil</v-icon>
          </v-btn>
          <v-btn
            class="mr-6"
            color="primary"
            @click="renameProjectOpen = false"
          >
            Close
            <v-icon right>mdi-close-circle</v-icon>
          </v-btn>
          <!-- <v-btn color="primary" @click="dialogEdit = false">Save</v-btn> -->
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar
      v-model="isProjectCreateSuccess"
      timeout="3000"
      top
      color="success"
    >
      {{ `Successfully Create Project:${projectAddOld}` }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="isProjectCreateSuccess = false"
        >
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
    <v-snackbar
      v-model="isProjectCreateError"
      timeout="3000"
      top
      color="warning"
    >
      {{ warningMessage }}
      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="isProjectCreateError = false"
        >
          <v-icon class="white--text">mdi-close-circle-outline</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { createProject, deleteProject, renameProject } from "./setProjectData";
export default {
  name: "projectEdit",
  props: {
    projectEditOpen: Boolean,
    projectDataAll: Object,
  },
  data() {
    return {
      projectList: [],
      projectAdd: "",
      projectAddOld: "", //for display in success snakbar
      isProjectCreateSuccess: false,
      isProjectCreateError: false,
      warningMessage: "",
      editProject: "",
      deleteProjectOpen: false,
      renameProjectOpen: false,
      projectRenamed: "",
    };
  },
  computed: {},
  methods: {
    addProject() {
      let projectName = this.projectAdd;
      if (projectName.startsWith("_")) {
        this.warningMessage = "Project name can't start with '_'";
        this.isProjectCreateError = true;
        return;
      }
      if (
        this.projectDataAll["_projectSummary"]["projectList"].includes(
          projectName
        )
      ) {
        this.warningMessage = `Project: ${projectName} has been created`;
        this.isProjectCreateError = true;
        return;
      }
      if (projectName.trim() == "") {
        this.warningMessage = "No project name detected";
        this.isProjectCreateError = true;
        return;
      }
      this.isProjectCreateSuccess = true;
      this.projectAddOld = this.projectAdd + "";
      createProject(projectName);
      this.projectList.push(projectName);
      this.projectAdd = "";
      //   console.log(project);
    },
    deleteProject(item) {
      deleteProject(item);
    },
    renameProject(newProjectName, oldProjectName) {
      console.log(newProjectName, oldProjectName);
      if (newProjectName.trim().startsWith("_")) {
        this.warningMessage = "Project name can't start with '_'";
        this.isProjectCreateError = true;
        return;
      }
      if (
        this.projectDataAll["_projectSummary"]["projectList"].includes(
          newProjectName
        )
      ) {
        this.warningMessage = `Project: ${newProjectName} has been created`;
        this.isProjectCreateError = true;
        return;
      }
      if (newProjectName.trim() == "") {
        this.warningMessage = "No project name detected";
        this.isProjectCreateError = true;
        return;
      }
      renameProject(newProjectName, oldProjectName);
      this.renameProjectOpen = false;
      this.$emit("rename", {
        newProjectName: newProjectName,
        oldProjectName: oldProjectName,
      });
    },
  },
  watch: {
    projectDataAll(n, o) {
      this.projectList = n["_projectSummary"]["projectList"];
    },
  },
  beforeUpdate: function () {
    this.projectList = this.projectDataAll["_projectSummary"]["projectList"];
  },
};
</script>

<style scoped>
.hover {
  background-color: white;
  cursor: default !important;
}
.hover:hover {
  background-color: gainsboro;
}
</style>