<template>
  <v-card class="d-flex flex-column">
    <v-toolbar color="primary" dark dense>
      <v-spacer />
      <v-toolbar-title>选中杂志目录</v-toolbar-title>
      <v-spacer />
    </v-toolbar>

    <v-data-table
      :headers="headers"
      :items="desserts"
      sort-by="journal"
      :search="search"
      :page.sync="page"
      :items-per-page="itemsPerPage"
      @page-count="pageCount = $event"
      hide-default-footer
    >
      <template v-slot:top>
        <v-card flat>
          <v-card-title class="mx-0 px-0">
            <v-text-field
              v-model="search"
              prepend-icon="mdi-table-search"
              label="请输入杂志全称、杂志缩写、ISSN、eISSN查询"
              hide-details
              class="pt-0 mt-3 mx-3"
              outlined
              clearable
            ></v-text-field>
          </v-card-title>
        </v-card>
      </template>
      <template v-slot:item.actions="{ item }">
        <!-- <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon> -->
        <v-icon
          @click="deleteItem(item)"
          color="red lighten-2"
          :disabled="add.includes(item.journal_abb)"
          >mdi-delete</v-icon
        >
      </template>
    </v-data-table>
    <v-pagination
      v-model="page"
      :length="pageCount"
      total-visible="15"
      circle
      class="my-3"
    ></v-pagination>
    <v-card v-if="del.length != 0" color="primary" dark tile>
      <v-container class="pa-0">
        <v-row align="center">
          <v-col class="col-2" justify="center">
            <p class="text-center ma-0">
              <v-icon class="mx-2">mdi-minus-box-multiple</v-icon
              >从过滤器删除杂志
            </p>
          </v-col>
          <v-col class="col-10" justify="center">
            <v-chip
              v-for="(value, key) in del"
              :key="key"
              close
              color="blue-grey"
              text-color="grey lighten-2"
              class="ma-1"
              @click:close="close(value)"
              >{{ value }}</v-chip
            >
            <!-- @click:close="$emit('eventClose',value)"
            @eventClose="close($event)"-->
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </v-card>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  data: () => ({
    search: "",
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    headers: [
      {
        text: "杂志全称",
        value: "journal",
        filterable: true,
        align: "start",
      },
      {
        text: "杂志缩写",
        value: "journal_abb",
        filterable: true,
        align: "start",
      },
      { text: "ISSN", value: "issn", filterable: true, align: "center" },
      { text: "eISSN", value: "eissn", filterable: true, align: "center" },
      // { text: "SCI Indexed", value: "sci", align: "center" },
      { text: "影响因子", value: "imf", align: "center" },
      { text: "JCR分区", value: "jcr", filterable: true, align: "center" },
      { text: "中科院分区", value: "cas", filterable: true, align: "center" },
      {
        text: "顶级/非顶级刊物",
        filterable: true,
        value: "top_cn",
        align: "center",
      },
      // { text: "Filtered Journal", value: "filtered", align: "center" },
      { text: "删除", value: "actions", sortable: false, align: "center" },
    ],
  }),

  computed: {
    desserts() {
      return this.$store.getters.filteredTable;
    },
    ...mapGetters({
      add: "getAdd",
      del: "getDel",
    }),
  },
  methods: {
    deleteItem(item) {
      const index = this.desserts.indexOf(item);
      // confirm("Are you sure you want to delete this item?") &&
      console.log(item);
      this.desserts.splice(index, 1);
      if (this.add.includes(item.journal_abb)) {
        this.$store.commit("setFilter", {
          param: "add",
          value: this.add.filter((val) => {
            return val != item.journal_abb;
          }),
        });
      } else {
        this.$store.commit("setFilter", {
          param: "del",
          value: this.del.concat(item.journal_abb),
        });
      }
    },
    close(closeAbb) {
      this.$store.commit("setFilter", {
        param: "del",
        value: this.del.filter((val) => {
          return val != closeAbb;
        }),
      });
    },
  },
};
</script>
