import { parse } from "./parse"
export async function getScihubPdf(pmid, doi, selectedURL) {
    //analyse sci_hub_url and extract the corresponding pdf_url, activate the pdf link
    let pdfURL
    let response = await new Promise(resolve => {
        chrome.runtime.sendMessage({ command: 'analyse_scihub', scihubURL: selectedURL, pmid: pmid, doi: doi },//, check: true 
            function (response) {
                resolve(response)
            })
    })
    if (response.pdf_url != "") {
        pdfURL = response.pdf_url
    } else {
        pdfURL = "error"
    }
    // console.log({ pmid: pmid, pdfURL: pdfURL })
    return { pmid: pmid, pdfURL: pdfURL }
}

export async function getFullTextPdf(item) {
    let parsed = parse(item);
    let option = await getOption()
    let url = await getScihubPdf(
        parsed.pmid,
        parsed.doi,
        option.selectedURL
    );
    return url
}

function getOption() {
    return new Promise(resolve => {
        chrome.storage.local.get(["options"], function (result) {
            resolve(result.options)
        })
    })
}