import { parseCsl, parseData, parse } from "./parse"
import { update, updateItem, updateItemSecondary } from "./setProjectData";
import { getFullTextPdf } from "./getScihubPdf"
var $ = require("jquery")
export async function downloadArticle(url, item, { again = false } = {}) {
    // let parsedData = parseData(item.data)
    let filename = await makeFilename(item)
    filename = filename.filename
    // console.log(filename)

    let isSuccess = await new Promise(resolve => {
        chrome.downloads.download(
            {
                url: url,
                filename: filename,
            },
            function (id) {
                var listener = chrome.downloads.onChanged.addListener(async function (downloadDelta) {
                    if (downloadDelta.id != id) return
                    // console.log(downloadDelta);
                    if (downloadDelta.state == undefined) return;
                    if (
                        downloadDelta.id == id &&
                        downloadDelta.state.current == "complete"
                    ) {
                        var filename = await getFilename(id)
                        if (filename.endsWith('.pdf')) {
                            updateItem(item.data.project, item.label, { downloadId: id });
                            // console.log("completed");
                            resolve(true)
                        } else {
                            chrome.downloads.removeFile(id, () => { })
                            if (again == true) { resolve(false); return }
                            let urlObject = await getFullTextPdf(item);
                            url = urlObject.pdfURL
                            $.ajax(url).always(async function () {
                                console.log('download again', url, filename, item.csl.title)
                                let isSuccessAgain = await downloadArticle(url, item, { again: true })
                                resolve(isSuccessAgain)
                            })
                        }
                        chrome.downloads.onChanged.removeListener(listener)
                    } else {
                        // console.log("inprogress");
                        if (downloadDelta.error != undefined) {
                            resolve(false)
                        }

                    }
                });
            }
        );
    })
    // console.log('isSuccess', isSuccess)
    return isSuccess

}

export async function downloadSupp(url, item, suppLabel) {
    let filename = await makeFilename(item, { suppLabel: suppLabel })
    filename = filename.filenameSupp
    // console.log(filename)
    var filenameListenerFunction = function (item, suggest) {
        // console.log('determining name', item)
        let type = item.filename.split('.').pop()
        let originalFilename = item.filename.split('.').slice(0, -1).join('.')
        if (suppLabel != "") {
            var ret = `${filename}.${type}`
        } else {
            var ret = `${filename}${originalFilename}.${type}`
            suppLabel = originalFilename
            console.log(ret)
        }
        suggest({ filename: ret })
        chrome.downloads.onDeterminingFilename.removeListener(filenameListenerFunction)
    }
    chrome.downloads.onDeterminingFilename.addListener(
        filenameListenerFunction
    )
    let isSuccess = await new Promise(resolve => {
        chrome.downloads.download(
            {
                url: url,
            },
            function (id) {
                var listener = chrome.downloads.onChanged.addListener(async function (downloadDelta) {
                    if (downloadDelta.id != id) return
                    console.log(downloadDelta);
                    if (downloadDelta.state == undefined) return;
                    if (
                        downloadDelta.id == id &&
                        downloadDelta.state.current == "complete"
                    ) {
                        let updateSuppObject = {}
                        updateSuppObject[`${suppLabel}`] = { downloadId: id, url: url, suppLabel: suppLabel }
                        updateItemSecondary(item.data.project, item.label, 'supplement', updateSuppObject)
                        // updateItem(item.data.project, item.label, { downloadId: id });
                        // console.log("completed");
                        resolve(true)
                    } else {
                        // console.log("inprogress");
                        if (downloadDelta.error != undefined) {
                            resolve(false)
                        }
                    }
                });
            }
        );
    })
    console.log('isSuccess', isSuccess)
    return isSuccess
}

export async function makeFilename(item, { styleId = -1, suppLabel = '' } = {}) {
    return new Promise(resolve => {
        chrome.storage.local.get(["options"], function (result) {
            let options = result.options
            if (styleId == -1) {
                styleId = options.collect.fullTextFilenameStyle
            }
            if (styleId == undefined) {
                resolve({ filename: '', filenameShort: '' })
                return
            }
            let parsed = parse(item);
            let project = handleNa(parsed.project);
            let lastAuthor = handleNa(parsed.lastAuthor);
            let lastAuthorFamily = handleNa(parsed.lastAuthorFamily);
            let firstAuthor = handleNa(parsed.firstAuthor);
            let firstAuthorFamily = handleNa(parsed.firstAuthorFamily);
            let year = handleNa(parsed.year);
            let title = handleNa(parsed.title);
            let journal = handleNa(parsed.containerTitleShort);
            let pmid = handleNa(parsed.pmid);
            var filenameShort
            let defaultFilenameShort = `${title}`
            switch (styleId) {
                case 1:
                    filenameShort = `${title}`
                    break
                case 2:
                    filenameShort = `${lastAuthorFamily}-${firstAuthorFamily}-${year}-${journal}-${title}`
                    break
                case 3:
                    filenameShort = `${pmid}`
                    break
                case 4:
                    if (pmid == "@#@#") {
                        filenameShort = defaultFilenameShort
                    } else {
                        filenameShort = `[${pmid}]${title}`
                    }
                    break
            }
            filenameShort = filenameShort.replace(/@#@#/, '')
                .replace(/[":\/\\*\<\>\?|]/g, "_")
                .slice(0, 250);
            if (filenameShort.trim() == "") {
                filenameShort = defaultFilenameShort
            }
            // console.log(filenameShort);
            let filename = `EasyPubMed/${project}/${filenameShort}.pdf`;
            let filenameShortPdf = `${filenameShort}.pdf`
            var filenameSupp = ""
            if (suppLabel != '') {
                suppLabel = suppLabel.replace(/[":\/\\*\<\>\?|]/g, "_").slice(0, 250);
                filenameSupp = `EasyPubMed/${project}/${filenameShort}/${suppLabel}`
            } else {
                filenameSupp = `EasyPubMed/${project}/${filenameShort}/`
            }
            resolve({ filename: filename, filenameShort: filenameShort, filenameShortPdf: filenameShortPdf, filenameSupp: filenameSupp })
        })
    })
}

function getFilename(id) {
    return new Promise((resolve) => {
        chrome.downloads.search(
            { id: id },
            function (result) {
                resolve(result[0].filename)
                // console.log(url, filename, item.csl.title)
                // console.log(result)
                // if (result[0].filename.endsWith('.pdf')) {
                //     resolve(true)
                // } else {
                //     resolve(false)
                // };
                // resolve(result[0].exists);
            }
        )
    });
}

function handleNa(i) {
    if (i == "NA" || i == undefined || i == null) {
        return '@#@#'
    } else {
        return i
    }
}