//     options.collect.fullTextFilenameStyle = undefined
//     chrome.storage.local.set({ options: options }, () => { })
// })
export function setOption(newOptionObject) {
    chrome.storage.local.get(["options"], function (result) {
        let options = result.options
        for (var [key, value] of Object.entries(newOptionObject)) {
            options[key] = value
            if (key == "collect") {
                updateSummaryAll(options)
            }
        }
        chrome.storage.local.set({ options: options }, () => { })
    })
}