<template>
  <div>
    <div>
      <v-row align="center" class="justify-space-between px-3 pt-3 pb-1">
        <v-select
          class="mr-2"
          v-model="project"
          :items="projectNames"
          label="Select Project"
          prepend-icon="mdi-alpha-p-box"
          style="max-width: 360px"
        >
        </v-select>
        <v-btn class="primary mx-2 success" @click="projectEditOpen = true">
          Edit Project<v-icon right>mdi-pencil</v-icon>
        </v-btn>
        <v-btn class="primary mx-2 success" @click="newArticleOpen = true">
          New Article<v-icon>mdi-plus</v-icon>
        </v-btn>
        <v-btn
          class="primary mx-2"
          @click="importArticleOpen = true"
          :disabled="project == 'ALL PROJECT'"
        >
          Import
          <!-- <v-icon>mdi-database-import</v-icon> -->
        </v-btn>
        <v-btn class="primary mx-2" @click="exportArticleOpen = true">
          Export
          <!-- <v-icon>mdi-database-export</v-icon> -->
        </v-btn>
        <v-badge
          bordered
          color="warning"
          :value="isDownloading"
          overlap
          icon="mdi-download"
          offset-x="20"
          offset-y="10"
          ><v-btn class="primary mx-2" @click="batchDownloadOpen = true">
            Batch Download
            <!-- <v-icon>mdi-download</v-icon> -->
          </v-btn>
        </v-badge>
        <v-btn class="primary mx-2" @click="settingsOpen = true">
          <v-icon>mdi-cog</v-icon></v-btn
        >
      </v-row>
      <v-row align="center" class="justify-space-between px-3 py-1">
        <v-text-field
          v-model="filter.search"
          label="Search"
          placeholder="Search Title, Jounral, Journal abbreviation, Tag or Note"
          prepend-icon="mdi-magnify"
          clearable
          spellcheck="false"
        >
        </v-text-field>
      </v-row>
      <v-row align="center" class="px-3 pt-3 pb-2 justify-space-between">
        <v-expansion-panels v-model="filterExpansion">
          <v-expansion-panel>
            <v-expansion-panel-header>Filter</v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-row>
                <v-col cols="6" class="py-1"
                  ><v-autocomplete
                    v-model="filter.tagSelected"
                    :items="tags"
                    color="primary"
                    label="Filter by Tags"
                    multiple
                  >
                    <template v-slot:selection="data">
                      <v-chip
                        v-bind="data.attrs"
                        :input-value="data.item"
                        @click="
                          tagSelected = tagSelected.filter((i) => {
                            return i != data.item;
                          })
                        "
                      >
                        {{ data.item }}
                      </v-chip>
                    </template>
                  </v-autocomplete></v-col
                >
                <v-col cols="6" class="py-1">
                  <v-autocomplete
                    v-model="filter.starSelected"
                    :items="starArray"
                    color="primary"
                    label="Filter by Star"
                    multiple
                  >
                  </v-autocomplete>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="3" class="py-1">
                  <v-text-field
                    v-model.lazy="filter.minYear"
                    class="centered-input"
                    label="Min Year"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="3" class="py-1">
                  <v-text-field
                    v-model.lazy="filter.maxYear"
                    class="centered-input"
                    label="Max Year"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="3" class="py-1"
                  ><v-text-field
                    v-model.lazy="filter.minIF"
                    class="centered-input"
                    label="Min Impact Factor"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="3" class="py-1"
                  ><v-text-field
                    v-model.lazy="filter.maxIF"
                    class="centered-input"
                    label="Max Impact Factor"
                    type="number"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="2" class="py-1"
                  ><v-checkbox v-model="filter.blueFlag" color="blue">
                    <template v-slot:prepend>
                      <v-icon color="blue">mdi-flag</v-icon>
                    </template>
                  </v-checkbox></v-col
                >
                <v-col cols="2" class="py-1"
                  ><v-checkbox v-model="filter.redFlag" color="red">
                    <template v-slot:prepend>
                      <v-icon color="red">mdi-flag</v-icon>
                    </template>
                  </v-checkbox></v-col
                >
                <v-col cols="4" class="py-1"
                  ><v-checkbox
                    v-model="filter.hasLinkedPdf"
                    color="primary"
                    label="Articles with downloaded full-text PDF"
                  >
                  </v-checkbox
                ></v-col>
                <v-col cols="4" class="py-1"
                  ><v-checkbox
                    v-model="filter.notHasLinkedPdf"
                    color="primary"
                    label="Articles without downloaded full-text PDF"
                  >
                  </v-checkbox
                ></v-col>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-row>
      <v-row align="center" class="px-3 py-1 justify-space-between">
        <v-checkbox
          :value="selectAllArticle"
          :label="`Select All (${projectDataFilteredSortedLength})`"
          @click="_clickSelectAll"
        ></v-checkbox>
        <!-- (${projectDataFilteredSorted.items.length}) -->
        <v-spacer></v-spacer>
        <div class="theme--light v-label pr-2">SORT BY</div>
        <v-btn
          small
          outlined
          primary
          :class="
            sort.updateTime == null
              ? `px-1 mx-2`
              : `px-1 mx-2 primary white--text`
          "
          @click="
            sort.updateTime = sort.updateTime == null ? 1 : -sort.updateTime;
            clearSort('updateTime');
          "
          >Update Time<v-icon>{{
            sort.updateTime == null
              ? "mdi-chevron-up"
              : sort.updateTime == 1
              ? "mdi-chevron-up"
              : "mdi-chevron-down"
          }}</v-icon></v-btn
        >
        <v-btn
          small
          outlined
          :class="
            sort.title == null ? `px-1 mx-2` : `px-1 mx-2 primary white--text`
          "
          @click="
            sort.title = sort.title == null ? 1 : -sort.title;
            clearSort('title');
          "
          >Title<v-icon>{{
            sort.title == null
              ? "mdi-chevron-up"
              : sort.title == 1
              ? "mdi-chevron-up"
              : "mdi-chevron-down"
          }}</v-icon></v-btn
        >
        <v-btn
          small
          outlined
          :class="
            sort.journal == null ? `px-1 mx-2` : `px-1 mx-2 primary white--text`
          "
          @click="
            sort.journal = sort.journal == null ? 1 : -sort.journal;
            clearSort('journal');
          "
          >Journal<v-icon>{{
            sort.journal == null
              ? "mdi-chevron-up"
              : sort.journal == 1
              ? "mdi-chevron-up"
              : "mdi-chevron-down"
          }}</v-icon></v-btn
        >
        <v-btn
          small
          outlined
          primary
          :class="
            sort.year == null ? `px-1 mx-2` : `px-1 mx-2 primary white--text`
          "
          @click="
            sort.year = sort.year == null ? 1 : -sort.year;
            clearSort('year');
          "
          >Year<v-icon>{{
            sort.year == null
              ? "mdi-chevron-up"
              : sort.year == 1
              ? "mdi-chevron-up"
              : "mdi-chevron-down"
          }}</v-icon></v-btn
        >
        <v-btn
          small
          outlined
          primary
          :class="
            sort.imf == null ? `px-1 mx-2` : `px-1 mx-2 primary white--text`
          "
          @click="
            sort.imf = sort.imf == null ? 1 : -sort.imf;
            clearSort('imf');
          "
          >IF<v-icon>{{
            sort.imf == null
              ? "mdi-chevron-up"
              : sort.imf == 1
              ? "mdi-chevron-up"
              : "mdi-chevron-down"
          }}</v-icon></v-btn
        >
        <v-btn
          small
          outlined
          primary
          :class="
            sort.star == null ? `px-1 mx-2` : `px-1 mx-2 primary white--text`
          "
          @click="
            sort.star = sort.star == null ? 1 : -sort.star;
            clearSort('star');
          "
          >Star<v-icon>{{
            sort.star == null
              ? "mdi-chevron-up"
              : sort.star == 1
              ? "mdi-chevron-up"
              : "mdi-chevron-down"
          }}</v-icon></v-btn
        >
      </v-row>
    </div>
    <div>
      <articleDisplay
        :projectData="projectDataFilteredSorted"
        :project="project"
        :selectedURL="selectedURL"
        :options="options"
        :selectAllArticle="selectAllArticle"
        :forceClear="forceClear"
        v-on:selectedArticles="_selectedArticles"
        v-on:disableSelectAll="_disableSelectAll"
      ></articleDisplay>
    </div>
    <ad class="mt-6" />
    <v-btn
      color="info lighten-1"
      fixed
      bottom
      right
      fab
      elevation="3"
      @click="scorllToTop"
      v-show="scrollY > 200"
    >
      <v-icon>mdi-arrow-up</v-icon>
    </v-btn>
    <!-- <Footer /> -->
    <projectEdit
      :projectEditOpen="projectEditOpen"
      :projectDataAll="projectDataAll"
      v-on:close="projectEditOpen = false"
      v-on:rename="projectNameRenamed"
    ></projectEdit>
    <newArticle
      :newArticleOpen="newArticleOpen"
      :project="project"
      :projectDataAll="projectDataAll"
      v-on:close="newArticleOpen = false"
    ></newArticle>
    <importArticle
      :importArticleOpen="importArticleOpen"
      :project="project"
      :projectDataAll="projectDataAll"
      v-on:close="importArticleClose"
    ></importArticle>
    <exportArticle
      :exportArticleOpen="exportArticleOpen"
      :project="project"
      :selectedArticles="selectedArticles"
      :projectDataFiltered="projectDataFiltered"
      :projectDataAll="projectDataAll"
      v-on:close="exportArticleOpen = false"
    ></exportArticle>
    <batchDownload
      :batchDownloadOpen="batchDownloadOpen"
      :project="project"
      :options="options"
      :selectedArticles="selectedArticles"
      :projectDataFiltered="projectDataFiltered"
      :projectDataAll="projectDataAll"
      v-on:close="batchDownloadOpen = false"
      v-on:startDownload="isDownloading = true"
      v-on:downloadComplete="
        batchDownloadOpen = true;
        isDownloading = false;
      "
    ></batchDownload>
    <settings
      :settingsOpen="settingsOpen"
      :options="options"
      v-on:close="settingsOpen = false"
    ></settings>
  </div>
</template>

// <script>
// import { mapState } from "vuex";
import ad from "./ad";
import articleDisplay from "./articleDisplay";
import projectEdit from "./projectEdit";
import newArticle from "./newArticle";
import importArticle from "./importArticle";
import exportArticle from "./exportArticle";
import batchDownload from "./batchDownload";
import settings from "./settings";
import { update } from "./setProjectData";
import ifqbt from "../json/ifqbt_2021.json";
import { parseCsl, parseData } from "./parse";
import { setOption } from "./setOption";

export default {
  name: "mainEn",
  components: {
    articleDisplay,
    projectEdit,
    newArticle,
    importArticle,
    exportArticle,
    batchDownload,
    settings,
    ad,
  },
  data: function () {
    return {
      selectedURL: "",
      options: {},
      projectDataAll: {},
      projectData: {},
      projectDataFiltered: {},
      projectDataFilteredSorted: {},
      project: "",
      projectNames: [],
      filter: {
        search: "",
        tagSelected: [],
        starSelected: [],
        minIF: "",
        maxIF: "",
        minYear: "",
        maxYear: "",
        redFlag: false,
        blueFlag: false,
        hasLinkedPdf: false,
        notHasLinkedPdf: false,
      },
      sort: {
        title: null,
        journal: null,
        year: null,
        imf: null,
        star: null,
        updateTime: 1,
      },
      tags: [],
      starArray: [
        { value: 0, text: "☆☆☆☆☆" },
        { value: 1, text: "⭐☆☆☆☆" },
        { value: 2, text: "⭐⭐☆☆☆" },
        { value: 3, text: "⭐⭐⭐☆☆" },
        { value: 4, text: "⭐⭐⭐⭐☆" },
        { value: 5, text: "⭐⭐⭐⭐⭐" },
      ],
      selectedArticles: [],
      selectAllArticle: false,
      forceClear: false,
      journalImfqbtData: {},
      projectEditOpen: false,
      newArticleOpen: false,
      importArticleOpen: false,
      exportArticleOpen: false,
      batchDownloadOpen: false,
      settingsOpen: false,
      scrollY: 0,
      isDownloading: false,
      filterExpansion: [],
    };
  },
  computed: {
    // ...mapState({}),
    projectDataFilteredSortedLength() {
      if (this.projectDataFilteredSorted.items == undefined) {
        return 0;
      } else {
        return this.projectDataFilteredSorted.items.length;
      }
    },
  },
  watch: {
    starSelected(n, o) {
      console.log("star", n, o);
    },
    selectedArticles(n, o) {
      console.log("selected-main-articles", n);
    },
    project(n) {
      if (n == "ALL PROJECT") {
        let projectData = this.createProjectAllSelect(this.projectDataAll);
        this.projectData = projectData;
        this.tags = projectData.summary.tags;
      } else {
        this.projectData = this.projectDataAll[n];
        this.options.collect.defaultProject = n;
        setOption({ collect: this.options.collect });
      }
      this.selectAllArticle = false;
      this.selectedArticles = [];
      this.filter = {
        search: "",
        tagSelected: [],
        starSelected: [],
        minIF: "",
        maxIF: "",
        minYear: "",
        maxYear: "",
        redFlag: false,
        blueFlag: false,
        hasLinkedPdf: false,
        notHasLinkedPdf: false,
      };
      this.sort = {
        title: null,
        journal: null,
        year: null,
        imf: null,
        star: null,
        updateTime: 1,
      };
    },
    projectDataAll(n) {
      // console.log("projectDataAll", n._projectSummary.projectList);
      if (this.project == "ALL PROJECT") {
        let projectData = this.createProjectAllSelect(n);
        this.projectData = projectData;
      } else {
        this.projectData = n[this.project];
      }
    },
    projectData(n) {
      this.doFilter();
      this.doSort();
      this.tags = this.projectData.summary.tags;
    },

    filter: {
      deep: true,
      handler: function (n) {
        // console.log(JSON.stringify(n));
        this.doFilter();
        this.doSort();
      },
    },
    sort: {
      deep: true,
      handler: function () {
        // this.doFilter();
        this.doSort();
      },
    },
    projectDataFilteredSorted(n, o) {
      // console.log("filteredSorted", n);
    },
  },
  methods: {
    doFilter() {
      let filter = this.filter;
      let projectData = this.projectData;
      let projectDataFiltered = {};
      for (var [name, item] of Object.entries(projectData.items)) {
        let parsed = this.parse(item);
        if (filter.search != "" && filter.search != null) {
          if (
            parsed.title.toLowerCase().indexOf(filter.search.toLowerCase()) ==
              -1 &&
            parsed.authors_string
              .toLowerCase()
              .indexOf(filter.search.toLowerCase()) == -1 &&
            parsed.containerTitleShort
              .toLowerCase()
              .indexOf(filter.search.toLowerCase()) == -1 &&
            parsed.containerTitle
              .toLowerCase()
              .indexOf(filter.search.toLowerCase()) == -1 &&
            parsed.note.toLowerCase().indexOf(filter.search.toLowerCase()) ==
              -1 &&
            parsed.pmid.toLowerCase().indexOf(filter.search.toLowerCase()) ==
              -1 &&
            parsed.pmcid.toLowerCase().indexOf(filter.search.toLowerCase()) ==
              -1 &&
            parsed.doi.toLowerCase().indexOf(filter.search.toLowerCase()) ==
              -1 &&
            parsed.tag
              .join("@#$")
              .toLowerCase()
              .indexOf(filter.search.toLowerCase()) == -1
          ) {
            continue;
          }
        }
        if (filter.tagSelected.length != 0) {
          let intersect = filter.tagSelected.filter((x) =>
            parsed.tag.includes(x)
          );
          if (intersect.length != filter.tagSelected.length) {
            continue;
          }
        }
        if (filter.starSelected.length != 0) {
          if (!filter.starSelected.includes(parsed.star)) {
            continue;
          }
        }
        if (
          filter.minIF != "" &&
          parseFloat(parsed.imf) < parseFloat(filter.minIF)
        ) {
          continue;
        }
        if (
          filter.maxIF != "" &&
          parseFloat(parsed.imf) > parseFloat(filter.maxIF)
        ) {
          continue;
        }
        if ((filter.minIF != "" || filter.maxIF != "") && parsed.imf == "NA") {
          continue;
        }
        if (
          filter.minYear != "" &&
          parseInt(parsed.year) < parseFloat(filter.minYear)
        ) {
          continue;
        }
        if (
          filter.maxYear != "" &&
          parseInt(parsed.year) > parseFloat(filter.maxYear)
        ) {
          continue;
        }
        if (filter.redFlag == true && parsed.redFlag == false) {
          continue;
        }
        if (filter.blueFlag == true && parsed.blueFlag == false) {
          continue;
        }
        if (filter.hasLinkedPdf == true && item.data.downloadId == undefined) {
          continue;
        }
        if (
          filter.notHasLinkedPdf == true &&
          item.data.downloadId != undefined
        ) {
          continue;
        }
        projectDataFiltered[name] = item;
      }
      this.projectDataFiltered = projectDataFiltered;
      // console.log(
      //   this.projectData,
      //   this.projectDataFiltered,
      //   this.projectDataFilteredSorted
      // );
    },
    doSort() {
      let projectData = this.projectData;
      let projectDataFiltered = this.projectDataFiltered;
      let projectDataBeforeSorted = [];
      let projectDataSorted = [];
      for (var [name, item] of Object.entries(projectDataFiltered)) {
        item.label = name;
        projectDataSorted.push(item);
      }
      for (var sortItem in this.sort) {
        if (this.sort[sortItem] != null) {
          break;
        }
      }
      var _this = this;
      if (sortItem == "title") {
        projectDataSorted.sort(function (a, b) {
          return _this.parse(a).title > _this.parse(b).title
            ? _this.sort[sortItem]
            : -_this.sort[sortItem];
        });
      }
      if (sortItem == "journal") {
        projectDataSorted.sort(function (a, b) {
          return _this.parse(a).containerTitleShort >
            _this.parse(b).containerTitleShort
            ? _this.sort[sortItem]
            : -_this.sort[sortItem];
        });
      }
      if (sortItem == "year") {
        projectDataSorted.sort(function (a, b) {
          if (_this.parse(a).year == "NA") {
            return 1;
          }
          if (_this.parse(b).year == "NA") {
            return -1;
          }
          return _this.parse(a).year > _this.parse(b).year
            ? _this.sort[sortItem]
            : -_this.sort[sortItem];
        });
      }
      if (sortItem == "imf") {
        projectDataSorted.sort(function (a, b) {
          if (_this.parse(a).imf == "NA") {
            return 1;
          }
          if (_this.parse(b).imf == "NA") {
            return -1;
          }
          return _this.parse(a).imf > _this.parse(b).imf
            ? _this.sort[sortItem]
            : -_this.sort[sortItem];
        });
      }
      if (sortItem == "star") {
        projectDataSorted.sort(function (a, b) {
          return _this.parse(a).star > _this.parse(b).star
            ? _this.sort[sortItem]
            : -_this.sort[sortItem];
        });
      }
      if (sortItem == "updateTime") {
        projectDataSorted.sort(function (a, b) {
          return _this.parse(a).updateTime < _this.parse(b).updateTime
            ? _this.sort[sortItem]
            : -_this.sort[sortItem];
        });
      }
      this.projectDataFilteredSorted = {
        items: projectDataSorted,
        summary: this.projectData.summary,
      };
    },
    clearSort(x) {
      for (var i in this.sort) {
        if (i != x) {
          this.$set(this.sort, i, null);
        }
      }
      // console.log(this.sort);
    },
    parse(item) {
      let parsedCsl = parseCsl(item.csl);
      let parsedData = parseData(item.data);
      let journal = parsedCsl.containerTitleShort;
      if (Object.keys(this.journalImfqbtData).indexOf(journal) == -1) {
        this.get_pmid_imfq(journal);
      }
      let imfqbt = {
        imf: this.journalImfqbtData[journal].imf,
        jcr: this.journalImfqbtData[journal].jcr,
        cas: this.journalImfqbtData[journal].cas,
        top: this.journalImfqbtData[journal].top,
      };
      let ret = { ...parsedCsl, ...parsedData, ...imfqbt };
      // console.log(ret);
      return ret;
    },
    get_pmid_imfq(journal) {
      //get imfq if cant find both, both return as -1
      let temp;
      let imfq = {};
      imfq["imf"] = "NA";
      imfq["jcr"] = "NA";
      imfq["cas"] = "NA";
      imfq["top"] = "NA";
      temp = ifqbt.filter(function (x) {
        return x.journal == journal;
      });
      if (temp.length != 0) {
        imfq["imf"] = temp[0].IF;
        imfq["jcr"] = temp[0].Q;
        imfq["cas"] = temp[0].B;
        imfq["top"] = temp[0].T;
      }
      this.$set(this.journalImfqbtData, journal, imfq);
      // return imfq;
    },
    // update(pack) {
    //   let project = pack.project;
    //   let itemName = pack.itemName;
    //   let item = pack.item;
    //   // this.projectDataAll[project]['items']['data'][itemName] = item
    //   update(project, itemName, item);
    // },
    projectNameRenamed(pack) {
      console.log("a", pack);
      if (pack.oldProjectName == this.project) {
        this.project = pack.newProjectName;
      }
    },
    _clickSelectAll() {
      this.selectAllArticle = !this.selectAllArticle;
      this.forceClear = this.selectAllArticle == false ? true : false;
    },
    _disableSelectAll() {
      this.selectAllArticle = false;
      this.forceClear = false;
    },
    _selectedArticles(articles) {
      this.selectedArticles = articles;
    },
    createProjectAllSelect(projectDataAll) {
      let _projectData = { items: {}, summary: { ids: [], tags: [] } };
      for (var project in projectDataAll) {
        if (project == "_projectSummary") {
          continue;
        }
        _projectData.summary.tags = _projectData.summary.tags.concat(
          this.projectDataAll[project].summary.tags
        );
        let projectItems = this.projectDataAll[project].items;
        for (var item in projectItems) {
          if (Object.keys(_projectData.items).indexOf(item) != -1) {
            continue;
          }
          _projectData.items[item] = projectItems[item];
        }
      }
      _projectData.summary.ids = Object.keys(_projectData.items);
      _projectData.summary.tags = Array.from(
        new Set(_projectData.summary.tags)
      );
      return _projectData;
    },
    importArticleClose(x) {
      this.importArticleOpen = false;
      this.project = x.project;
    },
    scorllToTop() {
      window.scrollTo(0, 0);
    },
    handleScroll() {
      this.scrollY = window.scrollY;
    },
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
    var _this = this;
    chrome.storage.local.get(["options", "projectData"], function (result) {
      console.log(result);
      _this.selectedURL = result.options.selectedURL;
      _this.options = result.options;
      if (result.options.collect.fullTextFilenameStyle == undefined) {
        _this.settingsOpen = true;
      }
      let project = result.options.collect.defaultProject;
      let projectData = result.projectData;
      _this.project = project;
      _this.projectDataAll = projectData;
      _this.projectData = projectData[project];
      _this.projectNames = projectData._projectSummary.projectList.slice();
      _this.projectNames.unshift("ALL PROJECT");
      _this.tags = projectData[project].summary.tags;
    });
    var oldProjectData = "";
    var oldOptions = "";
    setInterval(function () {
      chrome.storage.local.get(["options", "projectData"], function (result) {
        // console.log("dd", result.projectData._projectSummary.projectList);
        // _this.options = result.options;
        if (oldOptions != JSON.stringify(result.options)) {
          oldOptions = JSON.stringify(result.options);
          _this.options = result.options;
          console.log("options change");
        }
        if (oldProjectData != JSON.stringify(result.projectData)) {
          // console.log("update");
          oldProjectData = JSON.stringify(result.projectData);
          _this.projectDataAll = result.projectData;
          _this.projectNames =
            result.projectData._projectSummary.projectList.slice();
          _this.projectNames.unshift("ALL PROJECT");
          console.log("projectdata change");
        }
      });
    }, 1000);
  },
};
</script>

